<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          padding: 0 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          box-sizing: border-box;
        "
      >
        <van-button
          plain
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="goChoose"
          ></van-button
        >
        <div
          style="
            flex: 1;
            margin: 0 16px;
            text-align: center;
            font-size: 14px;
            font-weight: bold;
            color: #fa6286;
            white-space: nowrap;
          "
        >
          {{ categoryDisplayName || "" }}
        </div>
        <van-button
          plain
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="go"
          ></van-button
        >
      </div>
      <van-checkbox-group
        v-model="result"
        style="width: 100%; box-sizing: border-box"
        checked-color="#fa6286"
        @change="changeCheckboxGroup"
      >
        <van-cell-group style="width: 100%; box-sizing: border-box">
          <van-cell
            size="large"
            is-link
            title-style="display:flex;"
            center
            clickable
            :key="item.id"
            @click="toggle(index)"
            v-for="(item, index) in list"
          >
            <template #title>
              <van-image
                :src="item.photosUrl"
                width="56"
                height="56"
                fit="contain"
              />
              <div
                style="
                  flex: 1;
                  display: flex;
                  flex-direction: column;
                  margin-left: 22px;
                  align-items: flex-start;
                  justify-content: space-between;
                "
              >
                <div
                  style="font-size: 14px; font-weight: bold; text-align: left"
                >
                  {{ item.name }}
                </div>
                <div
                  style="
                    font-size: 12px;
                    color: #999;
                    font-weight: 700;
                    text-align: left;
                  "
                >
                  {{ item.jianduanjieshao }}
                </div>
                <div
                  style="
                    font-size: 12px;
                    color: #666;
                    font-weight: 500;
                    text-align: left;
                    margin-top: 4px;
                  "
                >
                  ID: {{ item.goodscategoriesid || "-" }}
                  <span v-if="item.categoryName">
                    {{ item.categoryName }}
                  </span>
                </div>
              </div>
            </template>
            <template #right-icon>
              <!-- <van-checkbox
              :name="item.goodsgoodsidid"
              ref="checkboxes"
              @change="changeCheckbox"
              :max="1"
            /> -->
              <!--   @click  @change-->
              <van-checkbox
                :name="item?.goodsgoodsidid"
                ref="checkboxes"
                @click.stop.native="handle"
                @change="onChangeCheckbox"
                icon-size="28px"
              />
              <!-- (e) => handleCheckboxClick(item.goodsgoodsidid, e) -->
            </template>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>
      <div v-if="loading" style="text-align: center; padding: 20px; color: #999;">
        ...
      </div>
      <div v-if="!hasMore && list.length > 0" style="text-align: center; padding: 20px; color: #999;">
        
      </div>
    </div>
  </div>
</template>
<script>
// import wode1 from '@/assets/wode2.png';
export default {
  name: "CenterComp",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      logo3: require("../assets/logo3.png"),
      list: [],
      result: [],
      isChoose: false,
      page: 1,
      pageSize: 10,
      goodsgoodsidid: "",
      fromGuanggao: false,
      query: {},
      loading: false,
      hasMore: true,
      isReachBottom: false,
    };
  },
  computed: {
    categoryDisplayName() {
      if (!this.query) {
        return "";
      }
      return (
        this.query.categoryName ||
        this.query.goodscategoriesname ||
        this.query.name ||
        ""
      );
    },
  },
  methods: {
    toggle(index) {
      // this.$refs.checkboxes[index].toggle();
      console.log("index", index);
    },
    goExit() {
      localStorage.clear();
      this.$router.push("/");
    },
    goChoose() {
      this.$router.go(-1);
    },
    delOrCreate() {
      if (!this.isChoose) {
        this.$router.push("/item");
      } else {
        this.delItem();
      }
    },
    onChangeCheckbox(a) {
      console.log("+++++", a);
    },
    handle(e) {
      console.log("eee", e);
    },
    // getData() {
    //   this.$request
    //     .get("/goodsgoods", {
    //       params: {
    //         page: this.page,
    //         pageSize: this.pageSize,
    //         userId: this.user.userId,
    //       },
    //     })
    //     .then((res) => {
    //       console.log("", res);
    //       if (res?.code == 200) {
    //          let data = res.data.map((v) => {
    //           return { photosUrl: v.goodsfileslunbo[0]?.photosUrl, ...v };
    //         });
    //         this.list = data;
    //       }
    //     });
    // },
    // 
    async getData(isLoadMore = false) {
      // 
      if (this.loading || (isLoadMore && !this.hasMore)) {
        return;
      }

      this.loading = true;

      const page = isLoadMore ? this.page : 1;

      try {
        const res = await this.$request.get(
          "/goodsgoods/getWanyeLatestGoodsgoods",
          {
            params: {
              page: page,
              pageSize: this.pageSize,
              userId: this.user.userId,
            },
          }
        );
        console.log("", res);
        if (res?.code == 200) {
          let selectedArray = [];
          const hasPaixuQuery =
            this.query.paixuda !== undefined &&
            this.query.paixuxiao !== undefined;
          const categoryMatchId =
            this.query.categoryId ??
            this.query.goodscategoriesid ??
            this.query.goodscategoriesidid ??
            null;
          let data = res.data.map((v) => {
            // 
            let shouldSelect = false;
            if (hasPaixuQuery) {
              //  paixuda  paixuxiao  query 
              shouldSelect =
                String(v.paixuda) === String(this.query.paixuda) &&
                String(v.paixuxiao) === String(this.query.paixuxiao);
            } else if (
              categoryMatchId !== null &&
              categoryMatchId !== undefined &&
              categoryMatchId !== ""
            ) {
              // 
              shouldSelect =
                String(v.goodscategoriesid) === String(categoryMatchId);
            } else if (v.ispaixudapaixuxiao) {
              //  query 
              shouldSelect = true;
            }

            if (shouldSelect) {
              selectedArray.push(v?.goodsgoodsidid);
            }

            return {
              photosUrl: v.goodsfileslunbo[0]?.photosUrl,
              ispaixudapaixuxiao: shouldSelect,
              ...v,
            };
          });

          const categoryIds = [
            ...new Set(
              data
                .map((item) => item.goodscategoriesid)
                .filter((id) => id !== undefined && id !== null)
            ),
          ];
          let categoryNameMap = {};
          if (categoryIds.length) {
            categoryNameMap = await this.fetchCategoryNames(categoryIds);
          }
          data = data.map((item) => ({
            ...item,
            categoryName: categoryNameMap[item.goodscategoriesid] || "",
          }));

          if (isLoadMore) {
            // 
            this.list = [...this.list, ...data];
            this.page = page + 1;
            // 
            this.result = [...new Set([...this.result, ...selectedArray])];
          } else {
            // 
            this.list = data;
            this.page = 2; // 2
            // 
            this.result = selectedArray;
          }

          // 
          this.hasMore = res.data.length >= this.pageSize;

          console.log("selectedArray", this.result);
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      } finally {
        this.loading = false;
        this.isReachBottom = false;
      }
    },
    async fetchCategoryNames(ids) {
      const requests = ids.map((id) =>
        this.$request.get(`/goodscategories/${id}`)
      );
      const results = await Promise.allSettled(requests);
      const map = {};
      results.forEach((result, index) => {
        if (result.status === "fulfilled" && result.value?.code == 200) {
          map[ids[index]] = result.value.data?.name || "";
        }
      });
      return map;
    },
    // 
    loadMore() {
      if (this.hasMore && !this.loading) {
        this.getData(true);
      }
    },
    // 
    handleScroll() {
      if (this.loading || !this.hasMore || this.isReachBottom) {
        return;
      }

      // 
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      const windowHeight =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      const scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;

      // 50px
      if (scrollTop + windowHeight >= scrollHeight - 50) {
        if (!this.isReachBottom) {
          this.isReachBottom = true;
          this.loadMore();
        }
      }
    },
    // 
    initScrollListener() {
      window.addEventListener("scroll", this.handleScroll);
    },
    // 
    removeScrollListener() {
      window.removeEventListener("scroll", this.handleScroll);
    },
    delItem() {
      let choose = this.result.map((v) => v.id);
      this.$request.post("goodsdanping/del/batch", choose).then((res) => {
        console.log("", res);
        if (res.code == 200) {
          this.getData();
        }
      });
    },
    // 
    changeCheckboxGroup(a) {
      console.log("changeCheckboxGroup", a, this.result, this.query);
      this.goodsgoodsidid = a[0]?.goodsgoodsidid;
      // this.cancelSelected(a[a?.length - 1]);
    },
    cancelSelected(goodsgoodsidid) {
      this.$request
        .get("/goodsgoods/quxiaopaixudapaixuxiao", {
          params: { goodsgoodsidid },
        })
        .then((res) => {
          if (res.code == "200") {
            this.$router.push("/guanggao");
          } else {
            this.$toast("");
          }
        });
    },
    changeCheckbox(a) {
      console.log("changeCheckbox", a);
    },
    async go() {
      if (this.fromGuanggao) {
        console.log("go", this.query, this.result);
        const group = this.result.map((v) => {
          return {
            goodsgoodsidid: v,
            paixuda: Number(this.query.paixuda),
            paixuxiao: Number(this.query.paixuxiao),
          };
        });
        this.$request
          .post("/goodsgoods/batchUpdatepaixudapaixuxiao", group)
          .then((res) => {
            if (res.code == "200") {
              this.$router.push("/guanggao");
            } else {
              this.$toast("");
            }
          });
      } else {
        const categoryId =
          this.query.categoryId ??
          this.query.goodscategoriesid ??
          this.query.goodscategoriesidid ??
          null;
        if (!categoryId && categoryId !== 0) {
          this.$toast("");
          return;
        }
        if (!this.result?.length) {
          this.$toast("");
          return;
        }
        const payload = this.result.map((goodsgoodsidid) => ({
          goodsgoodsidid,
          goodscategoriesid: categoryId,
        }));
        try {
          const res = await this.$request.post(
            "/goodsgoods/batchUpdategoodscategoriesid",
            payload
          );
          if (res?.code === 200 || res?.code === "200") {
            this.$toast("");
            this.$router.go(-1);
          } else {
            this.$toast(res?.msg || "");
          }
        } catch (error) {
          console.error("", error);
          this.$toast("");
        }
      }
    },
    handleCheckboxClick(id, event) {
      console.log("handleCheckboxClick", event);
      const isChecked = event.target.checked; // 
      console.log(` ${id} ${isChecked ? "" : ""}`);
    },
  },

  mounted() {
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    console.log("$route", this.$route.query);
    if (user) {
      this.user = JSON.parse(user);
    }
    if (
      Object.keys(this.$route.query || {}).length > 0 ||
      this.$route.query.paixuda !== undefined
    ) {
      this.query = this.$route.query;
      console.log("select", this.query);
    }
    if (this.$route.query.fromGuanggao) {
      this.fromGuanggao = true;
    }
    this.getData();
    this.initScrollListener();
  },
  beforeDestroy() {
    this.removeScrollListener();
  },
};
</script>
