<template>
  <div style="display: flex; flex-direction: column; padding-bottom: 22px">
    <div style="display: flex" @click="handleClick">
      <img
        alt="logo"
        :src="data.headPhotoUrl"
        width="52"
        height="52"
        style="border-radius: 50%; object-fit: cover"
      />
      <div class="content-wrap">
        <div class="username">{{ data.username }}</div>
        <div class="content">{{ data.content }}</div>
        <div class="file-wrapper" id="images" ref="images">
          <img
            v-for="(img, index) in data.files"
            :key="index"
            :src="img.photosUrl"
            alt=""
            style="width: 120px; height: 120px; margin: 4px; object-fit: cover"
            @click="clickImg(img.photosUrl)"
          />
        </div>
        <div class="time">
          <div class="create-time">{{ data.createTime }}</div>
          <div style="display: flex; align-items: center">
            <img
              alt="logo"
              src="../assets/comments.png"
              width="15"
              height="13"
            /><span style="margin-left: 4px; object-fit: cover">{{
              data.commentNumber || 0
            }}</span>
          </div>
          <div style="display: flex; align-items: center">
            <img
              alt="logo"
              src="../assets/mine_like.png"
              width="15"
              height="13"
            /><span style="margin-left: 4px; object-fit: cover">{{
              data.loveNumber || 0
            }}</span>
          </div>
        </div>

        <div class="btn-wrap">
          <div class="manage-wrap">
            <div class="manage-btn">
              <div class="manage-connect" @click.stop="manageDelCommentEvt">
                {{ data.isDel ? "" : "" }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="btn-wrap">
            <div class="manage-wrap">
                <div class="manage-btn">
                    <div class="manage-connect" @click="manageDelCommentEvt">
                        
                    </div>
                </div>
            </div>
        </div> -->
  </div>
</template>

<script>
import "viewerjs/dist/viewer.css";
import Viewer from "viewerjs";
export default {
  data() {
    return {
      viewerOptions: {
        toolbar: false, // 
        navbar: true, // 
        title: true, // 
        fullscreen: true, // 
        movable: false, // 
        zoomable: true, // 
        loop: false,
      },
      gallery: null, //  Viewer 
    };
  },
  components: {
    // Viewer
  },
  props: {
    data: {},
  },
  mounted() {
    // this.gallery = new Viewer(document.getElementById('images'), this.viewerOptions);
    //  Viewer
    this.initViewer();
  },
  updated() {
    //  Viewer
    this.initViewer();
  },
  methods: {
    manageDelCommentEvt() {
      const userId = localStorage.getItem("bmk_userId") || "";
      let params = {
        articleId: this.data.articleId,
        articleIdid: this.data.articleIdid,
        status: "",
        userId: userId,
      };
      this.$request
        .post("article/updateArticleStatusBychaojiguanliyuan", params)
        .then((res) => {
          console.log("", res);
          if (res.code == "200") {
            // this.$emit('get')
            this.$set(this.data, "isDel", true);
          } else {
            console.log("");
          }
        });
    },
    initViewer() {
      //  DOM 
      this.$nextTick(() => {
        // 
        if (this.gallery) {
          this.gallery.destroy();
        }
        //  ref  Viewer 
        if (this.$refs.images) {
          this.gallery = new Viewer(this.$refs.images, this.viewerOptions);
        }
      });
    },
    clickImg() {
      //  Viewer
      if (this.gallery) {
        this.gallery.show();
      }
    },
    handleClick() {
      this.$emit("click", this.data);
    },
  },
  beforeDestroy() {
    //  Viewer 
    if (this.gallery) {
      this.gallery.destroy();
    }
  },
};
</script>

