<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
        <van-button
          plain
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="goChoose"
          >{{ isChoose ? "" : "" }}</van-button
        >
        <van-button
          plain
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="delOrCreate"
          >{{ isChoose ? "" : "" }}</van-button
        >
      </div>

      <div
        style="
          display: flex;
          padding: 20px 0px;
          border-radius: 15px 15px 0 0;
          margin-bottom: 30px;
        "
      >
        <img :src="logo2" style="width: 40px; height: 40px" />
        <div class="text" style="color: #999">
          
          <div style="color: #000">-</div>
        </div>
      </div>

      <van-cell-group
        style="padding: 0 10px; width: 100%; box-sizing: border-box"
        v-if="!isChoose"
      >
        <van-cell
          size="large"
          is-link
          title-style="display:flex;align-items: center"
          center
          v-for="item in list"
          :key="item.id"
          @click="goFenlei(item)"
        >
          <template #title>
            <img
              :src="item.tubiao"
              style="width: 56px; height: 56px; object-fit: cover"
            />
            <div
              style="
                display: flex;
                flex-direction: column;
                margin-left: 22px;
                align-items: flex-start;
                justify-content: space-between;
              "
            >
              <div style="font-size: 14px; font-weight: bold">
                {{ item.name }}
              </div>
              <div style="font-size: 12px; color: #999; font-weight: bold">
                {{ item.description }}
              </div>
              <div style="font-size: 12px; color: #999; font-weight: bold">
                {{ item.sortorder }}
              </div>
            </div>
          </template>
        </van-cell>
      </van-cell-group>

      <van-checkbox-group
        v-model="result"
        v-else
        style="padding: 0 10px; width: 100%; box-sizing: border-box"
        checked-color="#fa6286"
        @change="changeCheckboxGroup"
      >
        <van-cell-group
          style="padding: 0 10px; width: 100%; box-sizing: border-box"
        >
          <van-cell
            size="large"
            is-link
            title-style="display:flex;"
            center
            clickable
            :key="item.id"
            @click="toggle(index)"
            v-for="(item, index) in list"
          >
            <template #title>
              <img
                :src="item.tubiao"
                style="width: 56px; height: 56px; object-fit: cover"
              />
              <div
                style="
                  display: flex;
                  flex-direction: column;
                  margin-left: 22px;
                  align-items: flex-start;
                "
              >
                <div style="font-size: 14px; font-weight: bold">
                  {{ item.name }}
                </div>
                <div style="font-size: 12px; color: #999; font-weight: bold">
                  {{ item.description }}
                </div>
                <div style="font-size: 12px; color: #999; font-weight: bold">
                  {{ item.sortorder }}
                </div>
              </div>
            </template>
            <template #right-icon>
              <van-checkbox
                :name="item"
                ref="checkboxes"
                @change="changeCheckbox"
              />
            </template>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>
    </div>
  </div>
</template>
<script>
// import wode1 from '@/assets/wode2.png';
export default {
  name: "CenterComp",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      logo3: require("../assets/logo3.png"),
      list: [],
      result: [],
      isChoose: false,
    };
  },
  methods: {
    toggle(index) {
      // this.$refs.checkboxes[index].toggle();
      console.log("index", index);
    },
    goExit() {
      localStorage.clear();
      this.$router.push("/");
    },
    goChoose() {
      this.isChoose = !this.isChoose;
    },
    delOrCreate() {
      if (!this.isChoose) {
        this.$router.push("/category");
      } else {
        this.delItem();
      }
    },
    getData() {
      this.$request.get("/goodscategories").then((res) => {
        console.log("", res);
        if (res.code == 200) {
          this.list = res.data;
        }
      });
    },
    delItem() {
      let choose = this.result.map((v) => v.id);
      this.$request.post("goodscategories/del/batch", choose).then((res) => {
        console.log("", res);
        if (res.code == 200) {
          this.getData();
        }
      });
    },
    changeCheckboxGroup(a) {
      console.log("changeCheckboxGroup", a, this.result);
    },
    changeCheckbox(a) {
      console.log("changeCheckbox", a);
    },
    goFenlei(item) {
      this.$router.push({ path: "/category", query: item });
    },
    // 
    saveScrollPosition() {
      this.scrollPosition = window.scrollY;
      sessionStorage.setItem("goodsListScrollPosition", this.scrollPosition);
      sessionStorage.setItem("lastRoute", this.$route.name);
    },
    // 
    restoreScrollPosition() {
      const savedPosition = sessionStorage.getItem("goodsListScrollPosition");
      const lastRoute = sessionStorage.getItem("lastRoute");
      if (savedPosition && lastRoute == "category") {
        this.scrollPosition = parseInt(savedPosition);
        setTimeout(() => {
          window.scrollTo(0, this.scrollPosition);
        }, 100);
      }
    },
  },

  mounted() {
    const user = localStorage.getItem("bmk_user");
    console.log(" user", user);
    this.getData();
  },
  beforeRouteLeave(to, from, next) {
    this.saveScrollPosition();
    next();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.name === "category") {
        setTimeout(() => {
          vm.restoreScrollPosition();
        }, 0);
      }
    });
  },
};
</script>
