import CommentIcon from "@/components/icons/CommentIcon.vue";
import PublishIcon from "@/components/icons/PublishIcon.vue";
import ExitIcon from "@/components/icons/ExitIcon.vue";
export default {
  name: "CenterComp",
  components: {
    HomeIcon,
    GoodsIcon,
    CategoryIcon,
    ShopIcon,
    SupplierIcon,
    ItemIcon,
    DeliveryIcon,
    StatisticsIcon,
    ScanSortIcon,
    ScanDeliveryIcon,
    DetailIcon,
    PrintTicketIcon,
    OrderListIcon,
    BatchSettingIcon,
    OrderManageIcon,
    EditOrderIcon,
    WechatOrderIcon,
    UserSelectIcon,
    DeliveryUserIcon,
    MyDeliveryOrderIcon,
    AdIcon,
    FeedbackIcon,
    CouponIcon,
    QRCodeIcon,
    UserIcon,
    NewIcon,
    HotIcon,
    SearchIcon,
    CommentIcon,
    PublishIcon,
    ExitIcon,
  },
  data() {
    return {
      active: "",
      wode: {
        // active: '../assets/wode.png',
        // Vue CLIrequire()import
        active: require("../assets/wode.png"),
        // inactive: wode1,
        inactive: require("../assets/wode2.png"),
      },
      newest: {
        active: require("../assets/new.png"),
        inactive: require("../assets/new1.png"),
      },
      hot: {
        active: require("../assets/hot.png"),
        inactive: require("../assets/hot1.png"),
      },
      search: {
        active: require("../assets/search.png"),
        inactive: require("../assets/search1.png"),
      },
      comment: {
        active: require("../assets/search.png"),
        inactive: require("../assets/comments2.png"),
      },
      exit: {
        active: require("../assets/search.png"),
        inactive: require("../assets/exit2.png"),
      },
      shouye: {
        active: require("../assets/shouye.png"),
        inactive: require("../assets/shouye.png"),
      },
      logo2: require("../assets/logo2.png"),
      logo3: require("../assets/logo3.png"),
      guanggao: {
        active: require("../assets/guanggao.png"),
        inactive: require("../assets/guanggao.png"),
      },
      goods: {
        active: require("../assets/goods.png"),
        inactive: require("../assets/goods.png"),
      },
      category: {
        active: require("../assets/shuiguo.png"),
        inactive: require("../assets/shuiguo.png"),
      },
      shop: {
        active: require("../assets/location.png"),
        inactive: require("../assets/location.png"),
      },
      supplier: {
        active: require("../assets/peisong_goods.png"),
        inactive: require("../assets/peisong_goods.png"),
      },
      item: {
        active: require("../assets/upload-pic.png"),
        inactive: require("../assets/upload-pic.png"),
      },
      peisong: {
        active: require("../assets/navigate.png"),
        inactive: require("../assets/navigate.png"),
      },
      isConnected: false,
      clientId: null,
      connectionStatus: "",
      messages: [],
    };
  },
  mounted() {
    const user = localStorage.getItem("bmk_user");
    console.log("user", user);
  },
  methods: {
    goExit() {
      removeToken();
      localStorage.clear();
      this.$router.push("/");
    },
    connectSSE() {
      // ID
      const clientId = "user-" + Date.now();

      // SSE
      this.clientId = sseService.connect(clientId);
      this.isConnected = true;
      this.connectionStatus = "...";

      // 
      sseService.onMessage((data) => {
        this.handleMessage(data);
      });

      // 
      sseService.onError((error) => {
        this.connectionStatus = ": " + error.message;
        this.isConnected = false;
        this.addMessage("", ": " + error.message, "error");
      });
    },

    disconnectSSE() {
      sseService.disconnect();
      this.isConnected = false;
      this.connectionStatus = "";
      this.addMessage("", "", "system");
    },

    sendTestMessage() {
      if (!this.clientId) return;

      axios
        // .get(
        //   `${process.env.VUE_APP_API_BASE_URL}/sse/sendTestMessage?clientId=${this.clientId}`
        // )
        // .get(`http://192.168.1.55:9091/user/sse/sendTestMessage?clientId=1`)
        .get(`https://www.beimenkou.top/user/sse/sendTestMessage?clientId=1`)
        .then((response) => {
          this.addMessage("", response.data, "system");
        })
        .catch((error) => {
          this.addMessage(
            "",
            ": " + error.message,
            "error"
          );
        });
    },

    handleMessage(data) {
      if (data === "CONNECTED") {
        this.connectionStatus = "";
        this.addMessage("", "SSE", "system");
      } else if (data === "111222") {
        this.addMessage("", ": 111222", "test");
      } else {
        this.addMessage("", "" + data, "server");
      }
    },

    addMessage(source, content, type) {
      this.messages.unshift({
        time: new Date().toLocaleTimeString(),
        source,
        content,
        type,
      });

      // 
      if (this.messages.length > 50) {
        this.messages.pop();
      }
    },
  },
  beforeDestroy() {
    sseService.disconnect();
  },
};
</script>
