/* Updated Product Section Styles -  */
.product-section {
  margin-top: 8px;
  padding: 20px 16px 16px;
  background: linear-gradient(135deg, #fff 0%, #f8f9fb 100%);
  border-radius: 16px;
  box-shadow: 0 4px 16px rgba(250, 98, 134, 0.06);
  border: 1px solid rgba(250, 98, 134, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.product-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #fa6286 0%, #f5627a 100%);
  border-radius: 20px 20px 0 0;
}

.product-section:hover {
  box-shadow: 0 12px 40px rgba(250, 98, 134, 0.12);
  transform: translateY(-2px);
}

.product-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding: 0 4px 12px 4px;
  border-bottom: 2px solid rgba(250, 98, 134, 0.1);
  width: 100%;
  box-sizing: border-box;
  position: relative;
}

.product-header::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, #fa6286 0%, #f5627a 100%);
  border-radius: 1px;
}

.product-title {
  font-size: 20px;
  font-family: "SourceHanSansCN", sans-serif;
  font-weight: 700;
  color: #333;
  line-height: 1.2;
  position: relative;
}

.product-title::before {
  content: '';
  margin-right: 8px;
  font-size: 18px;
}

.product-summary {
  font-size: 14px;
  color: #666;
  line-height: 1.4;
  white-space: nowrap;
  background: rgba(250, 98, 134, 0.05);
  padding: 6px 12px;
  border-radius: 20px;
  border: 1px solid rgba(250, 98, 134, 0.1);
  font-weight: 500;
}

.product-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 0;
}

.product-item {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  background: #fff;
  border-radius: 12px;
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.04);
  position: relative;
  overflow: hidden;
  margin-bottom: 8px;
}

.product-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #fa6286 0%, #f5627a 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.product-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  border-color: rgba(250, 98, 134, 0.2);
}

.product-item:hover::before {
  opacity: 1;
}

.product-image {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 12px;
  flex-shrink: 0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease;
  border: 1px solid #f0f0f0;
}

.product-item:hover .product-image {
  transform: scale(1.05);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
}

.product-details {
  flex: 1;
  min-width: 0;
  padding-right: 8px;
}

.product-name {
  font-size: 14px;
  font-family: "SourceHanSansCN", sans-serif;
  font-weight: 600;
  color: #333;
  line-height: 1.4;
  margin-bottom: 8px;
  word-break: break-word;
  white-space: normal;
  text-align: left;
  display: block;
  overflow: visible;
  max-height: none;
}

.product-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 0;
  font-size: 12px;
  color: #666;
  line-height: 1.2;
}

.meta-label {
  font-weight: 500;
  color: #999;
  font-size: 11px;
}

.meta-value {
  font-weight: 600;
  color: #333;
  font-size: 12px;
}

/*  */
.product-meta {
  background: rgba(250, 98, 134, 0.05);
  padding: 4px 8px;
  border-radius: 6px;
  border: 1px solid rgba(250, 98, 134, 0.1);
}

.product-quantity {
  text-align: center;
  flex-shrink: 0;
  padding: 6px 10px;
  background: linear-gradient(135deg, #fa6286 0%, #f5627a 100%);
  border-radius: 8px;
  color: white;
