<template>
  <div class="global-container">
    <div class="top">
      <div class="header-section">
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 15px;
            font-weight: bold;
            transition: all 0.3s ease;
          "
          @click="$router.go(-1)"
        ></van-button>
        <h2 class="page-title"></h2>
      </div>
      <div class="pickup-container" v-if="order && order.id">
        <!--  -->
        <div class="order-card">


          <div class="order-header-header">
            <span class="order-status">{{ order.fenjianstatus || '' }}</span>
          </div>

        
          <div class="order-header">
            <h3 class="order-number">{{ order.goodsdingdannumber }}</h3>
          </div>
          <div class="order-info">
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ order.goodsuseridaddressname }}</span>
            </div>
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ order.goodsuseridaddressphone }}</span>
            </div>
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ order.goodsuseridaddressaddress }}</span>
            </div>
          </div>
          <div class="order-items" v-if="order.goodsdingdanitem && order.goodsdingdanitem.length > 0">
            <h4 class="items-title"></h4>
            <div v-for="good in order.goodsdingdanitem" :key="good.id" class="item-row">
              <img :src="good.goodsgoodsididshouphoto" alt="" class="item-image" />
              <div class="item-details">
                <span class="item-name">{{ good.goodsgoodsididname }}</span>
                <span class="item-quantity">x{{ good.goodsgoodsididnumber }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="no-orders">
        <p></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      order: {},
    };
  },
  methods: {
    async fetchOrder() {
      const orderId = this.$route.query.orderId;
      if (!orderId) {
        this.$toast('ID');
        return;
      }
      try {
        const response = await this.$request.get(`/goodsdingdan/findById`, {
          params: {
            id: orderId
          }
        });
        if (response?.code === '200') {
          this.order = response.data || {};
        } else {
          this.$toast('');
        }
      } catch (error) {
        console.error(':', error);
        this.$toast('');
      }
    },
  },
  mounted() {
    this.fetchOrder();
  },
};
</script>

<style scoped>
.top {
  margin: 0px;
  border-radius: 10px;
  padding: 20px 0px;
  box-sizing: border-box;
  display: unset;
  width: 100%;
  background-color: #f5f7fa;
}

.header-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  margin-bottom: 10px;
}

.page-title {
  text-align: center;
  color: #000;
  margin: 0;
  font-size: 28px;
  font-weight: bold;
  letter-spacing: 1px;
}

@media (min-width: 721px) {
  .global-container {
    background-color: #f5f7fa;
  }
  .top {
    width: 720px;
    background-color: #fff;
  }
  
  .header-section {
    padding: 0 20px;
  }
  
  .page-title {
    font-size: 32px;
    margin: 0;
  }
}

.pickup-container {
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  padding: 0 10px 20px; /* padding */
  font-family: Arial, sans-serif;
  text-align: left; /*  */
}

/*  */
.order-card {
  background: linear-gradient(135deg, #fa6286, #ff8fab);
  padding: 20px 10px;
  border-radius: 20px;
  margin-bottom: 0;
  color: white;
  position: relative;
  overflow: hidden;
}

.order-header {
  display: flex;
  justify-content: flex-start; /*  */
  align-items: center;
  margin-bottom: 15px;
  margin-top: 10px; /*  */
  flex-wrap: wrap; /*  */
  gap: 10px; /*  */
  text-align: left; /*  */
  padding: 0 5px;
}

.order-header-header {
  position: absolute;
  top: 10px;
  right: 15px;
  margin-bottom: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap; /*  */
  gap: 10px; /*  */
  text-align: left; /*  */
}

.order-number {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  color: white;
  flex: 1; /*  */
  min-width: 0; /* flex */
  word-break: break-word; /*  */
}

.order-status {
  background-color: rgba(255, 255, 255, 0.25);
  padding: 6px 14px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
  white-space: nowrap; /*  */
  flex-shrink: 0; /*  */
  backdrop-filter: blur(10px);
}

/*  */
@media (max-width: 720px) {
  .order-number {
    font-size: 16px; /*  */
  }
  
  .order-header {
    flex-direction: row; /* wrap */
    align-items: flex-start; /*  */
    margin-top: 20px; /*  */
    padding: 0 3px;
  }
  
  .order-status {
    order: 2; /*  */
    padding: 5px 12px;
    font-size: 11px;
  }
  
  .order-header-header {
    top: 8px;
    right: 12px;
  }
  
  .header-section {
    justify-content: flex-start;
    gap: 30px;
  }
  
  .page-title {
    font-size: 24px;
    text-align: left;
  }
}

.order-info {
  background-color: rgba(255, 255, 255, 0.15);
  padding: 15px 10px;
  border-radius: 15px;
  margin-bottom: 15px;
  text-align: left; /*  */
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.info-row {
  display: flex;
  margin-bottom: 8px;
  font-size: 14px;
}

.info-row:last-child {
  margin-bottom: 0;
}

.info-label {
  font-weight: bold;
  min-width: 60px;
  color: #fff;
}

.info-value {
  color: #fff;
  flex: 1;
  word-break: break-word;
}

/*  */
.order-items {
  background-color: rgba(255, 255, 255, 0.15);
  padding: 15px 10px;
  border-radius: 15px;
  text-align: left; /*  */
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.items-title {
  margin: 0 0 10px 0;
  font-size: 16px;
  font-weight: bold;
  color: white;
}

.item-row {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  padding: 8px;
  background-color: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  transition: background-color 0.2s ease;
}

.item-row:hover {
  background-color: rgba(255, 255, 255, 0.12);
}

.item-row:last-child {
  margin-bottom: 0;
}

.item-image {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.item-details {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.item-name {
  font-size: 14px;
  color: white;
  flex: 1;
}

.item-quantity {
  background-color: rgba(255, 255, 255, 0.25);
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  color: white;
  font-weight: bold;
}

/*  */
.no-orders {
  text-align: left; /*  */
  padding: 40px 20px;
  color: #999;
  font-size: 16px;
}
</style>
