<script>
import { BrowserMultiFormatReader } from "@zxing/library";

export default {
  components: {},
  props: {},
  data() {
    return {
      recentNumber: 0,
      list: [
        {
          name: "",
          guige: "220g/",
          number: 3,
          price: 125,
          scanNumber: 0,
          photosUrl: "",
          ord: "6925923780034",
          checked: false,
        },
        {
          name: "",
          guige: "330ml",
          number: 2,
          price: 25,
          scanNumber: 0,
          photosUrl: "",
          ord: "6974480214124",
          checked: false,
        },
      ],
      isScanning: false,
      result: [],
      user: null,
    };
  },
  methods: {
    updateStatus() {
      this.$request
        .post("/goodsdingdan/partialUpdate", {
          id: this.id,
          fenjianyuanuserid: this.user.userId,
          goodsdingdanstatus: "",
        })
        .then((res) => {
          console.log("======", res);
          if (res.code == "200") {
            this.$router.push("/scannerfenjian");
          } else {
            this.$Toast("");
          }
        });
    },
    // 
    async startScan() {
      this.isScanning = true;
      this.reader = new BrowserMultiFormatReader();

      try {
        // 1. 
        const devices = await this.reader.listVideoInputDevices();

        // 2. 
        let backCameraId = null;
        if (devices.length > 1) {
          backCameraId = devices.find(
            (device) =>
              device.label.toLowerCase().includes("back") ||
              device.label.includes("")
          )?.deviceId;
        }

        // 3. 
        const constraints = {
          video: {
            deviceId: backCameraId ? { exact: backCameraId } : undefined,
            facingMode: "environment",
            width: { ideal: 1920 },
            height: { ideal: 1080 },
          },
        };

        // 4. 
        await this.reader.decodeFromConstraints(
          constraints,
          this.$refs.video,
          async (result, err) => {
            if (result) {
              const scannedText = result.getText();
              await this.handleScanResult(scannedText);
              this.fetchPackageInfo(scannedText);
            }
            if (err && !(err instanceof DOMException)) {
              this.error = `: ${err.message}`;
            }
          }
        );
      } catch (err) {
        console.error(":", err);
        this.$toast("");
        this.isScanning = false;
      }
    },
    handleScanResult(scannedText) {
      this.$Toast(scannedText);
      const matchedItem = this.list.find((item) => item.ord == scannedText);

      if (matchedItem) {
        //  scanNumber
        if (matchedItem.scanNumber >= matchedItem.number) {
          this.$Toast(`${matchedItem.name} `);
          return;
        }
        if (matchedItem.scanNumber < matchedItem.number) {
          // scanNumber
          matchedItem.scanNumber = (matchedItem.scanNumber || 0) + 1;
          this.$Toast(`: ${matchedItem.name}`);
          this.recentNumber = (this.recentNumber || 0) + 1;

          if (matchedItem.scanNumber == matchedItem.number) {
            matchedItem.checked = true;
            this.$Toast("");
          }
        }
      } else {
        this.$Toast("");
      }
    },

    // 
    stopScan() {
      if (this.reader) {
        this.reader.reset();
      }
      this.isScanning = false;
    },

    async fetchPackageInfo(trackingNumber) {
      try {
        // this.packageInfo = {
        //   recipient: "",
        //   address: "123",
        //   trackingNumber,
        // };
        this.$request
          .post(`goodsdingdan/updateByfenjianyuanuserid`, {
            fenjianyuanuserid: this.user?.userId,
            goodsdingdannumber: trackingNumber,
          })
          .then((res) => {
            console.log("", res);
            this.$Toast(trackingNumber);
            if (res?.code == "200") {
              this.trackList.unshift({
                scannerNumber: trackingNumber,
                recipient: "",
                goodsuseridaddress: {
                  address: trackingNumber,
                },
              });
            } else {
              this.$Toast("");
            }
          })
          .catch((error) => {
            console.error("", error);
          });
      } catch (err) {
        console.error(":", err);
        this.error = "";
      }
    },
    changeCheckboxGroup(a) {
      console.log("changeCheckboxGroup", a, this.result);
    },
    changeCheckbox(a) {
      console.log("changeCheckbox", a);
    },
    getData(ord) {
      this.$request
        .get("/goodsdingdan/findByOrderNumber", {
          params: { goodsdingdannumber: ord },
          // params: { goodsdingdannumber: "ORD20250721234601701771184" },
        })
        .then((res) => {
          if (res.code == "200") {
            this.list = res.data?.goodsdingdanitem?.map((l) => ({
              ...l,
              name: l.goodsgoodsididname,
              // guige: "220g/",
              number: l.goodsgoodsididnumber,
              price: l.goodsgoodsididprice,
              scanNumber: 0,
              photosUrl: l.goodsgoodsididshouphoto,
              goodsgoodsidid: l.goodsgoodsidid,
              checked: false,
            }));
            this.address = res.data?.goodsuseridaddress?.address;
            this.total = res.data?.goodsgoodstotalnumber;
            this.id = res.data?.id;
            this.goodsdingdannumber = res.data?.goodsdingdannumber;
          } else {
            this.$Toast("");
          }
        });
    },
  },
  beforeDestroy() {
    this.stopScan();
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    if (user) {
      this.user = JSON.parse(user);
    }
    this.startScan();
    let ord = this.$route.query.ord;
    this.getData(ord);
  },
};
</script>

