      this.isverified = isverified;
      this.deliverymethods = deliverymethods;
      this.imgSrc = shoplogo;
      this.shopbanner = shopbanner;
      this.id = id;
    } else {
      this.$request
        .post(`/goodsshops/savewanye`, { chuangjianuserid: this.user.userId })
        .then((res) => {
          console.log("ID", res);
          this.goodsshopsidid = res.data?.goodsshopsidid;
          this.id = res.data?.id;
        });
    }
    this.$request.get(`/goodscategories`).then((res) => {
