<script>
import request from '@/utils/request';

export default {
  components: {},
  props: {},
  data() {
    return {
      favorableComment: [],
      ordersList: [],
      loading: false,
      pagination: {
        current: 1,
        size: 10,
        total: 0,
        pages: 0
      },
      statistics: {
        jinridingdan: 0,
        benzhoudingdan: 0,
        benyuedingdan: 0,
        daipeisongshuliang: 0,
        jinriyiwancheng: 0,
        zuoridingdan: 0
      },
    };
  },

  mounted() {
    this.loadOrders();
    this.loadStatistics();
  },

  methods: {
    async loadOrders(pageNum = 1) {
      try {
        this.loading = true;
        const response = await request({
          url: '/goodsdingdan/findBypeisongyuanuserid',
          method: 'get',
          params: {
            pageNum: pageNum,
            pageSize: this.pagination.size
          }
        });

        if (response.code == 200) {
          this.ordersList = response.data.records || [];
          // 
          this.pagination.current = response.data.current || pageNum;
          this.pagination.total = response.data.total || 0;
          this.pagination.pages = response.data.pages || 0;
        }
      } catch (error) {
        console.error(':', error);
        this.$toast('');
      } finally {
        this.loading = false;
      }
    },

    handlePageChange(page) {
      this.loadOrders(page);
    },

    async loadStatistics() {
      try {
        const response = await request({
          url: '/goodsdingdan/findBypeisongyuanuseriddeshuliang',
          method: 'get'
        });

        if (response.code == 200) {
          const data = response.data;
          this.statistics.jinridingdan = data.jinridingdan || 0;
          this.statistics.benzhoudingdan = data.benzhoudingdan || 0;
          this.statistics.benyuedingdan = data.benyuedingdan || 0;
          this.statistics.daipeisongshuliang = data.daipeisongshuliang || 0;
          this.statistics.jinriyiwancheng = data.jinriyiwancheng || 0;
          this.statistics.zuoridingdan = data.zuoridingdan || 0;
        }
      } catch (error) {
        console.error(':', error);
      }
    },

    getStatusText(order) {
      // activestatus
      if (order.activestatus === 'finish') {
        return '';
      } else if (order.activestatus === 'deliver') {
        return '';
      } else if (order.activestatus === 'choose') {
        return '';
      } else if (order.paymentstatus === '') {
        return '';
      } else if (order.paymentstatus === '') {
        return '';
      }
      return order.paymentstatus || '';
    },

    getStatusClass(order) {
      if (order.activestatus === 'finish') {
        return 'status-completed';
      } else if (order.activestatus === 'deliver') {
        return 'status-delivering';
      } else if (order.activestatus === 'choose') {
        return 'status-pending';
      } else if (order.paymentstatus === '') {
        return 'status-cancelled';
      } else if (order.paymentstatus === '') {
        return 'status-paid';
      }
      return 'status-unknown';
    },

    formatTime(timeStr) {
      if (!timeStr) return '';
      const date = new Date(timeStr);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  },
};
</script>

