<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
        <van-button
          plain
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="goChoose"
          >{{ isChoose ? "" : "" }}</van-button
        >
        <van-button
          plain
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="delOrCreate"
          >{{ isChoose ? "" : "" }}</van-button
        >
      </div>

      <div
        style="
          display: flex;
          padding: 20px 0px;
          border-radius: 15px 15px 0 0;
          margin-bottom: 30px;
        "
      >
        <img :src="logo2" style="width: 40px; height: 40px" />
        <div class="text" style="color: #999">
          
          <div style="color: #000">-</div>
        </div>
      </div>

      <van-search
        v-model="searchKeyword"
        placeholder=""
        show-action
        @search="onSearch"
        @clear="onClear"
        style="margin-bottom: 20px;"
      >
        <template #action>
          <div @click="onSearch"></div>
        </template>
      </van-search>

      <van-cell-group
        style="padding: 0 10px; width: 100%; box-sizing: border-box"
        v-if="!isChoose"
      >
        <van-cell
          size="large"
          is-link
          title-style="display:flex;"
          center
          v-for="item in list"
          :key="item.id"
          @click="go(item)"
        >
          <template #title>
            <van-image
              :src="item.photosUrl"
              width="56"
              height="56"
              fit="contain"
            />
            <div
              style="
                flex: 1;
                display: flex;
                flex-direction: column;
                margin-left: 22px;
                align-items: flex-start;
                justify-content: space-between;
              "
            >
              <div style="font-size: 14px; font-weight: bold; text-align: left">
                {{ item.name }}
              </div>
              <div
                style="
                  font-size: 12px;
                  color: #999;
                  font-weight: 700;
                  text-align: left;
                "
              >
                {{ item.jianduanjieshao }}
              </div>
            </div>
          </template>
        </van-cell>
      </van-cell-group>

      <van-checkbox-group
        v-model="result"
        v-else
        style="padding: 0 10px; width: 100%; box-sizing: border-box"
        checked-color="#fa6286"
        @change="changeCheckboxGroup"
      >
        <van-cell-group
          style="padding: 0 10px; width: 100%; box-sizing: border-box"
        >
          <van-cell
            size="large"
            is-link
            title-style="display:flex;"
            center
            clickable
            :key="item.id"
            @click="toggle(index)"
            v-for="(item, index) in list"
          >
            <template #title>
              <img
                :src="item.photosUrl"
                style="width: 56px; height: 56px; object-fit: cover"
              />
              <div
                style="
                  flex: 1;
                  display: flex;
                  flex-direction: column;
                  margin-left: 12px;
                "
              >
                <div
                  style="font-size: 14px; font-weight: bold; text-align: left"
                >
                  {{ item.name }}
                </div>
                <div style="font-size: 12px; color: #999; font-weight: 700">
                  {{ item.jianduanjieshao }}
                </div>
              </div>
            </template>
            <template #right-icon>
              <van-checkbox
                :name="item"
                ref="checkboxes"
                @change="changeCheckbox"
              />
            </template>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>

      <!--  -->
      <div class="loading-state" v-if="loading">
        <van-loading size="24px">...</van-loading>
      </div>

      <div class="loading-state" v-else-if="!hasMore && list.length > 0">
        <span></span>
      </div>

      <div class="loading-state" v-else-if="list.length === 0 && !loading">
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  name: "CenterComp",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      logo3: require("../assets/logo3.png"),
      list: [],
      result: [],
      isChoose: false,
      page: 1,
      pageSize: 10,
      searchKeyword: "",
      scrollPosition: 0,
      hasMore: true, // 
      loading: false, // 
      isReachBottom: false, // 
    };
  },
  methods: {
    goMenu() {
      this.$router.push("/center");
    },
    toggle(index) {
      console.log("index", index);
    },
    goExit() {
      localStorage.clear();
      this.$router.push("/");
    },
    goChoose() {
      this.isChoose = !this.isChoose;
    },
    delOrCreate() {
      if (!this.isChoose) {
        this.$router.push("/goodsmanage");
      } else {
        this.delItem();
      }
    },

    // 
    async getData(isLoadMore = false) {
      // 
      if (this.loading || (isLoadMore && !this.hasMore)) {
        return;
      }

      this.loading = true;

      try {
        const page = isLoadMore ? this.page : 1;

        const res = await this.$request.get(
          "/goodsgoods/getWanyeLatestGoodsgoods",
          {
            params: {
              page: page,
              pageSize: this.pageSize,
              userId: this.user?.userId,
            },
          }
        );

        console.log("", res);
        if (res?.code == 200) {
          let data = res.data.map((v) => {
            return { photosUrl: v.goodsfileslunbo[0]?.photosUrl, ...v };
          });

          if (isLoadMore) {
            // 
            this.list = [...this.list, ...data];
          } else {
            // 
            this.list = data;
          }
          // 
          this.page = page + 1;

          // 
          this.hasMore = data.length >= this.pageSize;

          console.log(
            `${isLoadMore ? "" : ""}: ${page}, : ${
              data.length
            }, : ${this.hasMore}`
          );
        } else {
          console.error(":", res);
          this.$toast(res?.msg || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      } finally {
        this.loading = false;
        this.isReachBottom = false;
      }
    },

    // 
    loadNextPage() {
      if (this.hasMore && !this.loading) {
        if (this.searchKeyword.trim()) {
          this.searchData(true);
        } else {
          this.getData(true);
        }
      }
    },

    // 
    onSearch() {
      this.resetPagination();
      if (this.searchKeyword.trim()) {
        this.searchData();
      } else {
        this.getData();
      }
    },

    // 
    onClear() {
      this.resetPagination();
      this.getData();
    },

    // 
    async searchData(isLoadMore = false) {
      if (this.loading || (isLoadMore && !this.hasMore)) {
        return;
      }

      this.loading = true;

      try {
        const page = isLoadMore ? this.page : 1;

        const res = await this.$request.get(
          "/goodsgoods/getWanyeshousuoGoodsgoods",
          {
            params: {
              page: page,
              pageSize: this.pageSize,
              goodsgoodsname: this.searchKeyword,
            },
          }
        );

        if (res?.code == 200) {
          let data = res.data.map((v) => {
            return { photosUrl: v.goodsfileslunbo[0]?.photosUrl, ...v };
          });

          if (isLoadMore) {
            this.list = [...this.list, ...data];
          } else {
            this.list = data;
          }
          this.page = page + 1;
          this.hasMore = data.length >= this.pageSize;
        } else {
          this.$toast(res?.msg || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      } finally {
        this.loading = false;
        this.isReachBottom = false;
      }
    },

    // 
    handleScroll() {
      if (this.loading || this.isReachBottom || !this.hasMore) {
        return;
      }

      // 
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      const windowHeight =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      const scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;

      // 50px
      if (scrollTop + windowHeight >= scrollHeight - 50) {
        if (!this.isReachBottom) {
          this.isReachBottom = true;
          this.loadNextPage();
        }
      }
    },

    // 
    initScrollListener() {
      window.addEventListener("scroll", this.handleScroll);
    },

    // 
    removeScrollListener() {
      window.removeEventListener("scroll", this.handleScroll);
    },

    delItem() {
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          let choose = this.result.map((v) => v.id);
          this.$request.post("/goodsgoods/del/batch", choose).then((res) => {
            console.log("", res);
            if (res.code == 200) {
              // 
              this.resetPagination();
              this.getData();
            }
          });
        })
        .catch((err) => {
          console.log("del===id===", err);
        });
    },

    // 
    resetPagination() {
      this.list = [];
      this.page = 1;
      this.hasMore = true;
      this.loading = false;
      this.result = [];
    },

    changeCheckboxGroup(a) {
      console.log("changeCheckboxGroup", a, this.result);
    },
    changeCheckbox(a) {
      console.log("changeCheckbox", a);
    },
    go(item) {
      this.$router.push({ path: "/goodsmanage", query: item });
    },

    // 
    saveScrollPosition() {
      this.scrollPosition = window.scrollY;
      sessionStorage.setItem("goodsListScrollPosition", this.scrollPosition);
      sessionStorage.setItem("lastRoute", this.$route.name);
    },

    // 
    restoreScrollPosition() {
      const savedPosition = sessionStorage.getItem("goodsListScrollPosition");
      const lastRoute = sessionStorage.getItem("lastRoute");
      if (savedPosition && lastRoute == "goods") {
        this.scrollPosition = parseInt(savedPosition);
        setTimeout(() => {
          window.scrollTo(0, this.scrollPosition);
        }, 100);
      }
    },
  },

  // 
  beforeRouteLeave(to, from, next) {
    this.saveScrollPosition();
    this.removeScrollListener();
    next();
  },

  // 
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // 
      if (from.name === "goodsmanage") {
        setTimeout(() => {
          vm.restoreScrollPosition();
        }, 0);
      }
    });
  },

  mounted() {
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    if (user) {
      this.user = JSON.parse(user);
    }
    this.getData();
    this.initScrollListener();
  },

  beforeUnmount() {
    this.removeScrollListener();
  },
};
</script>

