// src/utils/sse.js
import { EventSourcePolyfill } from 'event-source-polyfill';
class SSEService
{
    constructor()
    {
        this.eventSource = null;
        this.clientId = null;
        this.messageHandlers = [];
        this.errorHandlers = [];
    }

    connect (clientId)
    {
        // ID
        this.clientId = clientId || this.generateClientId();

        // 
        this.disconnect();

        // SSE

        // this.eventSource = new EventSourcePolyfill(`http://192.168.1.55:9091/user/sse/connect?clientId=${clientId}`, {
        this.eventSource = new EventSourcePolyfill(`https://www.beimenkou.top/user/sse/connect?clientId=${clientId}`, {
            // this.eventSource = new EventSourcePolyfill(` https://0ea6-2409-8a55-c2e-fc90-a51b-d974-c771-8016.ngrok-free.app/user/sse/connect?clientId=${clientId}`, {
            heartbeatTimeout: 60000, // 60
            // withCredentials: true // cookie
        });

        // 
        this.eventSource.onmessage = (event) =>
        {
            console.log("===event===", event);
            this.messageHandlers.forEach(handler => handler(event.data));
        };

        this.eventSource.onerror = (error) =>
        {
            this.errorHandlers.forEach(handler => handler(error));
            console.error('SSE Error:', error);
        };

        return this.clientId;
    }

    onMessage (handler)
    {
        console.log("====handler====", handler, handler.data);
        this.messageHandlers.push(handler);
    }

    onError (handler)
    {
        this.errorHandlers.push(handler);
    }

    disconnect ()
    {
        if (this.eventSource) {
            this.eventSource.close();
            this.eventSource = null;
        }
        this.messageHandlers = [];
        this.errorHandlers = [];
    }

    generateClientId ()
    {
        return 'client-' + Math.random().toString(36).substr(2, 9);
    }

    getClientId ()
    {
        return this.clientId;
    }
}

export default new SSEService();