import axios from "axios"
import router from "../router/router"
import { getToken } from './auth'

// axios
const request = axios.create({
  // baseURL: 'https://www.beimenkou.top:9092/', //   ip:port
  baseURL: 'https://www.beimenkou.top/', //   ip:port
  // baseURL: 'http://192.168.1.55:9091/', //   ip:port
  // baseURL: 'https://0ea6-2409-8a55-c2e-fc90-a51b-d974-c771-8016.ngrok-free.app/', //   ip:port
  timeout: 30000, // 30s
})

// request 
// 
// token
request.interceptors.request.use(
  (config) =>
  {
    // config.headers["Content-Type"] = "application/json;charset=utf-8" // 
    // token
    if (config.url.includes('user/registerUsernamePassword') || config.url.includes('user/loginUsernamePassword')) {
      console.log("");
      //  headers 
      config.headers = config.headers || {};

      //  token
      config.headers['token'] = '';
    } else {

      // let user = localStorage.getItem("bmk_token") || "" // 
      // config.headers["token"] = user // 

      const token = getToken()
      if (token) {
        config.headers['token'] = `${token}`
      }
      config.headers["ngrok-skip-browser-warning"] = "true";
    }
    console.log("config", config);

    // if(config.url.startsWith('/user')||config.url.startsWith('/imsingle')){
    //   config.baseURL="http://localhost:9080"
    // }
    return config
  },
  (error) =>
  {
    console.error("request error: " + error) // for debug
    return Promise.reject(error)
  }
)

// response 
// 
request.interceptors.response.use(
  (response) =>
  {
    let res = response.data
    console.log("response resss", res, typeof res, response)
    // 
    if (typeof res === "string") {
      res = res ? JSON.parse(res) : res
      console.log("typeof res === 'string'", res)
    }
    if (res.code === "401" || res.code === 401) {
      // 
      localStorage.removeItem("bmk_token")
      localStorage.removeItem("bmk_userId")
      localStorage.removeItem("bmk_username")
      localStorage.removeItem("bmk_user")
      localStorage.removeItem("access_token")
      
      // 
      router.push("/")
      
      // 
      if (typeof window !== 'undefined' && window.$toast) {
        window.$toast("")
      }
    }
    return res
  },
  (error) =>
  {
    console.error("response error: " + error) // for debug
    
    // 
    if (error.response) {
      const status = error.response.status
      if (status === 401) {
        // 
        localStorage.removeItem("bmk_token")
        localStorage.removeItem("bmk_userId")
        localStorage.removeItem("bmk_username")
        localStorage.removeItem("bmk_user")
        localStorage.removeItem("access_token")
        
        // 
        router.push("/")
        
        // 
        if (typeof window !== 'undefined' && window.$toast) {
          window.$toast("")
        }
      } else if (status === 403) {
        if (typeof window !== 'undefined' && window.$toast) {
          window.$toast("")
        }
      } else if (status >= 500) {
        if (typeof window !== 'undefined' && window.$toast) {
          window.$toast("")
        }
      }
    } else if (error.request) {
      // 
      if (typeof window !== 'undefined' && window.$toast) {
        window.$toast("")
      }
    }
    
    return Promise.reject(error)
  }
)

export default request
