<template>
  <div class="global-container">
    <div class="top">
      <div class="page">
        <div class="header">
          <van-button
            type="primary"
            color="#fa6286"
            style="
              border: 2px solid #fa6286;
              border-radius: 10px;
              font-weight: bold;
              width: auto;
              min-width: 70px;
              height: 44px;
              padding: 0 12px;
            "
            size="normal"
            @click="goBack"
          ></van-button>
          <div class="title-wrapper">
            <h1 class="title"></h1>
          </div>
        </div>
        <div class="form-container" ref="formContainer">
          <van-form>
            <van-field
              v-model="refundFee"
              type="number"
              label=""
              placeholder=""
              class="form-field"
            />
            <van-field
              v-model="comment"
              rows="3"
              type="textarea"
              label=""
              placeholder=""
              class="form-field"
            />
          </van-form>
          <van-button
            type="primary"
            color="#fa6286"
            block
            round
            size="large"
            native-type="submit"
            @click="submit"
            class="submit-btn"
          >
            
          </van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Button as VanButton } from "vant";

export default {
  components: {
    VanButton,
  },
  props: {},
  data() {
    return {
      items: [],
      user: {},
      comment: "",
      refundFee: "",
      id: "",
      outtradeno: "",
      outrefundno: "",
    };
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    if (user) {
      this.user = JSON.parse(user);
    }
    console.log("", this.$route.query);
    if (this.$route.query.id) {
      this.id = this.$route.query.id;
    }
    if (this.$route.query.outrefundno) {
      this.outrefundno = this.$route.query.outrefundno;
    }
    if (this.$route.query.outtradeno) {
      this.outtradeno = this.$route.query.outtradeno;
    }
  },
  methods: {
    // 
    goBack() {
      //  sessionStorage
      sessionStorage.setItem(
        "scrollPosition",
        window.pageYOffset || document.documentElement.scrollTop
      );
      this.$router.go(-1);
    },
    submit() {
      const inputAmount = parseFloat(this.refundFee);
      if (isNaN(inputAmount) || inputAmount <= 0) {
        this.$toast('');
        return;
      }
      //  1   100int
      const actualRefundFee = Math.round(inputAmount * 100);  // 

      this.$dialog.confirm({
        title: '',
        message: '',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {
        // 
        if (this.$refs.formContainer) {
          this.$refs.formContainer.classList.add('shadow-active');
        }

        this.$request
          .post("/wxPayController/refund", {
            id: this.id,
            refundFee: actualRefundFee,  //  100
            outtradeno: this.outtradeno,
            refundidbeizhu: this.comment,
            tuikuantibaouserid: this.user.userId,
            goodsdingdanstatus: "",
          })
          .then((res) => {
            console.log("======", res);
            if (res.code === 'SUCCESS') {
              // 
              const refundAmount = (res.data.amount.refund / 100).toFixed(2);
              // 
              if (this.$refs.formContainer) {
                this.$refs.formContainer.classList.remove('shadow-active');
              }
              // 
              this.$dialog.alert({
                title: '',
                message: `${refundAmount}`,
                confirmButtonText: ''
              });
              // 
            } else {
              this.$toast(res.msg || '');
              // 
              if (this.$refs.formContainer) {
                this.$refs.formContainer.classList.remove('shadow-active');
              }
            }
          })
          .catch(() => {
            this.$toast('');
            // 
            if (this.$refs.formContainer) {
              this.$refs.formContainer.classList.remove('shadow-active');
            }
          });
      }).catch(() => {
        // 
      });
    },
  },
};
</script>

