          style="
            border: 2px solid #fa6286;
            border-radius: 15px;
            font-weight: bold;
            transition: all 0.3s ease;
          "
          @click="$goMenu"
          ></van-button
        >
        <h2 class="page-title"></h2>
      </div>
      <div class="pickup-container">
        <!--  -->
        <div
          v-for="(item, index) in trackList"
          :key="item.id"
          class="order-card"
          @click="goToOrderItem(item)"
        >


          <!--  -->
          <div class="order-header">
            <div class="order-number-section">
              <div class="order-sequence">{{ trackList.length - index }}</div>
              <div class="order-number">
                <div class="order-number-main">{{ item.goodsdingdannumber.slice(0, -5) }}</div>
                <div class="order-number-suffix" v-if="item.goodsdingdannumber && item.goodsdingdannumber.length > 5">
                  {{ item.goodsdingdannumber.slice(-5) }}
                </div>
              </div>
            </div>
            <div class="order-status-badge">
              
            </div>
          </div>
          <div class="order-info">
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ item.goodsuseridaddressname }}</span>
            </div>
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ item.goodsuseridaddressphone }}</span>
            </div>
