            <h4 class="items-title"></h4>
            <div v-for="good in item.goodsdingdanitem" :key="good.id" class="item-row">
              <img :src="good.goodsgoodsididshouphoto" alt="" class="item-image" />
              <div class="item-details">
                <span class="item-name">{{ good.goodsgoodsididname }}</span>
