</template>

<script>
export default {
  data() {
    return {
      trackList: [],
      user: {},
    };
  },
  methods: {
    // goToOrderItem(item) {
    //   this.$router.push({
    //     name: 'fenjiandingdanitem',
    //     query: { orderId: item.id }
    //   });
    // },

    goToOrderItem(item) {
  this.$router.push({
    name: 'fenjiandingdanitem',
    query: { order: encodeURIComponent(JSON.stringify(item)) }
  });
},
    async fetchOrders() {
      try {
        const response = await this.$request.get(`/goodsdingdan/findByFenjianyuanuseridAndStatus`, {
          params: {
            fenjianyuanuserid: this.user?.userId,
            fenjianstatus: ''
          }
        });
        if (response?.code === '200') {
          this.trackList = response.data || [];
        } else {
          this.$toast('');
        }
      } catch (error) {
        console.error(':', error);
        this.$toast('');
      }
    },
  },
  mounted() {
    const user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
    this.fetchOrders();
  },
};
</script>

