<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
          padding: 0 30px;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
      </div>
      <div class="pickup-container">
        <h2></h2>

        <div v-if="isScanning" class="scan-area">
          <div class="scanner-frame">
            <video
              ref="video"
              autoplay
              playsinline
              class="scanner-video"
            ></video>
            <div class="scanner-overlay">
              <div class="corner top-left"></div>
              <div class="corner top-right"></div>
              <div class="corner bottom-left"></div>
              <div class="corner bottom-right"></div>
            </div>
          </div>
        </div>

        <!--  -->
        <div
          v-for="(item, index) in trackList"
          :key="item.scannerNumber"
          class="package-info"
        >
          <h3 style="color: white;">{{ trackList.length - index }}</h3>
          <p style="color: white;">{{ item.scannerNumber }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { BrowserMultiFormatReader, DecodeHintType, BarcodeFormat } from "@zxing/library";

export default {
  data() {
    return {
      trackList: [],
      isScanning: false,
      reader: null,
      scannedText: "",
      user: {},
    };
  },
  methods: {
    // 
    async startScan() {
      this.isScanning = true;

      //  QR  TRY_HARDER 
      const hints = new Map();
      hints.set(DecodeHintType.POSSIBLE_FORMATS, [BarcodeFormat.QR_CODE]);
      hints.set(DecodeHintType.TRY_HARDER, false);
      this.reader = new BrowserMultiFormatReader(hints);

      try {
        // 1. 
        const devices = await this.reader.listVideoInputDevices();

        // 2. 
        let backCameraId = null;
        if (devices.length > 1) {
          backCameraId = devices.find(
            (device) =>
              device.label.toLowerCase().includes("back") ||
              device.label.includes("")
          )?.deviceId;
        }

        // 3.  - 
        const constraints = {
          video: {
            deviceId: backCameraId ? { exact: backCameraId } : undefined,
            facingMode: "environment",
            width: { ideal: 640 }, // 
            height: { ideal: 480 }, // 
            frameRate: { ideal: 30 }, // 
          },
        };

        // 4. 
        await this.reader.decodeFromConstraints(
          constraints,
          this.$refs.video,
          (result, err) => {
            if (result) {
              const scannedText = result.getText();
              this.scannedText = scannedText;

              let isDuplicate = this.trackList.some(
                (item) => item.scannerNumber === scannedText
              );

              if (isDuplicate) {
                this.$toast("");
                return;
              }

              // 
              this.$toast("");

              // 
              this.fetchPackageInfo(scannedText); // 
            }
            if (err && !(err instanceof DOMException)) {
              console.error(":", err);
            }
          }
        );
      } catch (err) {
        console.error(":", err);
        this.$toast("");
        this.isScanning = false;
      }
    },

    // 
    stopScan() {
      if (this.reader) {
        this.reader.reset();
      }
      this.isScanning = false;
    },

    async fetchPackageInfo(trackingNumber) {
      try {
        this.$request
          .post(`/goodsdingdan/goodsdingdanfenjianyuanuserid`, {
            fenjianyuanuserid: this.user?.userId,
            goodsdingdannumber: trackingNumber,
          })
          .then((res) => {
            console.log("", res);
            if (res?.code == "200") {
              this.trackList.unshift({
                scannerNumber: trackingNumber,
                recipient: "",
                goodsuseridaddress: {
                  address: trackingNumber,
                },
              });
            } else {
              this.$toast("");
            }
          })
          .catch((error) => {
            console.error("", error);
          });
      } catch (err) {
        console.error(":", err);
      }
    },
  },
  beforeDestroy() {
    this.stopScan();
  },
  mounted() {
    this.startScan();
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
  },
};
</script>

