<template>
  <div class="mobile-order-detail">
    <!--  -->
    <div class="status-bar">
      <div class="status-text">{{ order.goodsdingdanstatus || '' }}</div>
      <div class="amount">{{ order.shijiprice || 0 }}</div>
    </div>

    <!--  -->
    <div class="section" v-if="order.paymentstatus === ''">
      <div class="section-title"></div>
             <div class="timeline">
         <div v-for="item in statusList" :key="item.code" class="timeline-item">
           <div class="timeline-dot" :class="{ active: activeStatus === item.code }"></div>
           <div class="timeline-content">
             <div class="timeline-text">{{ item.name }}</div>
           </div>
         </div>
       </div>
    </div>

    <!--  -->
    <div class="section">
      <div class="section-title"></div>
      <div class="products">
        <div v-for="item in order.goodsdingdanitem" :key="item.id" class="product-card">
          <img :src="item.goodsgoodsididshouphoto" class="product-img" />
          <div class="product-text">
            <div class="product-name">{{ item.goodsgoodsididname }}</div>
            <div class="product-info">: {{ item.goodsgoodsididnumber }} | : {{ item.goodsgoodsididprice }}</div>
            <div class="product-price">: {{ item.goodsgoodsididsubtotal }}</div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="price-detail">
        <div class="price-row"><span></span><span>{{ order.totalprice || 0 }}</span></div>
        <div class="price-row"><span></span><span>{{ order.peisongprice || 0 }}</span></div>
        <div class="price-row total"><span></span><span>{{ order.shijiprice || 0 }}</span></div>
      </div>
    </div>

    <!--  -->
    <div class="section" v-if="order.leixing === ''">
      <div class="section-title"></div>
      <div class="info-card">
        <div class="info-row">
          <span class="label"></span>
          <span class="value">{{ order.goodsuseridaddress?.address || '-' }}</span>
        </div>
        <div class="info-row">
          <span class="label"></span>
          <span class="value">{{ order.goodsuseridaddress?.name }} {{ order.goodsuseridaddress?.phone }}</span>
        </div>
        <div class="info-row">
          <span class="label"></span>
          <span class="value">{{ order.comment || '' }}</span>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="section">
      <div class="section-title"></div>
      <div class="info-card">
        <div class="info-row">
          <span class="label"></span>
          <span class="value small">{{ order.goodsdingdannumber }}</span>
        </div>
        <div class="info-row">
          <span class="label"></span>
          <span class="value">{{ formatTimeC(order.createdata) }}</span>
        </div>
      </div>
      <div class="action-bar">
        <van-button type="primary" size="large" @click="copy(order.goodsdingdannumber)"></van-button>
        <van-button type="info" size="large" @click="refresh"></van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {
  name: 'MobileOrderDetail',
  data() {
    return {
      order: {
        goodsdingdanstatus: '',
        shijiprice: 125,
        totalprice: 120,
        peisongprice: 5,
        goodsdingdanitem: [
          { id: 1, goodsgoodsididshouphoto: '', goodsgoodsididname: '', goodsgoodsididnumber: 2, goodsgoodsididprice: 50, goodsgoodsididsubtotal: 100 }
        ],
        goodsuseridaddress: { address: '', name: '', phone: '13800138000' },
        goodsdingdannumber: 'TEST001',
        comment: '',
        createdata: '2025-01-01 10:00',
        paymentstatus: '',
        leixing: '',
      },
      activeStatus: 'choose',
      statusList: [
        { name: '', code: 'pay' },
        { name: '', code: 'choose' },
        { name: '', code: 'deliver' },
        { name: '', code: 'finish' },
      ],
    };
  },
  methods: {
    formatTimeC(date) {
      if (!date) return '';
      const d = new Date(date);
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')} ${String(d.getHours()).padStart(2, '0')}:${String(d.getMinutes()).padStart(2, '0')}`;
    },
    async copy(number) {
      try {
        await navigator.clipboard.writeText(number);
        Toast.success('');
      } catch (err) {
        Toast.fail('');
      }
    },
    async refresh() {
      Toast.success('');
    },
  },
};
</script>

<style scoped>
.mobile-order-detail {
  background: #f5f5f5;
  min-height: 100vh;
  padding-bottom: 20px;
}

.status-bar {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 30px 20px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.status-text {
  font-size: 32px;
  font-weight: bold;
}

.amount {
  font-size: 36px;
  font-weight: bold;
}

.section {
  background: white;
  margin: 20px 15px;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.section-title {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

.timeline {
  position: relative;
  padding-left: 30px;
}

.timeline-item {
  position: relative;
  padding-bottom: 30px;
}

.timeline-dot {
  position: absolute;
  left: -25px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #ddd;
  border: 3px solid white;
  box-shadow: 0 0 0 2px #ddd;
}

.timeline-dot.active {
  background: #667eea;
  box-shadow: 0 0 0 2px #667eea;
}

.timeline-text {
  font-size: 26px;
  color: #666;
}

.products {
  margin-bottom: 20px;
}

.product-card {
  display: flex;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
}

.product-card:last-child {
  border-bottom: none;
}

.product-img {
  width: 120px;
  height: 120px;
  border-radius: 8px;
  object-fit: cover;
}

.product-text {
  flex: 1;
  margin-left: 20px;
}

.product-name {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 8px;
  color: #333;
}

.product-info {
  font-size: 24px;
  color: #999;
  margin-bottom: 8px;
}

.product-price {
  font-size: 26px;
  color: #ff4757;
  font-weight: bold;
}

.price-detail {
  border-top: 1px solid #eee;
  padding-top: 20px;
  margin-top: 20px;
}

.price-row {
  display: flex;
  justify-content: space-between;
  font-size: 26px;
  margin-bottom: 12px;
  color: #666;
}

.price-row.total {
  font-size: 32px;
  font-weight: bold;
  color: #ff4757;
  padding-top: 12px;
  border-top: 1px solid #eee;
  margin-top: 12px;
}

.info-card {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
}

.info-row {
  display: flex;
  margin-bottom: 20px;
  font-size: 26px;
}

.info-row:last-child {
  margin-bottom: 0;
}

.info-row .label {
  width: 120px;
  color: #999;
}

.info-row .value {
  flex: 1;
  color: #333;
  text-align: right;
}

.info-row .value.small {
  font-size: 22px;
}

.action-bar {
  margin-top: 20px;
}

.action-bar button {
  margin-bottom: 12px;
  border-radius: 25px;
  font-size: 28px;
}

.action-bar button:last-child {
  margin-bottom: 0;
}
</style>
