<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
          padding: 0 30px;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
      </div>
      <div class="pickup-container">
        <h2></h2>

        <!--  -->
        <div class="input-group">
          <label for="trackingNumber"></label>
          <input
            id="trackingNumber"
            v-model="trackingNumber"
            type="text"
            placeholder=""
          />
        </div>

        <!--  -->
        <div v-if="isScanning" class="scan-area">
          <video ref="video" autoplay playsinline class="scanner-video"></video>
          <button @click="stopScan" class="stop-scan-btn"></button>
        </div>
        <!--  -->
        <div v-if="trackingNumber" class="package-info">
          <h3></h3>
          <p>{{ trackingNumber }}</p>
          <p>{{ packageInfo.recipient || "..." }}</p>
          <p>{{ packageInfo.address || "..." }}</p>
        </div>

        <!--  -->
        <div class="action-buttons">
          <button
            @click="confirmPickup"
            :disabled="!trackingNumber"
            class="confirm-btn"
          >
            
          </button>
        </div>
        <button @click="startScan" class="scan-btn"></button>
      </div>
    </div>
  </div>
</template>

<script>
import { BrowserMultiFormatReader } from "@zxing/library";

export default {
  data() {
    return {
      trackingNumber: "",
      isScanning: false,
      reader: null,
      packageInfo: {
        recipient: "",
        address: "",
      },
      error: null,
    };
  },
  methods: {
    // 
    async startScan() {
      this.isScanning = true;
      this.reader = new BrowserMultiFormatReader();

      try {
        // 1. 
        const devices = await this.reader.listVideoInputDevices();

        // 2. 
        let backCameraId = null;
        if (devices.length > 1) {
          backCameraId = devices.find(
            (device) =>
              device.label.toLowerCase().includes("back") ||
              device.label.includes("")
          )?.deviceId;
        }

        // 3. 
        const constraints = {
          video: {
            deviceId: backCameraId ? { exact: backCameraId } : undefined,
            facingMode: "environment",
            width: { ideal: 1920 },
            height: { ideal: 1080 },
          },
        };

        // 4. 
        await this.reader.decodeFromConstraints(
          constraints,
          this.$refs.video,
          (result, err) => {
            if (result) {
              this.trackingNumber = result.getText(); // 
              this.stopScan();
              this.fetchPackageInfo(result.getText()); // 
            }
            if (err && !(err instanceof DOMException)) {
              this.error = `: ${err.message}`;
            }
          }
        );
      } catch (err) {
        console.error(":", err);
        this.error = "";
        this.isScanning = false;
      }
    },

    // 
    stopScan() {
      if (this.reader) {
        this.reader.reset();
      }
      this.isScanning = false;
    },

    // 
    async fetchPackageInfo(trackingNumber) {
      try {
        // API
        this.packageInfo = {
          recipient: "",
          address: "123",
          // ...
          trackingNumber,
        };
      } catch (err) {
        console.error(":", err);
        this.error = "";
      }
    },

    // 
    async confirmPickup() {
      if (!this.trackingNumber) {
        this.error = "";
        return;
      }

      try {
        // API
        alert(` ${this.trackingNumber} `);
        // 
        this.resetForm();
      } catch (err) {
        console.error(":", err);
        this.error = "";
      }
    },

    // 
    resetForm() {
      this.trackingNumber = "";
      this.packageInfo = {
        recipient: "",
        address: "",
      };
      this.error = null;
    },
  },
  beforeDestroy() {
    this.stopScan();
  },
};
</script>

