/**
 * 
 * Web Bluetooth API
 */
import printer from './printer';

// BLEUUIDAPRINTER
// UUID: 49535343-fe7d-4ae5-8fa9-9fafd205e455
// /RXUUID: 49535343-8841-43f4-a8d4-ecbe34729bb3
// TXUUID: 49535343-1e4d-4bd9-ba61-23c647249616
// UUID: 49535343-aca3-481c-91ec-d85e28a60318
const SERVICE_UUID = '49535343-fe7d-4ae5-8fa9-9fafd205e455';
const RX_CHARACTERISTIC_UUID = '49535343-8841-43f4-a8d4-ecbe34729bb3'; // 
const TX_CHARACTERISTIC_UUID = '49535343-1e4d-4bd9-ba61-23c647249616'; // 
// 

class PrintService {

    constructor() {
      this.device = null;
      this.server = null;
      this.rxCharacteristic = null;
      this.txCharacteristic = null;
      this.connected = false;
    }

    // 
    async connect() {
      // Web Bluetooth API
      if (!navigator.bluetooth) {
        console.error('Web Bluetooth API');
        return false;
      }
      try {
        // 
        this.device = await navigator.bluetooth.requestDevice({
          acceptAllDevices: true,
          optionalServices: [SERVICE_UUID]
        });
        // 
        this.device.addEventListener('gattserverdisconnected', () => {
          this.connected = false;
          this.server = null;
          this.rxCharacteristic = null;
          this.txCharacteristic = null;
        });
        // GATT
        this.server = await this.device.gatt.connect();
        // 
        const service = await this.server.getPrimaryService(SERVICE_UUID);
        // RX
        this.rxCharacteristic = await service.getCharacteristic(RX_CHARACTERISTIC_UUID);
        // TX
        this.txCharacteristic = await service.getCharacteristic(TX_CHARACTERISTIC_UUID);
        // TX
        if (this.txCharacteristic) {
          await this.txCharacteristic.startNotifications();
          //  characteristicvaluechanged 
          // 
          // this.txCharacteristic.addEventListener('characteristicvaluechanged', (event) => {
          //   //  event.target.value
          // });
        }
        this.connected = true;
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    }

    // 
    async disconnect() {
      if (this.device && this.server) {
        try {
          await this.server.disconnect();
          this.connected = false;
          this.server = null;
          this.rxCharacteristic = null;
          this.txCharacteristic = null;
          return true;
        } catch (error) {
          console.error(':', error);
          return false;
        }
      }
      return true;
    }

    // 
    isConnected() {
      return this.connected && this.device !== null;
    }

    // 
    async sendData(data) {
      if (!this.isConnected() || !this.rxCharacteristic) {
        throw new Error('');
      }
      try {
        // 128/256
        const chunkSize = 256;
        for (let i = 0; i < data.length; i += chunkSize) {
          const chunk = data.slice(i, i + chunkSize);
          await this.rxCharacteristic.writeValueWithoutResponse(chunk);
        }
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    }

    // 
    async printReceipt(data) {
      try {
        if (!this.isConnected()) {
          const connected = await this.connect();
          if (!connected) {
            throw new Error('');
          }
        }
        const buffer = printer.printReceipt(data);
        await this.sendData(buffer);
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    }

    // 
    async printTestPage() {
      const testData = {
        title: '',
        subtitle: '',
        date: new Date().toLocaleString(),
        orderNo: 'TEST' + Date.now().toString().slice(-8),
        items: [
          { name: '1', quantity: 2, price: 10.00, amount: 20.00 },
          { name: '2', quantity: 1, price: 15.50, amount: 15.50 },
        ],
        total: '35.50',
        qrcode: 'https://example.com/test',
        footer: ''
      };
      return await this.printReceipt(testData);
    }
  }

  export default new PrintService();