<script>
import { BrowserMultiFormatReader, DecodeHintType, BarcodeFormat } from "@zxing/library";
import GoodsIcon from "@/components/icons/GoodsIcon.vue";

export default {
  components: {
    GoodsIcon,
  },
  data() {
    return {
      trackList: [],
      isScanning: false,
      reader: null,
      scannedText: "",
      user: {},
      cancelOrderInput: "", // 
      cancelOrderError: "", // 
      showCancelOrderDialog: false, // 
      currentCancelOrder: null, // 
    };
  },
  computed: {
    // 
    totalGoodsCount() {
      return this.trackList.reduce((total, order) => {
        return total + (order.goodsgoodstotalnumber || 0);
      }, 0);
    },
    // 
    totalPrice() {
      return this.trackList.reduce((total, order) => {
        return total + (parseFloat(order.totalprice) || 0);
      }, 0);
    },
  },
  methods: {
    // 
    goBack() {
      this.$router.go(-1);
    },
    // 
    async copyToClipboard(item) {
      const { ditudingwei } = item.goodsuseridaddress;
      if (navigator.clipboard) {
        try {
          await navigator.clipboard.writeText(ditudingwei);
          this.$Toast("");
          return;
        } catch (err) {
          console.warn("", err);
        }
      }
      this.copyFallback(ditudingwei);
    },
    copyFallback(ditudingwei) {
      const input = document.createElement("input");
      input.value = ditudingwei;
      document.body.appendChild(input);
      input.select();
      document.execCommand("copy");
      document.body.removeChild(input);
      this.$Toast("");
    },
    copyAndCall(phone) {
      this.copyTelephone(phone);
      window.location.href = `tel:${phone}`;
    },
    copyTelephone(text) {
      const input = document.createElement("input");
      input.value = text;
      document.body.appendChild(input);
      input.select();
      document.execCommand("copy");
      document.body.removeChild(input);
    },
    report(id, operate, outtradeno) {
      console.log("report===id===", id);
      let path;
      switch (operate) {
        case "xiangqing":
          path = "/peisongdetail";
          break;
        case "baobei":
          path = "/abnormalreport";
          break;
        case "weizhi":
          path = "/peisonglocation";
          break;
        case "tuikuan":
          path = "/refund";
          break;
      }
      this.$router.push({ path, query: { id, outtradeno } });
    },
    // 
    confirmCancelOrder(item) {
      const orderNumber = item.goodsdingdannumber || "";

      if (!orderNumber || orderNumber.length < 5) {
        this.$toast("");
        return;
      }

      // 
      this.currentCancelOrder = item;
      this.cancelOrderInput = "";
      this.cancelOrderError = "";
      this.showCancelOrderDialog = true;
    },

    // 
    handleCancelOrderInput(value) {
      // 5
      const numbersOnly = value.replace(/\D/g, "");
      this.cancelOrderInput = numbersOnly.slice(0, 5); // 5
      // 
      this.cancelOrderError = "";
    },

    // 
    handleCancelOrderConfirm() {
      if (!this.currentCancelOrder) {
        return;
      }

      const orderNumber = this.currentCancelOrder.goodsdingdannumber || "";
      const lastFiveDigits = orderNumber.slice(-5);
      const userInput = this.cancelOrderInput.trim();

      // 
      this.cancelOrderError = "";

      if (!userInput) {
        this.cancelOrderError = "";
        this.$toast("");
        return;
      }

      // 5
      if (!/^\d{5}$/.test(userInput)) {
        this.cancelOrderError = "5";
        this.$toast("5");
        return;
      }

      // 
      if (userInput !== lastFiveDigits) {
        this.cancelOrderError = "";
        this.$toast("");
        // 
        this.cancelOrderInput = "";
        return;
      }

      // 
      if (confirm(`\n${orderNumber}`)) {
        // 
        this.cancelOrderWithVerification(this.currentCancelOrder);
      } else {
        // 
        console.log("");
      }
    },

    // 
    cancelOrderWithVerification(item) {
      this.$request
        .post("/goodsdingdan/guanliyuanquxiaogoodsdingdan", {
          id: item.id,
          dingdanquxiaotibaouserid: this.user.userId,
        })
        .then((res) => {
          console.log("======", res);
          if (res.code == 200) {
            // 
            this.$toast(res.msg || "");
            // 
            this.currentCancelOrder = null;
            this.cancelOrderInput = "";
            this.cancelOrderError = "";
            // 
            this.showCancelOrderDialog = false;
            // 
            const index = this.trackList.findIndex(order => order.id === item.id);
            if (index > -1) {
              this.trackList.splice(index, 1);
            }
          } else {
            this.$toast(res.msg || "");
          }
        })
        .catch((error) => {
          console.error("", error);
          this.$toast("");
        });
    },
    confirmDeliver(item) {
      // 
      this.$router.push({
        path: "/songdapaizhao",
        query: {
          id: item.id,
          outtradeno: item.outtradeno
        }
      });
    },
    async deliverOrder(item) {
      try {
        const res = await this.$request.post("/goodsdingdan/partialUpdate", {
          id: item.id,
          peisongyuanuserid: this.user.userId,
          peisongstatus: "",
        });
        console.log("======", res);
        if (res.code == 200) {
          this.$Toast("");
          // 
          const index = this.trackList.findIndex(order => order.id === item.id);
          if (index > -1) {
            this.trackList.splice(index, 1);
          }
        } else {
          this.$Toast("");
        }
      } catch (error) {
        console.error(":", error);
        this.$Toast("");
      }
    },
    openMap(item) {
      const { address, latitude, longitude } = item.goodsuseridaddress;
      const universalUrl = `https://uri.amap.com/navigation?to=${longitude},${latitude},${encodeURIComponent(
        address
      )}&mode=car&src=yourAppName`;
      window.location.href = universalUrl;
    },
    formatTimestamp(timestamp) {
      if (!/^\d{14}$/.test(timestamp)) {
        console.warn("14");
        return timestamp;
      }

      const month = timestamp.substring(4, 6);
      const day = timestamp.substring(6, 8);
      const hour = timestamp.substring(8, 10);
      const minute = timestamp.substring(10, 12);

      const formattedMonth = parseInt(month, 10);
      const formattedDay = parseInt(day, 10);
      const formattedHour = parseInt(hour, 10);
      const formattedMinute = parseInt(minute, 10);

      return `${formattedMonth}${formattedDay}${formattedHour}:${formattedMinute
        .toString()
        .padStart(2, "0")}`;
    },

    // 
    async startScan() {
      this.isScanning = true;

      //  QR  TRY_HARDER 
      const hints = new Map();
      hints.set(DecodeHintType.POSSIBLE_FORMATS, [BarcodeFormat.QR_CODE]);
      hints.set(DecodeHintType.TRY_HARDER, false);
      this.reader = new BrowserMultiFormatReader(hints);

      try {
        // 1. 
        const devices = await this.reader.listVideoInputDevices();

        // 2. 
        let backCameraId = null;
        if (devices.length > 1) {
          backCameraId = devices.find(
            (device) =>
              device.label.toLowerCase().includes("back") ||
              device.label.includes("")
          )?.deviceId;
        }

        // 3.  - 
        const constraints = {
          video: {
            deviceId: backCameraId ? { exact: backCameraId } : undefined,
            facingMode: "environment",
            width: { ideal: 640 }, // 
            height: { ideal: 480 }, // 
            frameRate: { ideal: 30 }, // 
          },
        };

        // 4. 
        await this.reader.decodeFromConstraints(
          constraints,
          this.$refs.video,
          (result, err) => {
            if (result) {
              const scannedText = result.getText();
              this.scannedText = scannedText;

              let isDuplicate = this.trackList.some(
                (item) => item.scannerNumber === scannedText
              );

              if (isDuplicate) {
                return;
              }

              // 
              this.fetchPackageInfo(scannedText); // 
            }
            if (err && !(err instanceof DOMException)) {
              console.error(":", err);
            }
          }
        );
      } catch (err) {
        console.error(":", err);
        this.$toast("");
        this.isScanning = false;
      }
    },

    // 
    stopScan() {
      if (this.reader) {
        this.reader.reset();
      }
      this.isScanning = false;
    },

    async fetchPackageInfo(trackingNumber) {
      try {
        this.$request
          .get("/goodsdingdan/findByOrderNumber", {
            params: { goodsdingdannumber: trackingNumber },
          })
          .then((res) => {
            console.log("", res);
            if (res?.code == "200") {
              // 
              this.trackList.unshift({
                ...res.data,
                scannerNumber: trackingNumber,
              });
            } else {
              this.$toast("");
            }
          })
          .catch((error) => {
            console.error("", error);
            this.$toast("");
          });
      } catch (err) {
        console.error(":", err);
        this.$toast("");
      }
    },
  },
  beforeDestroy() {
    this.stopScan();
  },
  mounted() {
    this.startScan();
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
  },
};
</script>

