<script>
export default {
  data() {
    return {
      isLogin: false,
      headPhotoUrl: "https://img01.yzcdn.cn/vant/cat.jpeg",
      username: "",
      gender: "",
      school: "",
      phoneNumber: "",
      email: "",
      address: "",
      checked: true,
      userId: "",
      fileList: [],
      imageBase64: "", //  Base64 
    };
  },
  methods: {
    getNickname() {
      // 
      this.$request
        .post("user/userSaveOrUpdateWanye", {
          username: this.username,
          school: this.school,
          phoneNumber: this.phoneNumber,
          email: this.email,
          address: this.address,
          userId: this.userId,
          headPhotoUrl: this.headPhotoUrl,
          xinbie: this.gender == "2" ? "" : this.gender == "1" ? "" : "",
          // xinbie: this.gender
        })
        .then((res) => {
          if (res.code == 200) {
            let userInfo = JSON.stringify(res.data);
            localStorage.setItem("bmk_user", userInfo || {}); // 
            this.$router.push("/center");
          }
        });
    },
    chooseAvatar() {
      // 
    },
    selectSchool() {
      // 
      this.$router.push("/school");
    },
    onChange(e) {
      console.log(e);
    },
    //  Base64  Blob
    base64ToBlob(base64, contentType = "", sliceSize = 512) {
      const byteCharacters = atob(base64.split(",")[1]);
      const byteArrays = [];

      for (
        let offset = 0;
        offset < byteCharacters.length;
        offset += sliceSize
      ) {
        const slice = byteCharacters.slice(offset, offset + sliceSize);

        const byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
        }

        const byteArray = new Uint8Array(byteNumbers);
        byteArrays.push(byteArray);
      }

      return new Blob(byteArrays, { type: contentType });
    },
    afterRead(e) {
      console.log("afterRead", e, e.content); //base64
      this.headPhotoUrl = e.content;
      const base64Data = e.content;
      const blob = this.base64ToBlob(base64Data, "image/jpeg"); //  Blob
      console.log("blob", blob);
      //  FormData 
      const formData = new FormData();
      formData.append("userId", this.userId); // ID
      formData.append("file", blob, "headPhoto.jpg"); //  Blob  FormData
      console.log("formData:", formData.get("userId"), formData.get("file"));

      this.$request
        .post(`/user/uploadFileHeadPhotoUrlWangye`, formData)
        .then((res) => {
          console.log("", res);
          if (res?.code == "200") {
            this.$Toast("");
            this.headPhotoUrl = res.data;
          } else {
            this.$Toast("");
          }
        })
        .catch((error) => {
          console.error("", error);
        });
    },
  },
  created() {
    const user = localStorage.getItem("bmk_user");
    const userInfo = JSON.parse(user);
    // this.username = userInfo.username;
    // this.userId = userInfo.userId;
    // this.address = userInfo.address;
    // this.email = userInfo.email;
    // this.gender = userInfo.xinbie;
    // this.phoneNumber = userInfo.phoneNumber;
    // this.headPhotoUrl = userInfo.headPhotoUrl;
    // console.log("user", userInfo, userInfo.username, this.username);

    // 
    this.$request.get("/user/" + userInfo.userId).then((res) => {
      console.log("", res);
      const result = res.data;
      //  school 
      if (!result.school && this.$route.query.schoolName) {
        this.school = this.$route.query.schoolName;
      } else {
        this.school = result.school;
      }

      this.username = result.username;
      this.userId = result.userId;
      this.address = result.address;
      this.email = result.email;
      this.gender =
        result.xinbie == "" ? "2" : result.xinbie == "" ? "1" : "";
      this.phoneNumber = result.phoneNumber;
      this.headPhotoUrl = result.headPhotoUrl;
    });
  },
  mounted() {
    console.log("query", this.$route.query);
    if (this.$route.query?.schoolId) {
      this.school = this.$route.query.schoolName;
    }
  },
};
</script>

