<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
          padding: 0 20px;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
      </div>
      <!-- <div class="page-search" @scroll="handleScroll"> -->
      <div class="page-search">
        <div class="search-container">
          <van-search
            v-model="searchKey"
            placeholder=""
            @search="onSearch"
            show-action
            placeholder-style="font-size:15px;color:#666;"
          >
            <template #action>
              <div class="cancel-btn" @click="onSearch"></div>
            </template>
          </van-search>
        </div>
        <div class="trend-list">
          <div
            class="trend-item"
            v-for="item in trendData"
            :key="item.articleId"
          >
            <TrendItem :data="item" @click="goDetail(item)" @get="getData" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TrendItem from "../components/TrendItem.vue";
export default {
  components: {
    TrendItem,
  },
  data() {
    return {
      trendData: [],
      page: 1,
      pageSize: 10,
      pageFirstComments: 1,
      pageSizeFirstComments: 10,
      userId: "",
      loading: false, // 
      hasMoreData: true,
      searchKey: "",
    };
  },
  methods: {
    getData(key) {
      return this.$request
        .get("article/getSearchValueArticles", {
          params: {
            page: this.page,
            pageSize: this.pageSize,
            pageFirstComments: this.pageFirstComments,
            pageSizeFirstComments: this.pageSizeFirstComments,
            userId: this.userId || "",
            searchValue: key,
          },
        })
        .then((res) => {
          if (!res || res.length === 0) {
            this.hasMoreData = false;
          }
          if (this.page === 1) {
            this.trendData = res || [];
          } else {
            this.trendData.push(...(res || []));
          }
        });
    },
    loadMoreData() {
      if (this.loading || !this.hasMoreData) return; // 
      this.loading = true; // 
      this.page++; // 
      this.getData(this.searchKey).finally(() => {
        this.loading = false; // 
      });
    },
    goDetail(item) {
      console.log("goDetail");
      this.$router.push({ path: "/detail", query: { id: item.articleIdid } });
    },
    handleScroll(event) {
      console.log("Scroll Top:", event.target.scrollTop);
      console.log("Client Height:", event.target.clientHeight);
      console.log("Scroll Height:", event.target.scrollHeight);

      const scrollTop = event.target.scrollTop;
      const windowHeight = event.target.clientHeight;
      const documentHeight = event.target.scrollHeight;

      if (scrollTop + windowHeight >= documentHeight - 200 && !this.loading) {
        this.loadMoreData();
      }
    },
    onSearch() {
      console.log("", this.searchKey);
      this.getData(this.searchKey);
    },
  },
  mounted() {
    this.userId = localStorage.getItem("bmk_userId");
    // window.addEventListener("scroll", this.handleScroll); // 
  },
  beforeDestroy() {
    // window.removeEventListener("scroll", this.handleScroll); // 
  },
};
</script>

