<template>
  <div class="global-container">
    <div class="top">
      <div class="header-section">
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 15px;
            font-weight: bold;
            transition: all 0.3s ease;
          "
          @click="$router.go(-1)"
        ></van-button>
        <h2 class="page-title"></h2>
      </div>
      <div class="pickup-container" v-if="order && order.id">
        <!--  -->
        <div class="order-card">
          <div class="order-header-header">
            <span class="order-status">{{ order.fenjianstatus || '' }}</span>
          </div>
        
          <div class="order-header">
            <h3 class="order-number">{{ order.orderIndex }}{{ order.goodsdingdannumber }}</h3>
          </div>
          

          <!--  -->
          <div class="sorting-section" v-if="order.goodsdingdanitem && order.goodsdingdanitem.length > 0">
            <h4 class="sorting-title"></h4>
            <div class="progress-info">
              <p>: <span :class="{ 'animate-count': showAnimation }">{{ scannedTotal }}</span> / : {{ totalItems }}</p>
              <p>: <span :class="{ 'animate-count': showAnimation }">{{ remaining }}</span></p>
            </div>
            <van-button 
              @click="toggleScan" 
              :type="isScanning ? (remaining === 0 ? 'primary' : 'danger') : 'success'" 
              :color="isScanning ? (remaining === 0 ? '#00baad' : '#f44336') : '#00baad'"
              class="scan-button"
            >
              {{ isScanning ? (remaining === 0 ? ' >>' : '') : '' }}
            </van-button>
            <div v-if="isScanning" class="scan-container">
              <!--  -->
              <div class="scan-area">
                <div class="scanner-frame">
                  <video
                    ref="video"
                    autoplay
                    playsinline
                    class="scanner-video"
                  ></video>
                  <div class="scanner-overlay">
                    <div class="corner top-left"></div>
                    <div class="corner top-right"></div>
                    <div class="corner bottom-left"></div>
                    <div class="corner bottom-right"></div>
                  </div>
                </div>
              </div>
              
              <!--  -->
              <div v-if="dialogVisible && currentGood" class="product-popup">
                <div class="product-dialog-content">
                  <img :src="currentGood.goodsgoodsididshouphoto" alt="" class="product-img" />
                 
                  <p class="product-progress" :class="{ 'animate-count': true }">: {{ currentGood.scanned }} / {{ currentGood.goodsgoodsididnumber }}</p>
                  <h4 class="product-name">{{ currentGood.goodsgoodsididname }}</h4>
                </div>
              </div>
            </div>
          </div>

          <div class="order-items" v-if="order.goodsdingdanitem && order.goodsdingdanitem.length > 0">
            <h4 class="items-title"></h4>
            <div v-for="good in order.goodsdingdanitem" :key="good.id" class="item-row">
              <img :src="good.goodsgoodsididshouphoto" alt="" class="item-image" />
              <div class="item-details">
                <span class="item-name">{{ good.goodsgoodsididname }}</span>
                <span class="item-quantity">x{{ good.goodsgoodsididnumber }}</span>
                <span class="scanned-count" :class="{ 'animate-count': showAnimation && good.id === currentGood?.id }" v-if="good.scanned !== undefined"> {{ good.scanned }}/{{ good.goodsgoodsididnumber }}</span>
              </div>
            </div>
          </div>





          <div class="order-info">
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ order.goodsuseridaddressname }}</span>
            </div>
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ order.goodsuseridaddressphone }}</span>
            </div>
            <div class="info-row">
              <span class="info-label"></span>
              <span class="info-value">{{ order.goodsuseridaddressaddress }}</span>
            </div>
          </div>

        </div>
      </div>
      <div v-else class="no-orders">
        <p></p>
      </div>
    </div>
  </div>
</template>

<script>
import { BrowserMultiFormatReader } from "@zxing/library";

export default {
  data() {
    return {
      order: {},
      user: {},
      isScanning: false,
      reader: null,
      scannedText: "",
      scannedTotal: 0,
      totalItems: 0,
      remaining: 0,
      dialogVisible: false,
      currentGood: null,
      showAnimation: false,
      scrollDuration: 100, // 
    };
  },
  methods: {
    // time 
    scrollToSmoothly(pos, time) {
      const currentPos = window.pageYOffset;
      let start = null;
      if (time == null) time = this.scrollDuration;
      pos = +pos;
      time = +time;
      const step = (currentTime) => {
        start = !start ? currentTime : start;
        const progress = currentTime - start;
        if (currentPos < pos) {
          window.scrollTo(0, ((pos - currentPos) * progress / time) + currentPos);
        } else {
          window.scrollTo(0, currentPos - ((currentPos - pos) * progress / time));
        }
        if (progress < time) {
          window.requestAnimationFrame(step);
        } else {
          window.scrollTo(0, pos);
        }
      };
      window.requestAnimationFrame(step);
    },

    // 
    toggleScan() {
      if (this.isScanning) {
        if (this.remaining === 0) {
          this.completeSorting();
        } else {
          this.stopScan();
        }
      } else {
        this.startScan();
      }
    },

    // 
    async completeSorting() {
      try {
        await this.$request.post("/goodsdingdan/updateYifenjianByfenjianyuanuserid", {
          goodsdingdannumber: this.order.goodsdingdannumber,
          fenjianyuanuserid: this.user?.userId,
        });
        this.$toast("");
        this.stopScan();
        // 
        this.$router.go(-1);
      } catch (error) {
        console.error("", error);
        this.$toast("");
      }
    },

    // 
    async startScan() {
      this.isScanning = true;
      this.reader = new BrowserMultiFormatReader();

      try {
        // 1. 
        const devices = await this.reader.listVideoInputDevices();

        // 2. 
        let backCameraId = null;
        if (devices.length > 1) {
          backCameraId = devices.find(
            (device) =>
              device.label.toLowerCase().includes("back") ||
              device.label.includes("")
          )?.deviceId;
        }

        // 3. 
        const constraints = {
          video: {
            deviceId: backCameraId ? { exact: backCameraId } : undefined,
            facingMode: "environment",
            width: { ideal: 1920 },
            height: { ideal: 1080 },
          },
        };

        // 4. 
        await this.reader.decodeFromConstraints(
          constraints,
          this.$refs.video,
          (result, err) => {
            if (result) {
              const scannedText = result.getText();
              this.scannedText = scannedText;
              this.processScan(scannedText);
            }

            if (err && !(err instanceof DOMException)) {
              console.error(":", err);
            }
          }
        );
      } catch (err) {
        console.error(":", err);
        this.isScanning = false;
      }
    },

    // 
    processScan(code) {
      let found = false;
      for (let good of this.order.goodsdingdanitem) {
        if (good.goodsgoodsidid === code) {
          if (good.scanned < good.goodsgoodsididnumber) {
            good.scanned++;
            this.scannedTotal++;
            this.remaining--;
            found = true;
            this.currentGood = { ...good }; // 
            this.dialogVisible = true;
            this.triggerAnimation();
            this.scrollToScanArea();
            this.updateGoodSorting(good.id, this.user?.userId);
            if (good.scanned >= good.goodsgoodsididnumber) {
              this.$toast("");
            }
          } else {
            this.$toast("");
          }
          break;
        }
      }
      if (!found) {
        this.$toast("");
      }
      // 
    },

    // 
    scrollToScanArea() {
      this.$nextTick(() => {
        const scanContainer = this.$el.querySelector('.scan-container');
        if (scanContainer) {
          const rect = scanContainer.getBoundingClientRect();
          const scrollPos = window.pageYOffset + rect.top - (window.innerHeight / 2) + (rect.height / 2);
          this.scrollToSmoothly(scrollPos, this.scrollDuration);
        }
      });
    },

    // 
    closeProductPopup() {
      this.dialogVisible = false;
      this.currentGood = null;
    },

    // 
    triggerAnimation() {
      this.showAnimation = true;
      this.$nextTick(() => {
        setTimeout(() => {
          this.showAnimation = false;
        }, 600); //  + 
      });
    },

    // API
    async updateGoodSorting(goodId, userId) {
      try {
        // API
        await this.$request.post("goodsdingdanitem/updateFenjian", {
          fenjianyuanuserid: userId,
          goodsdingdanitemid: goodId, // 
          scanned: true, // 
        });
        console.log("");
      } catch (error) {
        console.error("", error);
        this.$toast("");
      }
    },

    // 
    stopScan() {
      if (this.reader) {
        this.reader.reset();
      }
      this.isScanning = false;
      this.scannedText = "";
      this.dialogVisible = false;
      this.showAnimation = false;
    },
  },
  beforeDestroy() {
    this.stopScan();
  },
  mounted() {
    const orderStr = this.$route.query.order;
    if (orderStr) {
      try {
        this.order = JSON.parse(decodeURIComponent(orderStr));
        // 
        if (this.order.goodsdingdanitem && this.order.goodsdingdanitem.length > 0) {
          this.order.goodsdingdanitem.forEach(good => {
            good.scanned = 0;
          });
          this.totalItems = this.order.goodsdingdanitem.reduce((sum, good) => sum + parseInt(good.goodsgoodsididnumber || 0), 0);
          this.remaining = this.totalItems;
        }
      } catch (error) {
        console.error(':', error);
        this.$toast('');
      }
    } else {
      this.$toast('');
      this.$router.go(-1); // 
    }
    // 
    const userStr = localStorage.getItem("bmk_user");
    if (userStr) {
      this.user = JSON.parse(userStr);
    }
  },
};
</script>

