<template>
  <div class="register-container">
    <div class="register-card">
      <!-- Logo -->
      <div class="logo-section">
        <div class="logo-wrapper">
          <img alt="logo" src="../assets/logo.png" class="logo-image" />
        </div>
        <h1 class="app-title"></h1>
        <p class="app-subtitle"></p>
      </div>

      <!--  -->
      <div class="form-section">
        <div class="input-group">
          <div class="input-wrapper">
            <i class="input-icon"></i>
            <input
              v-model="username"
              placeholder=""
              class="form-input"
              required
            />
          </div>
          <div class="input-wrapper">
            <i class="input-icon"></i>
            <input
              v-model="password"
              placeholder=""
              type="password"
              class="form-input"
              required
            />
          </div>
          <div class="input-wrapper">
            <i class="input-icon"></i>
            <input
              v-model="password2"
              placeholder=""
              type="password"
              class="form-input"
              required
            />
          </div>
        </div>

        <!--  -->
        <div v-if="tips" class="tips-message">
          <i class="tips-icon"></i>
          <span>{{ tips }}</span>
        </div>

        <!--  -->
        <div class="button-group">
          <button class="register-btn" @click="register">
            <span class="btn-text"></span>
            <i class="btn-icon"></i>
          </button>
          <button class="back-btn" @click="goBack">
            <span class="btn-text"></span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    name: 'RegisterComp',
    data ()
    {
        return {
            username: '',
            password: '',
            password2: '',
            tips: ''
        };
    },
    methods: {
        register() {
            if (!this.username || !this.password) {
                this.tips = `${this.username ? '' : ''}`;
                return this.$Toast(this.tips);
            }
            if (this.password2 != this.password) {
                this.tips = "";
                return this.$Toast(this.tips);
            }
            this.$request.post('user/registerUsernamePassword', { 
                username: this.username, 
                password: this.password 
            }).then(res => {
                console.log('', res);
                if (res.code === '200') {
                    this.$router.push({
                        path: '/',
                        query: {
                            username: this.username,
                            password: this.password
                        }
                    });
                } else {
                    this.tips = ':' + res.data;
                    this.$Toast(this.tips);
                }
            });
        },
        goBack() {
            this.$router.push({ path: '/' });
        }
    }
}
</script>
