<template>
  <div class="page-container">
    <div class="content-container">
      <div class="trend-list" @scroll="handleScroll">
        <div
          style="
            display: flex;
            justify-content: space-between;
            padding: 8px 15px;
          "
        >
          <van-button
            type="primary"
            color="#fa6286"
            style="
              border: 2px solid #fa6286;
              border-radius: 10px;
              font-weight: bold;
            "
            @click="$goMenu"
            ></van-button
          >
        </div>
        <div class="trend-wrap">
          <div
            class="trend-item"
            v-for="item in trendData"
            :key="item.articleId"
          >
            <TrendItem :data="item" @click="goDetail(item)" @get="getData" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TrendItem from "../components/TrendItem.vue";
export default {
  name: "TheHot",
  components: {
    TrendItem,
  },
  data() {
    return {
      trendData: [],
      page: 1,
      pageSize: 10,
      pageFirstComments: 1,
      pageSizeFirstComments: 10,
      userId: "",
      loading: false, // 
      hasMoreData: true,
    };
  },
  methods: {
    getData() {
      return this.$request
        .get("article/getSchoolHostArticles", {
          params: {
            page: this.page,
            pageSize: this.pageSize,
            pageFirstComments: this.pageFirstComments,
            pageSizeFirstComments: this.pageSizeFirstComments,
            userId: this.userId,
          },
        })
        .then((res) => {
          console.log("getSchoolHostArticles", res);
          // if (!res || res.length === 0) {
          //   this.hasMoreData = false;
          // }
          if (res && res.length < this.pageSize) {
            this.hasMoreData = false;
          } else {
            this.hasMoreData = true;
          }
          if (this.page === 1) {
            this.trendData = res || [];
          } else {
            this.trendData.push(...(res || []));
          }
        });
    },
    loadMoreData() {
      if (this.loading || !this.hasMoreData) return;
      this.loading = true;
      this.page++;
      this.getData().finally(() => {
        this.loading = false;
      });
    },
    goDetail(item) {
      console.log("goDetail");
      // this.$router.push({ name: 'detail', params: item });
      this.$router.push({ path: "/detail", query: { id: item.articleIdid } });
    },
    handleScroll(event) {
      console.log("Scroll Top:", event.target.scrollTop);
      console.log("Client Height:", event.target.clientHeight);
      console.log("Scroll Height:", event.target.scrollHeight);

      const scrollTop = event.target.scrollTop;
      const windowHeight = event.target.clientHeight;
      const documentHeight = event.target.scrollHeight;

      if (scrollTop + windowHeight >= documentHeight - 200 && !this.loading) {
        this.loadMoreData();
      }
    },
  },
  mounted() {
    this.userId = localStorage.getItem("bmk_userId");
    this.getData();
    window.addEventListener("scroll", this.handleScroll); // 
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll); // 
  },
};
</script>

