<template>
  <div class="global-container">
    <div class="top">
      <div class="pickup-container">
        <h2></h2>

        <div v-if="isScanning" class="scan-area">
          <div class="scanner-frame">
            <video
              ref="video"
              autoplay
              playsinline
              class="scanner-video"
            ></video>
            <div class="scanner-overlay">
              <div class="corner top-left"></div>
              <div class="corner top-right"></div>
              <div class="corner bottom-left"></div>
              <div class="corner bottom-right"></div>
            </div>
          </div>
        </div>
        <div class="scan-result">{{ scannedText }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { BrowserMultiFormatReader } from "@zxing/library";

export default {
  data() {
    return {
      trackList: [],
      trackingNumber: "",
      isScanning: false,
      reader: null,
      packageInfo: {
        recipient: "",
        address: "",
      },
      error: null,
      items: [],

      isReach: 0,
      disabled: false,

      isConnected: false,
      clientId: null,
      connectionStatus: "",
      messages: [],

      user: {},
      userId: "",

      positionNum: 0,
      positionLogs: [],
      windowHeight: document.body.clientHeight,
      geolocation: null,
      watchId: null,

      newLocation: "",
      latitude: "",
      longitude: "",
      scannedText: "",
      sid: "",
    };
  },
  methods: {
    handleSliderChange(value) {
      // this.isReach = Math.min(Math.max(this.isReach, 0), 54);
      this.isReach = Math.min(Math.max(value, 0), 54);
    },
    changeStatus() {
      // if (this.isReach == 54) {
      this.disabled = true;
      this.$router.push({
        path: "/scandetail",
        // query: { ord: item.scannerNumber },
        query: { ord: this.scannedText },
      });
      // } else {
      //   this.isReach = 0;
      // }
    },

    // 
    async startScan() {
      this.isScanning = true;
      this.reader = new BrowserMultiFormatReader();

      try {
        // 1. 
        const devices = await this.reader.listVideoInputDevices();

        // 2. 
        let backCameraId = null;
        if (devices.length > 1) {
          backCameraId = devices.find(
            (device) =>
              device.label.toLowerCase().includes("back") ||
              device.label.includes("")
          )?.deviceId;
        }

        // 3. 
        const constraints = {
          video: {
            deviceId: backCameraId ? { exact: backCameraId } : undefined,
            facingMode: "environment",
            width: { ideal: 1920 },
            height: { ideal: 1080 },
          },
        };

        // 4. 
        await this.reader.decodeFromConstraints(
          constraints,
          this.$refs.video,
          (result, err) => {
            if (result) {
              const scannedText = result.getText();
              this.scannedText = scannedText;
              // this.$router.go(-1)
              this.$router.push({
                path: "/goodsmanage",
                query: { sid: this.$route.query.sid, text: scannedText },
              });
            }

            if (err && !(err instanceof DOMException)) {
              this.error = `: ${err.message}`;
            }
          }
        );
      } catch (err) {
        console.error(":", err);
        this.$toast("");
        this.isScanning = false;
      }
    },

    // 
    stopScan() {
      if (this.reader) {
        this.reader.reset();
      }
      this.isScanning = false;
    },

    async fetchPackageInfo(trackingNumber) {
      try {
        // this.packageInfo = {
        //   recipient: "",
        //   address: "123",
        //   trackingNumber,
        // };
        this.$request
          .post(`goodsdingdan/updateByfenjianyuanuserid`, {
            fenjianyuanuserid: this.user?.userId,
            goodsdingdannumber: trackingNumber,
          })
          .then((res) => {
            console.log("", res);
            this.$Toast(trackingNumber);
            if (res?.code == "200") {
              this.trackList.unshift({
                scannerNumber: trackingNumber,
                recipient: "",
                goodsuseridaddress: {
                  address: trackingNumber,
                },
              });
            } else {
              this.$Toast("");
            }
          })
          .catch((error) => {
            console.error("", error);
          });
      } catch (err) {
        console.error(":", err);
        this.error = "";
      }
    },

    async confirmPickup() {
      if (!this.trackingNumber) {
        this.error = "";
        return;
      }

      try {
        alert(`${this.trackingNumber} `);
        this.resetForm();
      } catch (err) {
        console.error(":", err);
        this.error = "";
      }
    },

    resetForm() {
      this.trackingNumber = "";
      this.packageInfo = {
        recipient: "",
        address: "",
      };
      this.error = null;
    },
  },
  beforeDestroy() {
    this.stopScan();
  },
  mounted() {
    this.startScan();
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("scanGoods==query", this.user, this.$route.query);
    if (this.$route.query.sid) {
      this.sid = this.$route.query.sid;
    }
  },
};
</script>

