<template>
    <div class="page-school">
        <div class="header">
            <div style="margin-left: 29px; font-weight: bold;">
                {{ schoolName }}
            </div>
            <van-button color="#FB4C77" class="cancel-btn" size="small" @click="selectSchool">
                
            </van-button>
        </div>

        <div class="content">
            <van-cell-group style="width: 100%;" :border="false">
                <van-cell v-for="(item) in schoolList" :key="item.schoolId" :title="item.schoolName"
                    @click="select(item)">
                    <van-icon slot="right-icon" name="success" color="#fb4c77" v-if="item.schoolName === schoolName"
                        size="20px" />
                </van-cell>
            </van-cell-group>
        </div>

        <div class="footer">
            <div class="search">
                <van-search v-model="searchKey" placeholder="" @search="onSearch" use-left-icon-slot
                    placeholder-style="font-size: 32rpx"></van-search>
                <van-button color="#FB4C77" class="cancel-btn" size="small" @click="onSearch">
                    
                </van-button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data ()
    {
        return {
            schoolList: [],
            school: {},
            schoolName: '',
            trend: false,
            searchKey: '',
        };
    },
    created ()
    {
        this.fetchSchoolList();
    },
    methods: {
        fetchSchoolList ()
        {
            this.$request.get('school/findAll').then((res) =>
            {
                if (res.code == 200) {
                    this.schoolList = res.data;
                } else {
                    this.$toast('');
                }
            });
        },
        select (item)
        {
            console.log("select", item);
            this.school = item;
            this.schoolName = item.schoolName;
        },
        selectSchool ()
        {
            this.$router.push({ path: '/wode', query: this.school })
        },
        onSearch ()
        {
            this.$request.get('school/findAllLikeSchoolName', { params: { schoolName: this.searchKey } }).then((res) =>
            {
                if (res.code === 200) {
                    this.schoolList = res.data;
                } else {
                    this.$toast('');
                }
            });
        },
    },
};
</script>

