<script>
import sseService from "@/utils/sse";
import axios from "axios";

export default {
  data() {
    return {
      isConnected: false,
      clientId: null,
      connectionStatus: "",
      messages: [],
      user: {},
      userId: "",
    };
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    if (user) {
      this.user = JSON.parse(user);
      this.userId = this.user?.userId;
    } else {
      this.$Toast("");
    }
  },
  methods: {
    connectSSE() {
      // ID
      const clientId = this.userId;

      // SSE
      this.clientId = sseService.connect(clientId);
      console.log("this.clientId:", this.clientId);
      this.isConnected = true;
      this.connectionStatus = "...";

      // 
      sseService.onMessage((data) => {
        this.handleMessage(data);
      });

      // 
      sseService.onError((error) => {
        this.connectionStatus = ": " + error.message;
        this.isConnected = false;
        this.addMessage("", ": " + error.message, "error");
      });
    },

    disconnectSSE() {
      sseService.disconnect();
      this.isConnected = false;
      this.connectionStatus = "";
      this.addMessage("", "", "system");
    },

    sendTestMessage() {
      if (!this.clientId) return;

      axios
        .get(
          `http://192.168.1.55:9091/user/sse/sendTestMessage?clientId=${this.userId}`
          //   ` https://0ea6-2409-8a55-c2e-fc90-a51b-d974-c771-8016.ngrok-free.app/user/sse/sendTestMessage?clientId=${this.userId}`
        )
        .then((response) => {
          this.addMessage("", response.data, "system");
        })
        .catch((error) => {
          this.addMessage(
            "",
            ": " + error.message,
            "error"
          );
        });
    },

    // 
    getLocation() {
      if (
        window.location.protocol !== "https:" &&
        window.location.hostname !== "localhost" &&
        window.location.hostname !== "127.0.0.1"
      ) {
        this.addMessage("", " HTTPS ", "error");
        return;
      }

      if (navigator.geolocation) {
        this.addMessage("", "...", "location");

        navigator.geolocation.getCurrentPosition(
          (position) => {
            console.log("", position);
            const { latitude, longitude } = position.coords;
            this.addMessage(
              "",
              `: ${latitude}, : ${longitude}`,
              "location"
            );
            // http://192.168.1.55:9091/user/updateLocation?longitude=111&latitude=222&user_id=11

            axios
              .post(
                "http://192.168.1.55:9091/user/updateLocation",
                {
                  userId: this.userId,
                  longitude: longitude,
                  latitude: latitude,
                },
                {
                  headers: {
                    token: this.user.token,
                    "Content-Type": "application/json",
                  },
                }
              )
              .then((res) => {
                this.$Toast("", res);
              });
          },
          (error) => {
            this.addMessage("", ": " + error.message, "error");
          }
        );
      } else {
        this.addMessage("", "", "error");
      }
    },

    handleMessage(data) {
      if (data === "CONNECTED") {
        this.connectionStatus = "";
        this.addMessage("", "SSE", "system");
      } else if (data === "111222") {
        this.addMessage("", "", "test");
        this.getLocation(); // "111222"
      } else {
        this.addMessage("", "" + data, "server");
      }
    },

    addMessage(source, content, type) {
      this.messages.unshift({
        time: new Date().toLocaleTimeString(),
        source,
        content,
        type,
      });

      // 
      if (this.messages.length > 50) {
        this.messages.pop();
      }
    },
  },
  beforeDestroy() {
    sseService.disconnect();
  },
};
</script>

