<template>
  <div class="geolocation-container">
    <div
      id="pos-area"
      class="position-area"
      :style="{ height: windowHeight - 110 + 'px' }"
    >
      <p class="demo"><br /></p>
      <div v-for="(item, index) in positionLogs" :key="index">
        <p>{{ item.id }}</p>
        <pre v-if="item.type === 'position'">{{
          formatPosition(item.data)
        }}</pre>
        <p v-else-if="item.type === 'error'"></p>
        <p v-else-if="item.type === 'start_watch'"></p>
        <p v-else-if="item.type === 'stop_watch'"></p>
      </div>
    </div>

    <div class="btn-area">
      <button @click="getPreciseLocation"></button>
      <button @click="getIpLocation"></button>
      <button @click="startWatchPosition"></button>
      <button @click="stopWatchPosition"></button>
    </div>
  </div>
</template>

<script>
export default {
  // name: "Geolocation",
  data() {
    return {
      positionNum: 0,
      positionLogs: [],
      windowHeight: document.body.clientHeight,
      geolocation: null,
      watchId: null,
    };
  },
  mounted() {
    // 
    this.geolocation = new window.qq.maps.Geolocation(
      "7NMBZ-Q24WW-4CLRO-OKBAZ-TE3VF-2WFMC",
      "myapp"
    );

    // 
    window.addEventListener("resize", this.updateWindowHeight);
  },
  beforeDestroy() {
    // 
    window.removeEventListener("resize", this.updateWindowHeight);

    // 
    if (this.watchId) {
      this.geolocation.clearWatch();
    }
  },
  methods: {
    updateWindowHeight() {
      this.windowHeight = document.body.clientHeight;
    },

    getPreciseLocation() {
      const options = { timeout: 8000 };
      this.geolocation.getLocation(this.showPosition, this.showErr, options);
    },

    getIpLocation() {
      this.geolocation.getIpLocation(this.showPosition, this.showErr);
    },

    startWatchPosition() {
      this.positionNum++;
      this.positionLogs.push({
        id: this.positionNum,
        type: "start_watch",
      });
      this.scrollToBottom();

      this.watchId = this.geolocation.watchPosition(this.showPosition);
    },

    stopWatchPosition() {
      if (this.watchId) {
        this.geolocation.clearWatch();
        this.watchId = null;

        this.positionNum++;
        this.positionLogs.push({
          id: this.positionNum,
          type: "stop_watch",
        });
        this.scrollToBottom();
      }
    },

    showPosition(position) {
      this.positionNum++;
      this.positionLogs.push({
        id: this.positionNum,
        type: "position",
        data: position,
      });
      this.scrollToBottom();
    },

    showErr() {
      this.positionNum++;
      this.positionLogs.push({
        id: this.positionNum,
        type: "error",
      });
      this.scrollToBottom();
    },

    scrollToBottom() {
      this.$nextTick(() => {
        const posArea = document.getElementById("pos-area");
        posArea.scrollTop = posArea.scrollHeight;
      });
    },

    formatPosition(position) {
      return JSON.stringify(position, null, 4);
    },
  },
};
</script>

