          </div>
        </div>
        
        <div class="section-actions">
           <button class="cancel-btn" @click="showPeisongDialog = false; showBatchDialog = false">
             
           </button>
          <button class="confirm-btn peisong-confirm-btn" @click="confirmBatchSetPeisong">
            
          </button>
        </div>
