<script>
import { Toast } from "vant";

export default {
  components: {},
  props: {},
  data() {
    return {
      showBatchDialog: false,
      showPeisongDialog: false,
      batchUserId: "",
      batchPeisongUserId: "",
      items: [],
      user: {},
      unassignedCount: 0,
      unassignedOrders: [], // 
      unassignedPeisongCount: 0,
      unassignedPeisongOrders: [], // 
    };
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
    if (this.user.userId) {
      this.getData();
      this.getUnassignedCount();
      this.getUnassignedPeisongCount();
    } else {
      Toast("");
    }
  },
  methods: {
    batchSet() {
      this.showBatchDialog = true;
      this.showPeisongDialog = false; // 
    },
    batchSetPeisong() {
      this.showPeisongDialog = true;
      this.showBatchDialog = false; // 
    },
    async confirmBatchSet() {
      if (!this.batchUserId.trim()) {
        Toast("ID");
        return;
      }
      
      try {
        // 
        const unassignedOrders = await this.getUnassignedOrders();
        
        if (!unassignedOrders || unassignedOrders.length === 0) {
          Toast("");
          this.showBatchDialog = false;
          this.batchUserId = "";
          return;
        }
        
        // 
        const list = unassignedOrders.map(item => ({
        goodsdingdannumber: item.goodsdingdannumber,
        fenjianyuanuserid: Number(this.batchUserId)
      }));
        
        console.log(":", list);
        
        // 
        const res = await this.$request.post("/goodsdingdan/piliangupdateByfenjianyuanuserid", list);
        
          console.log("", res);
          if (res.code == 200) {
            Toast(res.data || "");
          this.getData(); // 
          this.getUnassignedCount(); // 
          } else {
            Toast("");
          }
      } catch (error) {
          console.error(":", error);
          Toast("");
      } finally {
        this.showBatchDialog = false;
        this.showPeisongDialog = false;
        this.batchUserId = "";
      }
    },
    async confirmBatchSetPeisong() {
      if (!this.batchPeisongUserId.trim()) {
        Toast("ID");
        return;
      }
      
      try {
        // 
        const unassignedPeisongOrders = await this.getUnassignedPeisongOrders();
        
        if (!unassignedPeisongOrders || unassignedPeisongOrders.length === 0) {
          Toast("");
          this.showPeisongDialog = false;
          this.batchPeisongUserId = "";
          return;
        }
        
        // 
        const list = unassignedPeisongOrders.map(item => ({
          goodsdingdannumber: item.goodsdingdannumber,
          peisongyuanuserid: Number(this.batchPeisongUserId)
        }));
        
        console.log(":", list);
        
        // 
        const res = await this.$request.post("/goodsdingdan/piliangupdateBypeisongyuanuserid", list);
        
        console.log("", res);
        if (res.code == 200) {
          Toast(res.data || "");
          this.getData(); // 
          this.getUnassignedPeisongCount(); // 
        } else {
          Toast("");
        }
      } catch (error) {
        console.error(":", error);
        Toast("");
      } finally {
        this.showPeisongDialog = false;
        this.showBatchDialog = false;
        this.batchPeisongUserId = "";
      }
    },
    getData() {
      if (this.user?.userId) {
        // 
        this.$request
          .get("/goodsdingdan/findByPeisongyuanAndStatus", {
            params: {
              peisongyuanuserid: this.user?.userId,
              peisongstatus: "",
            },
          })
          .then((res) => {
            console.log("", res);
            if (res.code == 200) {
              this.items = res.data;
            }
          });
      }
    },
    // 
    getUnassignedOrders() {
      return this.$request
        .get("/goodsdingdan/findByFenjianyuanuseridIsNull")
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            return res.data;
          }
          return [];
        })
        .catch((error) => {
          console.error(":", error);
          return [];
        });
    },
    // 
    getUnassignedCount() {
      this.$request
        .get("/goodsdingdan/findByFenjianyuanuseridIsNull")
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            this.unassignedOrders = res.data || [];
            this.unassignedCount = this.unassignedOrders.length;
          }
        })
        .catch((error) => {
          console.error(":", error);
          this.unassignedOrders = [];
          this.unassignedCount = 0;
        });
    },
    // 
    getUnassignedPeisongCount() {
      this.$request
        .get("/goodsdingdan/findByPeisongyuanuseridIsNull")
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            this.unassignedPeisongOrders = res.data || [];
            this.unassignedPeisongCount = this.unassignedPeisongOrders.length;
          }
        })
        .catch((error) => {
          console.error(":", error);
          this.unassignedPeisongOrders = [];
          this.unassignedPeisongCount = 0;
        });
    },
    // 
    getUnassignedPeisongOrders() {
      return this.$request
        .get("/goodsdingdan/findByPeisongyuanuseridIsNull")
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            return res.data;
          }
          return [];
        })
        .catch((error) => {
          console.error(":", error);
          return [];
        });
    },
  },
};
</script>

