<script>
import "viewerjs/dist/viewer.css";
import Viewer from "viewerjs";
export default {
  name: "TrendDetail",
  data() {
    return {
      viewerOptions: {
        toolbar: false, // 
        navbar: true, // 
        title: true, // 
        fullscreen: true, // 
        movable: false, // 
        zoomable: true, // 
        loop: false,
      },
      gallery: null, //  Viewer 
      data: {},
      page: 1,
      pageSize: 10,
      pageFirstComments: 1,
      pageSizeFirstComments: 10,
      articleIdid: "",
      message: "",
      user: {},
      commentId: "",
      placeholder: "...",
    };
  },
  components: {
    // Viewer
  },
  props: {
    // data: {}
  },
  mounted() {
    // console.log("params--", this.$route.params);
    // this.data = this.$route.params;
    const id = this.$route.query.id; // ID
    console.log("this.$route.query", this.$route.query);
    if (id) {
      this.articleIdid = id;
      console.log("articleIdid", this.articleIdid);
      this.getArticleById(id); // ID
    }
    //  Viewer
    this.initViewer();
    this.getArticleById();
    let user = localStorage.getItem("bmk_user");
    if (user) {
      this.user = JSON.parse(user);
    }
    console.log("user", this.user);
  },
  updated() {
    //  Viewer
    this.initViewer();
  },
  methods: {
    chooseComment(id, name) {
      this.placeholder = "" + name + ":";
      if (id) {
        this.commentId = id;
      } else {
        this.commentId = "";
      }
      this.$refs.myInput.focus();
    },
    initViewer() {
      //  DOM 
      this.$nextTick(() => {
        // 
        if (this.gallery) {
          this.gallery.destroy();
        }
        //  ref  Viewer 
        if (this.$refs.images) {
          this.gallery = new Viewer(this.$refs.images, this.viewerOptions);
        }
      });
    },
    clickImg() {
      //  Viewer
      if (this.gallery) {
        this.gallery.show();
      }
    },
    manageDelSecondCommentEvt(idx, commentId) {
      let userId = localStorage.getItem("bmk_userId") || "";
      let params = {
        commentId: commentId,
        articleIdid: this.articleIdid,
        status: "",
        userId,
      };
      this.$request
        .post("comment/updateCommentStatusBychaojiguanliyuan", params)
        .then((res) => {
          console.log("commentId", commentId);
          if (res.code == "200") {
            // this.$emit('get')
            const targetSecond = this.data.commentDTO[
              idx
            ].secondLevelCommentList.findIndex((s) => s.commentId == commentId);
            this.$set(
              this.data.commentDTO[idx].secondLevelCommentList[targetSecond],
              "isDel",
              true
            );
          } else {
            console.log("");
          }
        });
    },
    manageDelCommentEvt(commentId) {
      let userId = localStorage.getItem("bmk_userId") || "";
      let params = {
        commentId: commentId,
        articleIdid: this.articleIdid,
        status: "",
        userId,
      };
      this.$request
        .post("comment/updateCommentStatusBychaojiguanliyuan", params)
        .then((res) => {
          console.log("commentId", commentId);
          if (res.code == "200") {
            // this.$emit('get')
            const target = this.data.commentDTO.findIndex(
              (v) => v.commentId == commentId
            );
            this.$set(this.data.commentDTO[target], "isDel", true);
          } else {
            console.log("");
          }
        });
    },
    getArticleById() {
      let userId = localStorage.getItem("bmk_userId") || "";
      this.$request
        .get("article/getArticlesByArticleIdid", {
          params: {
            page: this.page,
            pageSize: this.pageSize,
            pageFirstComments: this.pageFirstComments,
            pageSizeFirstComments: this.pageSizeFirstComments,
            articleIdid: this.articleIdid,
            userId,
          },
        })
        .then((res) => {
          console.log("getArticleById--res", res);
          this.data = res[0] || {};
        });
    },
    sendComment() {
      console.log("", this.message);
      const { headPhotoUrl, userId, username } = this.user;
      this.$request
        .post("comment/saveOrUpdateWanye", {
          headPhotoUrl,
          userId,
          username,
          commentContent: this.message,
          commentIdid: this.commentId,
          articleIdid: this.articleIdid,
        })
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            this.message = "";
            this.$Toast("");
            this.getArticleById();
          } else {
            this.$Toast("");
          }
        });
    },
  },
  beforeDestroy() {
    //  Viewer 
    if (this.gallery) {
      this.gallery.destroy();
    }
  },
};
</script>

