                <span class="self-start font label"></span>
                <div class="flex-col self-stretch group_35">
                  <div class="self-stretch divider view_8"></div>
                </div>
                <div style="display: flex; justify-content: center; margin: 20px 0">
                  <van-uploader
                    :after-read="afterReadOne"
                    :max-count="1"
                    preview-size="140px"
                    accept="image/*"
                    :show-upload="false"
                  >
                    <van-image
                      v-if="imgSrc1 && !imgSrc1.includes('upload-pic')"
                      :src="imgSrc1"
                      radius="16"
                      width="112"
                      height="112"
                      fit="cover"
                      class="uploaded-image"
                    />
                    <div v-else class="upload-placeholder">
