    getOrderData() {
      // 
      const goodsdingdannumber = this.$route.params.goodsdingdannumber;
      if (!goodsdingdannumber) {
        Toast("");
        return;
      }
      
      // 
      this.$request
        .get("/goodsdingdan/findgoodsdingdanbygoodsdingdannumber", {
          params: { goodsdingdannumber: goodsdingdannumber },
        })
        .then((res) => {
          console.log(":", res);
          if (res.code == "200" && res.data && res.data.length > 0) {
            // 
            this.orderData = res.data[0];
            console.log(":", this.orderData);
          } else {
            Toast("");
          }
        })
        .catch((error) => {
          console.error(":", error);
          Toast("");
        });
    },
    
    // 
    formatTimestamp(timestamp) {
      if (!timestamp || !/^\d{14}$/.test(timestamp)) {
        return timestamp || "";
      }

      const month = timestamp.substring(4, 6);
      const day = timestamp.substring(6, 8);
      const hour = timestamp.substring(8, 10);
      const minute = timestamp.substring(10, 12);

      const formattedMonth = parseInt(month, 10);
      const formattedDay = parseInt(day, 10);
      const formattedHour = parseInt(hour, 10);
      const formattedMinute = parseInt(minute, 10);

      return `${formattedMonth}${formattedDay}${formattedHour}:${formattedMinute
        .toString()
        .padStart(2, "0")}`;
    },
    
    // 
    async copyToClipboard() {
      const ditudingwei = this.orderData?.goodsuseridaddress?.ditudingwei || this.orderData?.goodsuseridaddressditudingwei;
      if (!ditudingwei) {
        Toast("");
        return;
      }
      
      if (navigator.clipboard) {
        try {
          await navigator.clipboard.writeText(ditudingwei);
          Toast("");
          return;
        } catch (err) {
          console.warn("", err);
        }
      }
      this.copyFallback(ditudingwei);
    },
    
    copyFallback(text) {
      const input = document.createElement("input");
      input.value = text;
      document.body.appendChild(input);
      input.select();
      document.execCommand("copy");
      document.body.removeChild(input);
      Toast("");
    },
    
    // 
    copyAndCall(phone) {
      if (!phone) {
        phone = this.orderData?.goodsuseridaddress?.phone || this.orderData?.goodsuseridaddressphone;
      }
      if (!phone) {
        Toast("");
        return;
      }
      this.copyTelephone(phone);
      window.location.href = `tel:${phone}`;
    },
    
    copyTelephone(text) {
      const input = document.createElement("input");
      input.value = text;
      document.body.appendChild(input);
      input.select();
      document.execCommand("copy");
      document.body.removeChild(input);
    },
    
    // 
    report(operate) {
      if (!this.orderData || !this.orderData.id) {
        Toast("");
        return;
      }
      
      let path;
      switch (operate) {
        case "xiangqing":
          path = "/peisongdetail";
          break;
        case "baobei":
          path = "/abnormalreport";
          break;
        case "weizhi":
          path = "/peisonglocation";
          break;
        case "tuikuan":
          path = "/refund";
          break;
      }
      if (path) {
        this.$router.push({ 
          path, 
          query: { 
            id: this.orderData.id, 
            outtradeno: this.orderData.outtradeno 
          } 
        });
      }
    },
    
    // 
    confirmCancelOrder() {
      if (!this.orderData) {
        return;
      }
      
      const orderNumber = this.orderData.goodsdingdannumber || "";
      
      if (!orderNumber || orderNumber.length < 5) {
        Toast("");
        return;
      }

      this.currentCancelOrder = this.orderData;
      this.cancelOrderInput = "";
      this.cancelOrderError = "";
      this.showCancelOrderDialog = true;
    },
    
    // 
    handleCancelOrderInput(value) {
      const numbersOnly = value.replace(/\D/g, "");
      this.cancelOrderInput = numbersOnly.slice(0, 5);
      this.cancelOrderError = "";
    },
    
    // 
    handleCancelOrderConfirm() {
      if (!this.currentCancelOrder) {
        return;
      }
      
      const orderNumber = this.currentCancelOrder.goodsdingdannumber || "";
      const lastFiveDigits = orderNumber.slice(-5);
      const userInput = this.cancelOrderInput.trim();
      
      this.cancelOrderError = "";
      
      if (!userInput) {
        this.cancelOrderError = "";
        Toast("");
        return;
      }
      
      if (!/^\d{5}$/.test(userInput)) {
        this.cancelOrderError = "5";
        Toast("5");
        return;
      }
      
      if (userInput !== lastFiveDigits) {
        this.cancelOrderError = "";
        Toast("");
        this.cancelOrderInput = "";
        return;
      }
      
      if (confirm(`\n${orderNumber}`)) {
        this.cancelOrderWithVerification(this.currentCancelOrder);
      }
