  font-weight: 500;
}

.no-data-subtitle {
  font-size: 14px;
  opacity: 0.8;
}

/*  */
@media (max-width: 767px) {
  .fixed-header {
    padding: 0;
  }
  

  
  .order-detail-container {
    margin: 6px 0;
    border-radius: 10px;
  }
  
  .order-header {
    padding: 8px 12px;
  }
  
  .order-icon {
