<template>
  <div class="login-container">
    <div class="login-card">
      <!-- Logo -->
      <div class="logo-section">
        <div class="logo-wrapper">
          <img alt="logo" src="../assets/logo.png" class="logo-image" />
        </div>
        <h1 class="app-title"></h1>
        <p class="app-subtitle"></p>
      </div>

      <!--  -->
      <div class="form-section">
        <div class="input-group">
          <div class="input-wrapper">
            <i class="input-icon"></i>
            <input
              v-model="username"
              placeholder=""
              class="form-input"
              required
            />
          </div>
          <div class="input-wrapper">
            <i class="input-icon"></i>
            <input
              v-model="password"
              placeholder=""
              type="password"
              class="form-input"
              required
            />
          </div>
        </div>

        <!--  -->
        <div class="button-group">
          <button class="login-btn" @click="login">
            <span class="btn-text"></span>
            <i class="btn-icon"></i>
          </button>
          <button class="register-btn" @click="goRegister">
            <span class="btn-text"></span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { setToken } from "@/utils/auth";
export default {
  name: "LoginComp",
  data() {
    return {
      username: "",
      password: "",
      tips: "",
    };
  },
  methods: {
    goRegister() {
      this.$router.push({ path: "/register" });
    },
    login(username, password) {
      if (username && password) {
        this.username = username;
        this.password = password;
      }
      if (!this.username || !this.password) {
        this.tips = `${this.username ? "" : ""}`;
        // return
        return this.$Toast(this.tips);
      }
      this.$request
        .get("user/loginUsernamePassword", {
          params: { username: this.username, password: this.password },
          headers: {
            "ngrok-skip-browser-warning": "true",
            // Accept: "application/json", //  JSON
          },
        })
        .then((res) => {
          console.log("", res);
          if (res?.userId) {
            this.$router.push("/center");
            const userInfo = JSON.stringify(res);
            //  localStorage
            localStorage.setItem("bmk_token", res.token);
            localStorage.setItem("bmk_userId", res.userId.toString()); // 
            localStorage.setItem("bmk_username", res.username || ""); // 
            localStorage.setItem("bmk_user", userInfo || {}); // 
            setToken(res.token);
            console.log("bmk_token", localStorage.getItem("bmk_token"));
          } else {
            // this.tips = '';
            this.$Toast("");
          }
        });
    },
  },
  mounted() {
    const { username, password } = this.$route.query;
    // 
    if (username && password) {
      this.login(username, password);
    }
  },
};
</script>
