<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$router.go(-1)"
          ></van-button
        >
      </div>

      <div
        style="
          display: flex;
          padding: 20px 0px;
          border-radius: 15px 15px 0 0;
          margin-bottom: 30px;
        "
      >
        <img :src="logo2" style="width: 40px; height: 40px" />
        <div class="text" style="color: #999">
          
          <div style="color: #000">-</div>
        </div>
      </div>

      <!--  -->
      <div class="stats-container" style="padding: 20px; margin-bottom: 20px;">
        <div class="stats-grid">
          <div class="stat-item">
            <div class="stat-value">{{ yiTuiGuang || 0 }}</div>
            <div class="stat-label"></div>
          </div>
          <div class="stat-item">
            <div class="stat-value">{{ yiHeXiao || 0 }}</div>
            <div class="stat-label"></div>
          </div>
        </div>
      </div>

      <!--  -->
      <div style="padding: 0 10px;">
        <div style="display: flex; justify-content: space-between; align-items: center; margin-bottom: 20px; padding: 0 15px;">
          <h3 style="margin: 0; color: #333;"></h3>
          <van-button
            size="small"
            :type="useOldApi ? 'primary' : 'default'"
            color="#fa6286"
            style="border-radius: 8px;"
            @click="toggleApi"
          >
            {{ useOldApi ? '' : '' }}
          </van-button>
        </div>

        <!--  -->
        <div class="loading-state" v-if="loading">
          <van-loading size="24px">...</van-loading>
        </div>

        <!--  -->
        <div v-else-if="recordList.length > 0">
          <van-cell-group>
            <van-cell
              v-for="record in recordList"
              :key="record.id"
              style="padding: 15px;"
            >
              <template #title>
                <div style="width: 100%;">
                  <div style="display: flex; justify-content: space-between; align-items: center; margin-bottom: 8px;">
                    <span style="font-weight: bold; color: #333;">: {{ record.type || '' }}</span>
                    <span style="font-size: 12px; color: #999;">
                      {{ formatDate(record.createTime) }}
                    </span>
                  </div>
                  <div v-if="record.status" style="font-size: 12px; color: #666;">
                    : {{ record.status === 1 ? '' : '' }}
                  </div>
                  <div v-if="record.remark" style="font-size: 12px; color: #999; margin-top: 4px;">
                    {{ record.remark }}
                  </div>
                </div>
              </template>
            </van-cell>
          </van-cell-group>

          <!--  -->
          <div class="pagination-container" style="text-align: center; padding: 20px;">
            <van-pagination
              v-model="currentPage"
              :total-items="totalItems"
              :items-per-page="pageSize"
              :show-page-size="3"
              force-ellipses
              @change="onPageChange"
            />
          </div>
        </div>

        <!--  -->
        <div class="loading-state" v-else>
          <span></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tuiguangcodejilu",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      recordList: [],
      loading: false,
      currentPage: 1,
      pageSize: 500,
      totalItems: 0,
      yiTuiGuang: 0,
      yiHeXiao: 0,
      unlimitedqrcodeUserId: null,
      useOldApi: false, // false=true=
    };
  },
  methods: {
    // 
    async getPromotionRecords(page = 1) {
      if (!this.unlimitedqrcodeUserId) {
        this.$toast("ID");
        return;
      }

      this.loading = true;
      try {
        //  useOldApi 
        const apiUrl = this.useOldApi 
          ? "/unlimitedqrcodejilu/getUnlimitedqrcodejilu"  // 
          : "/unlimitedqrcodejilu/getUnlimitedqrcodejilujihuo";  // 

        const res = await this.$request.get(apiUrl, {
          params: {
            page: page,
            pageSize: this.pageSize,
            unlimitedqrcodeUserId: this.unlimitedqrcodeUserId
          }
        });

        if (res?.code == 200) {
          const data = res.data;
          if (data) {
            this.recordList = data.unlimitedqrcodejilus || [];
            this.yiTuiGuang = data.yiTuiGuang || 0;
            this.yiHeXiao = data.yiHeXiao || 0;
            // 
            this.totalItems = Math.max(this.recordList.length * page, this.yiTuiGuang);
          }
        } else {
          this.$toast(res?.msg || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      } finally {
        this.loading = false;
      }
    },

    // 
    toggleApi() {
      this.useOldApi = !this.useOldApi;
      this.currentPage = 1;
      this.getPromotionRecords(1);
    },

    // 
    onPageChange(page) {
      this.currentPage = page;
      this.getPromotionRecords(page);
    },

    // 
    formatDate(dateStr) {
      if (!dateStr) return '';
      try {
        const date = new Date(dateStr);
        return date.toLocaleString('zh-CN', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit'
        });
      } catch (error) {
        return dateStr;
      }
    },
  },

  mounted() {
    // ID
    const userId = this.$route.query.userId;
    if (userId) {
      this.unlimitedqrcodeUserId = parseInt(userId);
      // 
      this.getPromotionRecords();
    } else {
      this.$toast("ID");
    }
  },
};
</script>

