<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="saveFenlei"
          ></van-button
        >
      </div>

      <div
        style="
          display: flex;
          padding: 20px 0px;
          border-radius: 15px 15px 0 0;
          width: 100%;
          box-sizing: border-box;
        "
      >
        <img :src="logo2" style="width: 40px; height: 40px" />
        <div class="text" style="display: flex; flex-wrap: wrap">
          <div style="color: #999; white-space: nowrap"></div>
          <div style="color: #000; white-space: nowrap">-</div>
        </div>
      </div>

      <div
        style="
          padding: 0 30px;
          font-size: 14px;
          font-weight: bold;
          width: 100%;
          box-sizing: border-box;
        "
      >
        <div
          style="display: flex; justify-content: flex-start; margin-bottom: 6px"
        >
          
        </div>
        <!-- <van-uploader :after-read="afterRead" :max-count="1" style="width: 100%"> -->
        <van-image
          :src="fankuitupian"
          radius="12"
          style="width: 100%"
          fit="contain"
          @click="zoom"
        />
        <!-- </van-uploader> -->
      </div>

      <div style="padding: 10px 0px; width: 100%">
        <div
          style="
            font-size: 14px;
            font-weight: bold;
            box-sizing: border-box;
            background-color: #fff;
            border-radius: 12px;
          "
        >
          <van-form @submit="onSubmit">
            <van-field
              type="textarea"
              autosize
              rows="1"
              v-model="fankuiyijian"
              name=""
              label=""
              placeholder=""
              readonly
            />
            <van-field
              type="textarea"
              autosize
              rows="1"
              v-model="guanliyuanhuifu"
              name=""
              label=""
              placeholder=""
            />
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import wode1 from '@/assets/wode2.png';
import { ImagePreview } from "vant";

export default {
  name: "CenterComp",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      logo3: require("../assets/logo3.png"),
      list: ["a", "b"],
      result: [],
      isChoose: false,
      fankuitupian: require("../assets/shuiguo.png"),
      fankuiyijian: "",
      number: "",
      leibie: "",
      leibieid: "",
      columns: [],
      showPicker: false,
      user: {},
      query: {},
      categories: [],
      miaoshu: "",
      phone: "",
      id: "",
      guanliyuanhuifu: "",
    };
  },
  methods: {
    zoom() {
      ImagePreview({
        images: [this.fankuitupian],
        closeable: true,
      });
    },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    goExit() {
      localStorage.clear();
      this.$router.push("/");
    },
    goChoose() {
      this.isChoose = !this.isChoose;
    },
    onSubmit(values) {
      console.log("submit", values);
    },
    onConfirm(value) {
      this.leibie = value;
      let foundItem = this.categories.find(
        (item) => item?.name != null && item.name == value
      );
      if (foundItem) {
        this.leibieid = foundItem.id;
      } else {
        this.leibieid = "";
      }
      this.showPicker = false;
      console.log("", value, this.leibie);
    },
    saveFenlei() {
      this.$request
        .post(`goodsyijianfankui`, {
          guanliyuanhuifu: this.guanliyuanhuifu,
          guanliyuanuserid: this.user.id,
          id: this.id,
        })
        .then((res) => {
          if (res?.code == "200") {
            this.$Toast("");
            this.$router.push("/feedbackmanage");
          } else {
            this.$Toast("");
          }
        })
        .catch((error) => {
          console.error("", error);
        });
    },
  },

  mounted() {
    let user = localStorage.getItem("bmk_user");
    if (user) {
      this.user = JSON.parse(user);
    }
    console.log("user", this.user, this.$route.query);
    if (this.$route.query.id) {
      this.query = this.$route.query;
      const { fankuiyijian, id, fankuitupian, guanliyuanhuifu } = this.query;
      this.fankuitupian = fankuitupian;
      this.fankuiyijian = fankuiyijian;
      this.id = id;
      this.guanliyuanhuifu = guanliyuanhuifu;
    }
    this.$request.get(`/goodscategories`).then((res) => {
      this.columns = res?.data?.map((v) => v.name);
      this.categories = res?.data;
    });
  },
};
</script>
