//  - 
router.beforeEach((to, from, next) => {
  // 
  const publicPages = ['/', '/register']
  const authRequired = !publicPages.includes(to.path)
  
  //  token
  const token = getToken() || localStorage.getItem('bmk_token')
  
  if (authRequired) {
    if (!token) {
      //  token
      console.log('')
      return next('/')
    }
    
    //  token 
    if (!isTokenValid()) {
      // token 
      console.log('Token ')
      localStorage.removeItem("bmk_token")
      localStorage.removeItem("bmk_userId")
      localStorage.removeItem("bmk_username")
      localStorage.removeItem("bmk_user")
      localStorage.removeItem("access_token")
      return next('/')
    }
  }
  
  next()
})

export default router