<script>
// import wode1 from '@/assets/wode2.png';
export default {
  name: "CenterComp",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      logo3: require("../assets/logo3.png"),
      list: ["a", "b"],
      result: [],
      isChoose: false,
      imgSrc: require("../assets/shuiguo.png"),
      columns: ["", ""],
      showPicker: false,
      user: {},
      query: {},
      isactive: "",
      columnsActive: ["", ""],
      showPickerActive: false,
      columnsCategory: [],
      showPickerCategory: false,

      address: "",
      bankaccount: "",
      beizhu: "",
      companyregistration: "",
      contactaddress: "",
      contactemail: "",
      contactname: "",
      contactphone: "",
      createdate: "",
      deliveryterms: "",
      description: "",
      goodssuppliersidid: "",
      id: 0,
      isdeleted: "",
      isverified: "",
      logo: "",
      name: "",
      ownerid: 0,
      paymentterms: "",
      status: "",
      taxid: "",
      updatedate: "",
      website: "",
    };
  },
  methods: {
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    goExit() {
      localStorage.clear();
      this.$router.push("/");
    },
    goChoose() {
      this.isChoose = !this.isChoose;
    },
    //  Base64  Blob
    base64ToBlob(base64, contentType = "", sliceSize = 512) {
      const byteCharacters = atob(base64.split(",")[1]);
      const byteArrays = [];

      for (
        let offset = 0;
        offset < byteCharacters.length;
        offset += sliceSize
      ) {
        const slice = byteCharacters.slice(offset, offset + sliceSize);

        const byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
        }

        const byteArray = new Uint8Array(byteNumbers);
        byteArrays.push(byteArray);
      }

      return new Blob(byteArrays, { type: contentType });
    },
    afterRead(e) {
      console.log(e, this.user.userId);
      this.imgSrc = e.content;
      const base64Data = e.content;
      const blob = this.base64ToBlob(base64Data, "image/jpeg"); //  Blob
      console.log("blob", blob);
      const now = new Date();

      const formatNumber = (n) => {
        n = n.toString();
        return n[1] ? n : `0${n}`;
      };
      const formatTime = (date, format, seconds) => {
        console.log("formatTime:", date, format, seconds);
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const hour = date.getHours();
        const minute = date.getMinutes();
        const second = date.getSeconds();
        if (format == 1) {
          return `${year}-${formatNumber(month)}-${formatNumber(day)}`;
        } else if (format == 2) {
          if (seconds) {
            return `${formatNumber(month)}-${formatNumber(day)} ${formatNumber(
              hour
            )}:${formatNumber(minute)}}`;
          } else {
            return `${formatNumber(month)}-${formatNumber(day)} ${formatNumber(
              hour
            )}:${formatNumber(minute)}:${formatNumber(second)}`;
          }
        } else if (format == 3) {
          return `${year}-${formatNumber(month)}-${formatNumber(
            day
          )} ${formatNumber(hour)}:${formatNumber(minute)}:${formatNumber(
            second
          )}`;
        } else {
          return `${year}${formatNumber(month)}${formatNumber(
            day
          )}${formatNumber(hour)}${formatNumber(minute)}${formatNumber(
            second
          )}`;
        }
      };
      const filename = `supplierlogophotosUrl${formatTime(now)}`;
      //  FormData 
      const formData = new FormData();
      formData.append("id", this.user.userId); // ID
      formData.append("photosurlwei", filename);
      formData.append("file", blob, "photo.jpg"); //  Blob  FormData
      console.log(
        "formData:",
        formData.get("id"),
        formData.get("photosurlwei")
      );

      this.$request
        .post(`goodssuppliers/uploadGoodssuppliersPhotosUrlWangye`, formData)
        .then((res) => {
          console.log("", res);
          if (res?.code == "200") {
            this.$Toast("");
            this.imgSrc = res.data;
          } else {
            this.$Toast("");
          }
        })
        .catch((error) => {
          console.error("", error);
        });
    },
    onSubmit(values) {
      console.log("submit", values);
    },
    onConfirm(value) {
      this.deliverymethods = value;
      this.showPicker = false;
    },
    onConfirmActive(value) {
      this.isverified = value;
      this.showPickerActive = false;
    },
    saveFenlei() {
      this.$request
        .post(`/goodssuppliers/savewanyeGoodssuppliers`, {
          name: this.name,
          bankaccount: this.bankaccount,
          description: this.description,
          contactemail: this.email,
          address: this.address,
          companyregistration: this.companyregistration,
          paymentterms: this.paymentterms,
          contactname: this.contactname,
          contactphone: this.contactphone,
          ownerid: this.ownerid,
          beizhu: this.beizhu,
          taxid: this.taxid,
          website: this.website,
          isverified: this.isverified,
          deliveryterms: this.deliveryterms,
          id: this.id,
          logo: this.imgSrc,
        })
        .then((res) => {
          console.log("", res);
          if (res?.code == "200") {
            this.$Toast("");
            this.$router.push("/suppliermanage");
          } else {
            this.$Toast("");
          }
        })
        .catch((error) => {
          console.error("", error);
        });
    },
  },

  mounted() {
    let user = localStorage.getItem("bmk_user");
    if (user) {
      this.user = JSON.parse(user);
    }
    console.log("user", this.user, this.$route.query);
    if (this.$route.query.id) {
