<template>
  <div class="page-container">
    <div class="content-container">
      <div
        style="display: flex; justify-content: space-between; padding: 8px 15px"
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
      </div>
      <div class="comment-list" @scroll="handleScroll">
        <div class="comment-wrap">
          <div
            class="comment-item"
            v-for="(comment, ind) in data"
            :key="comment.commentId"
          >
            <div class="comment-header">
              <img alt="logo" :src="comment.headPhotoUrl" class="avatar" />
              <div class="content-wrap">
                <div class="username">{{ comment.username }}</div>
                <div class="content">{{ comment.commentContent }}</div>
                <div class="comment-footer">
                  <div class="status">{{ comment.status }}</div>
                  <div class="create-time">{{ comment.createTime }}</div>
                </div>
                <div class="btn-wrap">
                  <div
                    class="manage-btn"
                    @click.stop="manageDelCommentEvt(comment.commentId, ind)"
                  >
                    {{ comment.isDel ? "" : "" }}
                  </div>
                </div>
              </div>
            </div>

            <div
              class="reply-list"
              v-if="comment.secondLevelCommentList?.length > 0"
            >
              <div
                v-for="(second, secInd) in comment.secondLevelCommentList"
                :key="second.commentId"
                class="reply-item"
              >
                <img
                  alt="logo"
                  :src="second.headPhotoUrl"
                  class="avatar reply-avatar"
                />
                <div class="content-wrap">
                  <div class="username">{{ second.username }}</div>
                  <div class="content">{{ second.commentContent }}</div>
                  <div class="comment-footer">
                    <div class="status">{{ second.status }}</div>
                    <div class="create-time">{{ second.createTime }}</div>
                  </div>
                  <div class="btn-wrap">
                    <div
                      class="manage-btn"
                      @click.stop="
                        manageDelSecondCommentEvt(ind, second.commentId, secInd)
                      "
                    >
                      {{ second.isDel ? "" : "" }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "viewerjs/dist/viewer.css";
import Viewer from "viewerjs";
export default {
  name: "CommentComp",
  data() {
    return {
      viewerOptions: {
        toolbar: false,
        navbar: true,
        title: true,
        fullscreen: true,
        movable: false,
        zoomable: true,
        loop: false,
      },
      gallery: null,
      data: {},
      page: 1,
      pageSize: 20,
      pageFirstComments: 1,
      pageSizeFirstComments: 20,
      articleIdid: "",
      loading: false,
      hasMoreData: true,
    };
  },
  components: {},
  props: {},
  mounted() {
    this.initViewer();
    this.getComments();
    window.addEventListener("scroll", this.handleScroll);
  },
  updated() {
    this.initViewer();
  },
  methods: {
    initViewer() {
      this.$nextTick(() => {
        if (this.gallery) {
          this.gallery.destroy();
        }
        if (this.$refs.images) {
          this.gallery = new Viewer(this.$refs.images, this.viewerOptions);
        }
      });
    },
    clickImg() {
      if (this.gallery) {
        this.gallery.show();
      }
    },
    manageDelSecondCommentEvt(idx, commentId, index) {
      let userId = localStorage.getItem("bmk_userId") || "";
      let params = {
        commentId: commentId,
        articleIdid: this.articleIdid,
        status: "",
        userId,
      };
      this.$request
        .post("comment/updateCommentStatusBychaojiguanliyuan", params)
        .then((res) => {
          console.log("commentId", commentId);
          if (res.code == "200") {
            this.$set(
              this.data[idx].secondLevelCommentList[index],
              "isDel",
              true
            );
          } else {
            console.log("");
          }
        });
    },
    manageDelCommentEvt(commentId, index) {
      let userId = localStorage.getItem("bmk_userId") || "";
      let params = {
        commentId: commentId,
        articleIdid: this.articleIdid,
        status: "",
        userId,
      };
      this.$request
        .post("comment/updateCommentStatusBychaojiguanliyuan", params)
        .then((res) => {
          console.log("commentId", commentId);
          if (res.code == "200") {
            this.$set(this.data[index], "isDel", true);
          } else {
            console.log("");
          }
        });
    },
    getComments() {
      return this.$request
        .get("comment/getCommentsWangye", {
          params: {
            page: this.page,
            pageSize: this.pageSize,
          },
        })
        .then((res) => {
          console.log("getComments--res", res);
          if (!res || res.length === 0) {
            this.hasMoreData = false;
          }
          if (this.page === 1) {
            this.data = res || [];
          } else {
            this.data.push(...(res || []));
          }
        });
    },
    loadMoreData() {
      if (this.loading || !this.hasMoreData) return;
      this.loading = true;
      this.page++;
      this.getComments().finally(() => {
        this.loading = false;
      });
    },
    handleScroll(event) {
      const scrollTop = event.target.scrollTop;
      const windowHeight = event.target.clientHeight;
      const documentHeight = event.target.scrollHeight;

      if (scrollTop + windowHeight >= documentHeight - 200 && !this.loading) {
        this.loadMoreData();
      }
    },
  },
  beforeDestroy() {
    if (this.gallery) {
      this.gallery.destroy();
    }
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

