<script>
import { Button as VanButton, Icon } from "vant";
import { Field } from "vant";
import { Uploader } from "vant";
import { Image as VanImage } from "vant";

export default {
  components: {
    VanButton,
    VanIcon: Icon,
    VanField: Field,
    VanUploader: Uploader,
    VanImage,
  },
  props: {},
  data() {
    return {
      items: [null, null, null],
      imgSrc1: require("../assets/upload-pic.png"),
      fileList: [{ url: "https://img01.yzcdn.cn/vant/leaf.jpg" }],
      text: "",
      user: {},
      id: "",
      isUpload: false,
      peisongtibaoshijian: "",
    };
  },

  methods: {
    // 
    goBack() {
      //  sessionStorage
      sessionStorage.setItem(
        "scrollPosition",
        window.pageYOffset || document.documentElement.scrollTop
      );
      this.$router.go(-1);
    },
    //  Base64  Blob
    base64ToBlob(base64, contentType = "", sliceSize = 512) {
      const byteCharacters = atob(base64.split(",")[1]);
      const byteArrays = [];

      for (
        let offset = 0;
        offset < byteCharacters.length;
        offset += sliceSize
      ) {
        const slice = byteCharacters.slice(offset, offset + sliceSize);

        const byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
        }

        const byteArray = new Uint8Array(byteNumbers);
        byteArrays.push(byteArray);
      }

      return new Blob(byteArrays, { type: contentType });
    },
    getCurrentTimestamp() {
      const now = new Date();
      const pad = (n) => (n < 10 ? `0${n}` : `${n}`);
      return `${now.getFullYear()}-${pad(now.getMonth() + 1)}-${pad(
        now.getDate()
      )} ${pad(now.getHours())}:${pad(now.getMinutes())}:${pad(
        now.getSeconds()
      )}`;
    },
    afterReadOne(e) {
      console.log(e, this.user.userId);

      // 
      this.$toast.loading({
        message: '...',
        forbidClick: true,
        duration: 0 // 
      });

      const base64Data = e.content;
      const blob = this.base64ToBlob(base64Data, "image/jpeg"); //  Blob
      console.log("blob", blob);
      const now = new Date();

      const formatNumber = (n) => {
        n = n.toString();
        return n[1] ? n : `0${n}`;
      };
      const formatTime = (date, format, seconds) => {
        console.log("formatTime:", date, format, seconds);
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const hour = date.getHours();
        const minute = date.getMinutes();
        const second = date.getSeconds();
        if (format == 1) {
          return `${year}-${formatNumber(month)}-${formatNumber(day)}`;
        } else if (format == 2) {
          if (seconds) {
            return `${formatNumber(month)}-${formatNumber(day)} ${formatNumber(
              hour
            )}:${formatNumber(minute)}}`;
          } else {
            return `${formatNumber(month)}-${formatNumber(day)} ${formatNumber(
              hour
            )}:${formatNumber(minute)}:${formatNumber(second)}`;
          }
        } else if (format == 3) {
          return `${year}-${formatNumber(month)}-${formatNumber(
            day
          )} ${formatNumber(hour)}:${formatNumber(minute)}:${formatNumber(
            second
          )}`;
        } else {
          return `${year}${formatNumber(month)}${formatNumber(
            day
          )}${formatNumber(hour)}${formatNumber(minute)}${formatNumber(
            second
          )}`;
        }
      };
      const filename = `yuedingweizhiphotosUrl${formatTime(now)}`;
      //  FormData 
      const formData = new FormData();
      formData.append("userId", this.user.userId); // ID
      formData.append("photosurlwei", filename);
      formData.append("goodsdingdannumber", this.id);
      formData.append("file", blob, "photo.jpg"); //  Blob  FormData
      console.log(
        "formData:",
        formData.get("goodsdingdannumber"),
        formData.get("photosurlwei")
      );

      this.$request
        .post(`/goodsdingdan/uploadpeisongphoto`, formData)
        .then((res) => {
          console.log("", res);
          // 
          this.$toast.clear();
          if (res?.code == "200") {
            this.$Toast("");
            this.imgSrc1 = res.data;
            this.isUpload = true;
          } else {
            this.$Toast("");
          }
        })
        .catch((error) => {
          console.error("", error);
          // 
          this.$toast.clear();
          this.$Toast("");
        });
    },
    submit() {
      if (!this.isUpload) {
        return this.$toast("");
      }
      this.$request
        .post("/goodsdingdan/partialUpdate", {
          id: this.id,
          peisongtibaouserid: this.user.userId,
          peisongphoto: this.imgSrc1,
          peisongshuoming: this.text,
          peisongstatus: "",
          activestatus: "finish",
          peisongyuanuserid: this.user.userId,
          peisongtibaoshijian: (this.peisongtibaoshijian =
            this.getCurrentTimestamp()),
          goodsdingdanstatus: "",
        })
        .then((res) => {
          console.log("======", res);
          if (res.code == "200") {
            this.$router.go(-1);
          } else {
            this.$toast(res.msg);
          }
        });
    },
    // 
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    // 
    handleFileSelect(event) {
      const file = event.target.files[0];
      if (file) {
        this.processFile(file);
      }
    },
    // 
    openCamera() {
      // capture
      const input = document.createElement('input');
      input.type = 'file';
      input.accept = 'image/*';
      input.capture = 'environment'; // 
      input.style.display = 'none';
      
      input.onchange = (event) => {
        const file = event.target.files[0];
        if (file) {
          this.processFile(file);
        }
        // 
        document.body.removeChild(input);
      };
      
      // DOM
      document.body.appendChild(input);
      input.click();
    },
    // 
    processFile(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        // afterReadOne
        const fileData = {
          content: e.target.result,
          file: file
        };
        this.afterReadOne(fileData);
      };
      reader.readAsDataURL(file);
    },
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    if (user) {
      this.user = JSON.parse(user);
    }
    console.log("", this.$route.query.id);
    if (this.$route.query.id) {
      this.id = this.$route.query.id;
    }

    // 
    this.$nextTick(() => {
      // 
      this.openCamera();

      // 
      const savedPosition = sessionStorage.getItem("scrollPosition");
      if (savedPosition) {
        window.scrollTo(0, parseInt(savedPosition));
        // 
        sessionStorage.removeItem("scrollPosition");
      }
    });
  },
};
</script>

