            class="order-card"
            @click="viewOrderDetail(order)"
          >
            <!--  -->
            <div class="order-header">
              <div class="order-number-section">
                <div class="order-icon"></div>
                <div class="order-number">
                  <div class="order-number-main" v-html="formatOrderNumber(order.goodsdingdannumber)"></div>
                </div>
              </div>
              <div class="order-status-badge">
                {{ order.peisongstatus || '' }}
              </div>
            </div>
            
            <!--  -->
            <div class="order-body">
              <!--  -  -->
              <div class="status-section">
                <div class="status-item">
                  <span class="status-icon"></span>
                  <span class="status-label"></span>
                  <span class="status-value status-overall">{{ order.goodsdingdanstatus || '' }}</span>
                </div>
                <div class="status-item">
                  <span class="status-icon"></span>
                  <span class="status-label"></span>
                  <span class="status-value status-payment">{{ order.paymentstatus || '' }}</span>
                </div>
                <div class="status-item">
                  <span class="status-icon"></span>
