<script>
import { Toast } from "vant";

export default {
  components: {},
  props: {},
  data() {
    return {
      allOrders: [], // 
      user: {},
      currentPage: 1,
      pageSize: 10,
      totalOrders: 0,
      totalPages: 0,
      loading: false,
      hasMore: true,
    };
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
    if (this.user.userId) {
      this.getAllOrders();
      this.addScrollListener();
    } else {
      Toast("");
    }
  },
  beforeDestroy() {
    this.removeScrollListener();
  },
  methods: {
    // 
    getAllOrders(isLoadMore = false) {
      if (this.loading) return;
      
      this.loading = true;
      this.$request
        .get("/goodsdingdan/findalldingdan", {
          params: {
            page: this.currentPage,
            pageSize: this.pageSize,
          },
        })
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            const newOrders = res.data.records || [];
            if (isLoadMore) {
              this.allOrders = [...this.allOrders, ...newOrders];
            } else {
              this.allOrders = newOrders;
            }
            this.totalOrders = res.data.total || 0;
            this.totalPages = res.data.pages || 0;
            this.hasMore = this.currentPage < this.totalPages;
          }
        })
        .catch((error) => {
          console.error(":", error);
          Toast("");
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    addScrollListener() {
      window.addEventListener('scroll', this.handleScroll);
    },
    // 
    removeScrollListener() {
      window.removeEventListener('scroll', this.handleScroll);
    },
    // 
    handleScroll() {
      if (this.loading || !this.hasMore) return;
      
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight;
      
      // 100px
      if (scrollTop + windowHeight >= documentHeight - 100) {
        this.loadMore();
      }
    },
    // 
    loadMore() {
      if (this.hasMore && !this.loading) {
        this.currentPage++;
        this.getAllOrders(true);
      }
    },
    // 11-185
    formatOrderNumber(orderNumber) {
      if (!orderNumber) return '';
      
      const str = String(orderNumber);
      const length = str.length;
      
      // 
      if (length < 5) return str;
      
      let result = '';
      
      for (let i = 0; i < length; i++) {
        const char = str[i];
        const position = i + 1; // 1
        
        // 11-1810-17
        const isMiddleBold = position >= 11 && position <= 18;
        // 5
        const isLastBold = position > length - 5;
        
        if (isMiddleBold || isLastBold) {
          result += `<strong style="color: #000000; font-weight: 700;">${char}</strong>`;
        } else {
          result += char;
        }
      }
      
      return result;
    },
    // 
    viewOrderDetail(order) {
      console.log(":", order);
      // 
      this.$router.push({
        name: 'xiugaidingdanitem',
        params: {
          goodsdingdannumber: order.goodsdingdannumber
        }
      });
    },
  },
};
</script>

