    ossUpload(file, index, content) {
      return new Promise((resolve, reject) => {
        console.log("content", content);
        const now = new Date();

        const formatNumber = (n) => {
          n = n.toString();
          return n[1] ? n : `0${n}`;
        };
        const formatTime = (date, format, seconds) => {
          console.log("formatTime:", date, format, seconds);
          const year = date.getFullYear();
          const month = date.getMonth() + 1;
          const day = date.getDate();
          const hour = date.getHours();
          const minute = date.getMinutes();
          const second = date.getSeconds();
          if (format == 1) {
            return `${year}-${formatNumber(month)}-${formatNumber(day)}`;
          } else if (format == 2) {
            if (seconds) {
              return `${formatNumber(month)}-${formatNumber(
                day
              )} ${formatNumber(hour)}:${formatNumber(minute)}}`;
            } else {
              return `${formatNumber(month)}-${formatNumber(
                day
              )} ${formatNumber(hour)}:${formatNumber(minute)}:${formatNumber(
                second
              )}`;
            }
          } else if (format == 3) {
            return `${year}-${formatNumber(month)}-${formatNumber(
              day
            )} ${formatNumber(hour)}:${formatNumber(minute)}:${formatNumber(
              second
            )}`;
          } else {
            return `${year}${formatNumber(month)}${formatNumber(
              day
            )}${formatNumber(hour)}${formatNumber(minute)}${formatNumber(
              second
            )}`;
          }
        };
        const filename = `${this.articleIdid}-photosUrl-${formatTime(
          now
        )}-${index}`;

        //  FormData 
        const formData = new FormData();
        formData.append("userId", this.userId);
        formData.append("articleIdid", this.articleIdid);
        formData.append("photosurlwei", filename);
        formData.append("file", content, "photo.jpg"); //  Blob  FormData
        console.log(
          "formData:",
          formData,
          formData.get("userId"),
          formData.get("file")
        );

        this.$request
          .post(`article/uploadFilePhotosUrlWangye`, formData)
          .then((res) => {
            console.log("article/uploadFilePhotosUrlWangye", res);
            if (res.code == 200) {
              console.log("uploadFile success", res);
              resolve(res);
            } else {
              reject(new Error(`Upload failed with status code ${res.code}`));
            }
          });
      });
    },
