<script>
import PrintService from "../utils/printService";
// import printer from "../utils/printer";

// import wode1 from '@/assets/wode2.png';

export default {
  name: "CenterComp",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      logo3: require("../assets/logo3.png"),
      list: [],
      result: [],
      isChoose: false,
      scrollPosition: 0,
      debugPrinting: false,

      printData: {
        title: "",
        subtitle: "",
        date: new Date().toLocaleString(),
        orderNo: "ORDER" + Date.now().toString().slice(-8),
        items: [
          {
            name: " Member's Mark   6L(200mL*30)",
            quantity: 1,
            price: 85.9,
            amount: 85.9,
          },
        ],
        total: "10.00",
        qrcode: "https://example.com",
        footer: "",

        address: "218503",
        dizhi: "2232",
        phone: "139****2020",
        lianxiren: "~_~()",
        yujisongda: "12-16 18:30",
        yuedingweizhi: "",
        beizhu: "",
        leixing: "",
        // totalNum: 4,
        // shijizhifu: 69.11,
      },
    };
  },
  methods: {
    // 
    async connectBluetooth() {
      try {
        if (!navigator.bluetooth) {
          this.$toast.fail("");
          return;
        }

        this.$toast.loading({
          message: "...",
          forbidClick: true,
        });

        const device = await navigator.bluetooth.requestDevice({
          acceptAllDevices: true,
          optionalServices: ["generic_access"],
        });

        this.bluetoothDevice = device;
        this.$toast.success(": " + device.name);
      } catch (error) {
        this.$toast.fail(": " + error.message);
      }
    },

    async goPrint() {
      try {
        // 
        // if (
        //   !this.printData.title ||
        //   !this.printData.items ||
        //   this.printData.items.length === 0
        // ) {
        //   this.$toast.fail("");
        //   return;
        // }
        this.$toast.loading({ message: "...", forbidClick: true });
        // 
        this.printData.date = new Date().toLocaleString();
        this.calculateTotal();
        // PrintServiceGBKESC/POS
        const result = await PrintService.printReceipt(this.list[0]);
        if (result) {
          // this.$toast.success("");
        } else {
          this.$toast.fail("UUID");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast.fail(": " + error.message);
      }
    },

    // 
    addItem() {
      this.printData.items.push({
        name: "",
        quantity: 1,
        price: 0,
        amount: 0,
      });
    },

    // 
    removeItem(index) {
      this.printData.items.splice(index, 1);
      this.calculateTotal();
    },

    // 
    calculateAmount(index) {
      const item = this.printData.items[index];
      const quantity = parseFloat(item.quantity) || 0;
      const price = parseFloat(item.price) || 0;
      item.amount = (quantity * price).toFixed(2);
      this.calculateTotal();
    },

    // 
    calculateTotal() {
      let total = 0;
      this.printData.items.forEach((item) => {
        total += parseFloat(item.amount) || 0;
      });
      this.printData.total = total.toFixed(2);
    },
    saveScrollPosition() {
      this.scrollPosition = window.scrollY;
      sessionStorage.setItem("goodsListScrollPosition", this.scrollPosition);
      sessionStorage.setItem("lastRoute", this.$route.name);
    },
    restoreScrollPosition() {
      const savedPosition = sessionStorage.getItem("goodsListScrollPosition");
      const lastRoute = sessionStorage.getItem("lastRoute");
      if (savedPosition && lastRoute == "feedback") {
        this.scrollPosition = parseInt(savedPosition);
        setTimeout(() => {
          window.scrollTo(0, this.scrollPosition);
        }, 100);
      }
    },
    toggle(index) {
      // this.$refs.checkboxes[index].toggle();
      console.log("index", index);
    },
    goExit() {
      localStorage.clear();
      this.$router.push("/");
    },
    goChoose() {
      this.isChoose = !this.isChoose;
    },
    delOrCreate() {
      if (!this.isChoose) {
        this.$router.push("/fenlei");
      } else {
        this.delItem();
      }
    },
    getData() {
      this.$request
        .get("/goodsdingdan/findWeidayingdingdan", {
          params: { page: 1, pageSize: 50 },
        })
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            res.data?.map((v) => {
              v.date = this.formatter(v.createdata, 1);
            });
            this.printData = (res.data?.length > 0 && res.data[0]) || {};
            this.list = res.data;
            console.log("===this.printData===", this.printData);
          }
        });
    },
    delItem() {
      let choose = this.result.map((v) => v.id);
      this.$request.post("goodsyijianfankui/del/batch", choose).then((res) => {
        console.log("", res);
        if (res.code == 200) {
          this.getData();
        }
      });
    },
    changeCheckboxGroup(a) {
      console.log("changeCheckboxGroup", a, this.result);
    },
    changeCheckbox(a) {
      console.log("changeCheckbox", a);
    },
    goFenlei(item) {
      this.$router.push({ path: "/feedback", query: item });
    },
    formatter(rawDate, total) {
      if (!rawDate || typeof rawDate !== "string" || rawDate.length < 14) {
        console.error(":", rawDate);
        return rawDate;
      }
      if (total) {
        return `${rawDate.slice(0, 4)}-${rawDate.slice(4, 6)}-${rawDate.slice(
          6,
          8
        )} ${rawDate.slice(8, 10)}:${rawDate.slice(10, 12)}:${rawDate.slice(
          12,
          14
        )}`;
      } else {
        return `${rawDate.slice(0, 4)}-${rawDate.slice(4, 6)}-${rawDate.slice(
          6,
          8
        )}`;
      }
    },
    async printReceipts(receiptDataArray) {
      for (const data of receiptDataArray) {
        const result = await PrintService.printReceipt(data);
        console.log(":", result);
      }
    },
    async handleEachPrintSuccess(id, index) {
      console.log(`${index + 1}...`);

      try {
        const res = await this.$request
          .post("goodsdingdan/goodsdingdandayingchenggong", { id });
        if (res.code == "200") {
          console.log("", res);
        }
      } catch (error) {
        console.error(`${index + 1}:`, error);
      }
    },

    async debugPrint() {
      if (this._debugPrinting) return; // 
      this._debugPrinting = true;
      try {
        this.$toast.loading({
          message: "...",
          forbidClick: true,
        });
        // 
        // const data = { ...this.printData };
        // data.date = new Date().toLocaleString();
        // this.calculateTotal();
        // 
        // const buffer = printer.printReceipt(data);
        // console.log("ESC/POSUint8Array:", buffer);

        for (const order of this.list) {
          console.log(`: ${order.goodsdingdannumber}`);
          const result = await PrintService.printReceipt(order);
          if (result) {
            await this.handleEachPrintSuccess(order.id, this.list.indexOf(order));
          } else {
            console.error(":", order.goodsdingdannumber);
          }
        }
        this.$toast.success("");
      } catch (error) {
        console.error(":", error);
        this.$toast(": " + error);
      } finally {
        this._debugPrinting = false;
      }
    },
  },

  beforeRouteLeave(to, from, next) {
    this.saveScrollPosition();
    next();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.name === "feedback") {
        setTimeout(() => {
          vm.restoreScrollPosition();
        }, 0);
      }
    });
  },
  mounted() {
    const user = localStorage.getItem("bmk_user");
    console.log(" user", user);
    this.getData();
  },
};
</script>
