<template>
  <div class="global-container">
    <div class="top">
      <div class="header-container">
        <van-button
          type="primary"
          color="#fa6286"
          class="back-button"
          @click="$goMenu"
          ></van-button
        >
        <div class="title-container">
  <span class="title-text"> (  {{ items.length }}  )</span>
        </div>
      </div>
      <div class="flex-col page">
        <div class="sse-demo">
          <div class="sse-header">
            <h2>SSE</h2>
            <div class="sse-controls">
              <button
                @click="connectSSE"
                :disabled="isConnected"
                class="connect-btn"
              >
                {{ isConnected ? "" : "" }}
              </button>
              <button
                @click="sendTestMessage"
                :disabled="!isConnected"
                class="test-btn"
              >
                
              </button>
            </div>
          </div>

          <div class="sse-content">
            <div class="status-panel">
              <p class="status-info">
                <span class="status-label">ID:</span>
                <span class="status-value">{{ clientId || "" }}</span>
              </p>
              <p class="status-info">
                <span class="status-label">:</span>
                <span class="status-value">{{ connectionStatus }}</span>
              </p>
              <div v-if="error" class="error-message">
                {{ error.message }}
              </div>
            </div>
          </div>
        </div>

        <div class="mt-10 flex-col group">
          <div class="flex-col">
            <div class="flex-col">
              <div class="location-info-panel">
                <div class="location-info-item">
                  <span class="location-label">:</span>
                  <span
                    class="location-value"
                    :class="{
                      connected: isConnected,
                      disconnected: !isConnected,
                    }"
                    >{{ isConnected ? "" : "" }}</span
                  >
                </div>
                <div class="location-info-item">
                  <span class="location-label">:</span>
                  <span class="location-value">{{
                    newLocation || ""
                  }}</span>
                </div>
                <div class="location-info-row">
                  <div class="location-info-item half-width">
                    <span class="location-label">:</span>
                    <span class="location-value">{{ longitude || "-" }}</span>
                  </div>
                  <div class="location-info-item half-width">
                    <span class="location-label">:</span>
                    <span class="location-value">{{ latitude || "-" }}</span>
                  </div>
                </div>
              </div>
              <div
                class="mt-10 flex-col section_3"
                v-for="item in items"
                v-bind:key="item.id"
              >
                <div
                  class="flex-row justify-between items-center self-stretch section_4 address-wrapper"
                  style="gap: 12px"
                >
                  <div class="flex-row items-center address-container">
                    <div
                      class="flex-col justify-start items-center shrink-0 text-wrapper_5"
                    ><span class="font_6 text_11">{{ items.indexOf(item) + 1 }}</span>
                      <!-- <span class="font_6 text_11">1</span> -->
                    </div>
                    <span
                      class="ml-14 font_3 text_9 flex-1"
                      style="
                        word-break: break-word;
                        white-space: normal;
                      "
                      >{{ item.goodsuseridaddress?.address }}</span
                    >
                  </div>
                  <span class="font_3 text_10 icons-container">{{
                    item.peisongstatus
                  }}</span>
                </div>
                <div
                  class="flex-row justify-between items-center self-stretch group_3"
                >
                  <span class="font_7"
                    > {{ formatTimestamp(item.yujisongdadata) }}</span
                  >
                </div>
                <div
                  class="flex-row justify-between items-center self-stretch group_3 order-number-row"
                >
                  <span class="font_8 text_12"
                    >#{{ item.goodsdingdannumber }}</span
                  >
                </div>
                <div
                  class="flex-row justify-between items-center self-stretch group_4 address-wrapper"
                  @click="report(item.goodsdingdannumber, 'xiangqing')"
                >
                  <div class="flex-col items-start address-container">
                    <span class="font_2 text_13 address-title">{{
                      item.goodsuseridaddressaddress
                    }}</span>
                    <span class="mt-12 font_9 address-detail">{{
                      item.goodsuseridaddressdingweidizhi
                    }}</span>
                  </div>
                  <div class="flex-row icons-container">
                    <img
                      class="image_4 ml-3"
                      v-for="a in item.goodsdingdanitem"
                      v-bind:key="a.id"
                      :src="a.goodsgoodsididshouphoto"
                    />
                  </div>
                </div>
                <div
                  class="flex-col group_4"
                  @click="report(item.goodsdingdannumber, 'xiangqing')"
                >
                  <span class="self-end font_10 text_14"
                    >{{ item.goodsgoodstotalnumber }} ></span
                  >
                  <span class="mt-4 self-start font_7"
                    >{{ item.goodsuseridaddressname }} {{
                      item.goodsuseridaddressphone
                    }}</span
                  >
                </div>
                <span class="self-start font_11 text_15"
                  >{{ item.comment }}</span
                >
                <div class="grid">
                  <div
                    class="flex-col items-center relative grid-item"
                    @click="report(item.id, 'baobei')"
                  >
                    <img class="image_5" src="@/assets/abnormal.png" />
                    <span class="mt-6 font_12"></span>
                  </div>
                  <div
                    class="flex-col items-center relative grid-item"
                    @click="copyToClipboard(item)"
                  >
                    <img class="image_5" src="@/assets/copy.png" />
                    <span class="mt-6 font_12"></span>
                  </div>
                  <div
                    class="flex-col items-center relative grid-item"
                    @click="report(item.id, 'weizhi')"
                  >
                    <img class="image_5" src="@/assets/location.png" />
                    <span class="mt-6 font_12"></span>
                  </div>
                  <div
                    class="flex-col items-center relative grid-item"
                    @click="openMap(item)"
                  >
                    <img class="image_5" src="@/assets/navigate.png" />
                    <span class="mt-6 font_12"></span>
                  </div>
                  <div
                    class="flex-col items-center relative grid-item"
                    @click="confirmCancelOrder(item)"
                  >
                    <img class="image_5" src="@/assets/cancel.png" />
                    <span class="font_12 mt-7"></span>
                  </div>
                  <div
                    class="flex-col items-center relative grid-item"
                    @click="report(item.id, 'tuikuan', item.outtradeno)"
                  >
                    <img class="image_5" src="@/assets/refund.png" />
                    <span class="font_12 mt-7"></span>
                  </div>
                </div>
                <div class="shrink-0 self-stretch relative divider view"></div>
                <div class="flex-row self-stretch group_6 view_2">
                  <div
                    class="flex-col justify-start items-center shrink-0 text-wrapper_6 call-button"
                    @click="copyAndCall(item.goodsuseridaddress?.phone)"
                  >
                    <span class="font_13 text_16"></span>
                  </div>
                  <div
                    class="flex-col justify-start items-center shrink-0 text-wrapper_6 deliver-button"
                    @click="confirmDeliver(item)"
                  >
                    <span class="font_13 text_16"> ></span>
                  </div>
                </div>
              </div>
            </div>
            <div class="mt-24 flex-col"></div>
          </div>
        </div>
      </div>
    </div>
    
    <!--  -->
    <div v-if="showCancelOrderDialog" class="custom-popup-overlay" @click.self="showCancelOrderDialog = false">
      <div class="cancel-order-dialog">
        <!--  -->
        <div class="dialog-top-bar"></div>
        
        <!--  -->
        <div class="dialog-header">
          <div class="header-icon"></div>
          <h3 class="dialog-title"></h3>
          <p class="dialog-subtitle"></p>
        </div>
        
        <!--  -->
        <div class="dialog-content">
          <!--  -->
          <div class="order-info-card">
            <div class="order-info-header">
              <span class="order-icon"></span>
              <span class="order-label"></span>
            </div>
            <div class="order-number-wrapper">
              <div class="order-number">{{ currentCancelOrder?.goodsdingdannumber }}</div>
            </div>
          </div>
          
          <!--  -->
          <div class="input-section">
            <div class="input-label"></div>
            <van-field
              v-model="cancelOrderInput"
              type="tel"
              placeholder="-----"
              maxlength="5"
              class="order-input"
              :class="{ 'input-error': cancelOrderError }"
              :border="false"
              @input="handleCancelOrderInput"
            />
            <div v-if="cancelOrderError" class="error-message">{{ cancelOrderError }}</div>
          </div>
        </div>
        
        <!--  -->
        <div class="dialog-footer">
          <button
            class="cancel-btn"
            @click="showCancelOrderDialog = false"
          >
            
          </button>
          <button
            class="confirm-btn"
            @click="handleCancelOrderConfirm"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import sseService from "@/utils/sse";
import { Dialog } from "vant";

export default {
  components: {},
  props: {},
  data() {
    return {
      error: {},
      items: [],
      isConnected: false,
      clientId: null,
      connectionStatus: "",
      messages: [],

      user: {},
      userId: "",

      positionNum: 0,
      positionLogs: [],
      windowHeight: document.body.clientHeight,
      geolocation: null,
      watchId: null,

      newLocation: "",
      latitude: "",
      longitude: "",
      locationSent: false, // 
      cancelOrderInput: "", // 
      cancelOrderError: "", // 
      showCancelOrderDialog: false, // 
      currentCancelOrder: null, // 
    };
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
    if (this.user.userId) {
      this.getData();
      // SSE
      this.connectSSE();

      // 
      this.geolocation = new window.qq.maps.Geolocation(
        "7NMBZ-Q24WW-4CLRO-OKBAZ-TE3VF-2WFMC",
        "myapp"
      );

      this.getPreciseLocation();

      // 
      window.addEventListener("resize", this.updateWindowHeight);
    } else {
      this.$Toast("");
    }
  },
  methods: {
    updateLocation(longitude, latitude) {
      this.$request
        .post("/user/updateLocation", {
          userId: this.user?.userId,
          longitude,
          latitude,
        })
        .then((res) => {
          console.log("", res);
          // if (res.code === 200) {
          //   this.$Toast("");
          // } else {
          //   this.$Toast("" + (res.msg || ""));
          // }
        })
        .catch((error) => {
          console.error("", error);
          this.$Toast("");
        });
    },
    updateWindowHeight() {
      this.windowHeight = document.body.clientHeight;
    },

    getPreciseLocation() {
      const options = { timeout: 8000 };
      this.geolocation.getLocation(
        (position) => {
          console.log(":", {
            : position.lat,
            : position.lng,
            : position.accuracy,
            : position.addr || "",
          });
          this.newLocation = position.addr;
          this.latitude = position.lat;
          this.longitude = position.lng;
          this.showPosition(position);
          
          // 
          if (position.lat && position.lng && !this.locationSent) {
            console.log("");
            this.locationSent = true;
            this.updateLocation(position.lng, position.lat);
          }
        },
        (error) => {
          console.error(":", error);
          this.showErr(error);
        },
        options
      );
    },

    getIpLocation() {
      this.geolocation.getIpLocation(this.showPosition, this.showErr);
    },

    startWatchPosition() {
      if (this.watchId) {
        console.log("", this.watchId);
        this.geolocation.clearWatch();
      }

      this.positionNum++;
      this.positionLogs.push({
        id: this.positionNum,
        type: "start_watch",
      });
      console.log("", this.positionLogs);

      this.watchId = this.geolocation.watchPosition(this.showPosition);
    },
    stopWatchPosition() {
      if (this.watchId) {
        this.geolocation.clearWatch();
        this.watchId = null;

        this.positionNum++;
        this.positionLogs.push({
          id: this.positionNum,
          type: "stop_watch",
        });
      }
    },

    showPosition(position) {
      this.positionNum++;
      this.positionLogs.push({
        id: this.positionNum,
        type: "position",
        data: position,
      });
    },

    showErr() {
      this.positionNum++;
      this.positionLogs.push({
        id: this.positionNum,
        type: "error",
      });
    },

    connectSSE() {
      const clientId = this.user.userId;

      this.clientId = sseService.connect(clientId);
      console.log("this.clientId:", this.clientId);
      this.isConnected = true;
      this.connectionStatus = "...";

      sseService.onMessage((data) => {
        this.handleMessage(data);
      });

      sseService.onError((error) => {
        this.error = error;
        this.connectionStatus = ": " + error.message;
        this.isConnected = false;
        this.addMessage("", ": " + error.message, "error");
      });
    },

    disconnectSSE() {
      sseService.disconnect();
      this.isConnected = false;
      this.connectionStatus = "";
      this.addMessage("", "", "system");
    },

    sendTestMessage() {
      if (!this.clientId) return;

      this.$request
        .get("/user/sse/sendTestMessage", {
          params: {
            clientId: this.user.userId,
          },
        })
        .then((response) => {
          this.addMessage("", response.data, "system");
          this.getCurrentPositionAndSend();
        })
        .catch((error) => {
          this.addMessage(
            "",
            ": " + (error.message || ""),
            "error"
          );
        });
    },

    handleMessage(data) {
      console.log("handleMessage=====data====", data);
      if (data === "CONNECTED") {
        this.connectionStatus = "";
        this.addMessage("", "SSE", "system");
        this.isConnected = true;
      } else {
        // JSON
        try {
          const message = JSON.parse(data);
          if (message.type === "location_update") {
            // 
            this.addMessage("", "", "test");
            this.getCurrentPositionAndSend();
            return;
          }
        } catch (e) {
          // JSON
          console.log("JSON");
        }
        
        // 
        if (data == this.user.userId) {
          this.addMessage("", "", "test");
          this.getCurrentPositionAndSend();
        } else {
          this.addMessage("", "" + data, "server");
        }
      }
    },

    getCurrentPositionAndSend() {
      if (this.geolocation) {
        const options = { timeout: 8000 };
        this.geolocation.getLocation(
          (position) => {
            console.log(":", position);
            this.newLocation = position.addr;
            this.updateLocation(position.lng, position.lat);
          },
          (error) => {
            console.error(":", error);
            this.$Toast("");
          },
          options
        );
      }
    },
    addMessage(source, content, type) {
      this.messages.unshift({
        time: new Date().toLocaleTimeString(),
        source,
        content,
        type,
      });

      if (this.messages.length > 50) {
        this.messages.pop();
      }
    },
    formatTimestamp(timestamp) {
      if (!/^\d{14}$/.test(timestamp)) {
        console.warn("14");
        return timestamp;
      }

      const month = timestamp.substring(4, 6);
      const day = timestamp.substring(6, 8);
      const hour = timestamp.substring(8, 10);
      const minute = timestamp.substring(10, 12);

      const formattedMonth = parseInt(month, 10);
      const formattedDay = parseInt(day, 10);
      const formattedHour = parseInt(hour, 10);
      const formattedMinute = parseInt(minute, 10);

      return `${formattedMonth}${formattedDay}${formattedHour}:${formattedMinute
        .toString()
        .padStart(2, "0")}`;
    },
    getData() {
      if (this.user?.userId) {
        this.$request
          .get("/goodsdingdan/findByPeisongyuanAndStatus", {
            params: {
              peisongyuanuserid: this.user?.userId,
              peisongstatus: "",
            },
          })
          .then((res) => {
            console.log("", res);
            if (res.code == 200) {
              this.items = res.data;
            }
            // 
            if (this.latitude && this.longitude && !this.locationSent) {
              console.log("");
              this.locationSent = true;
              this.updateLocation(this.longitude, this.latitude);
            }
          });
      }
    },
    report(id, operate, outtradeno) {
      console.log("report===id===", id);
      let path;
      switch (operate) {
        case "xiangqing":
          path = "/peisongdetail";
          break;
        case "baobei":
          path = "/abnormalreport";
          break;
        case "weizhi":
          path = "/peisonglocation";
          break;
        case "tuikuan":
          path = "/refund";
          break;
      }
      this.$router.push({ path, query: { id, outtradeno } });
    },
    // 
    confirmCancelOrder(item) {
      console.log("confirmCancelOrder ", item);
      const orderNumber = item.goodsdingdannumber || "";
      
      if (!orderNumber || orderNumber.length < 5) {
        this.$toast("");
        return;
      }

      // 
      this.currentCancelOrder = item;
      this.cancelOrderInput = "";
      this.cancelOrderError = "";
      this.showCancelOrderDialog = true;
      console.log("showCancelOrderDialog :", this.showCancelOrderDialog);
    },
    
    // 
    handleCancelOrderInput(value) {
      // 5
      const numbersOnly = value.replace(/\D/g, "");
      this.cancelOrderInput = numbersOnly.slice(0, 5); // 5
      // 
      this.cancelOrderError = "";
    },
    
    // 
    handleCancelOrderConfirm() {
      if (!this.currentCancelOrder) {
        return;
      }
      
      const orderNumber = this.currentCancelOrder.goodsdingdannumber || "";
      const lastFiveDigits = orderNumber.slice(-5);
      const userInput = this.cancelOrderInput.trim();
      
      // 
      this.cancelOrderError = "";
      
      if (!userInput) {
        this.cancelOrderError = "";
        this.$toast("");
        return;
      }
      
      // 5
      if (!/^\d{5}$/.test(userInput)) {
        this.cancelOrderError = "5";
        this.$toast("5");
        return;
      }
      
      // 
      if (userInput !== lastFiveDigits) {
        this.cancelOrderError = "";
        this.$toast("");
        // 
        this.cancelOrderInput = "";
        return;
      }
      
      // 
      console.log("");
      if (confirm(`\n${orderNumber}`)) {
        // 
        console.log("");
        this.cancelOrderWithVerification(this.currentCancelOrder);
      } else {
        // 
        console.log("");
      }
    },
    
    // 
    cancelOrderWithVerification(item) {
      this.$request
        .post("/goodsdingdan/guanliyuanquxiaogoodsdingdan", {
          id: item.id,
          dingdanquxiaotibaouserid: this.user.userId,
        })
        .then((res) => {
          console.log("======", res);
          if (res.code == 200) {
            // 
            this.$toast(res.msg || "");
            // 
            this.currentCancelOrder = null;
            this.cancelOrderInput = "";
            this.cancelOrderError = "";
            // 
            this.showCancelOrderDialog = false;
            // 
            this.getData();
            //  setTimeout 
            setTimeout(() => {
              if (this.showCancelOrderDialog) {
                this.showCancelOrderDialog = false;
              }
            }, 100);
          } else {
            this.$toast(res.msg || "");
          }
        })
        .catch((error) => {
          console.error("", error);
          this.$toast("");
        });
    },
    
    cancleOrder(id) {
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          this.$request
            .post("/goodsdingdan/partialUpdate", {
              id,
              dingdanquxiaotibaouserid: this.user.userId,
              goodsdingdanstatus: "",
              peisongstatus: "",
            })
            .then((res) => {
              console.log("======", res);
              if (res.code == "200") {
                this.$toast("");
                this.getData();
              }
            });
        })
        .catch(() => {
          console.log("cancel===id===", id);
        });
    },
    // 
    confirmDeliver(item) {
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          this.deliverOrder(item);
        })
        .catch(() => {
          console.log("");
        });
    },
    // 
    async deliverOrder(item) {
      try {
        const res = await this.$request.post("/goodsdingdan/partialUpdate", {
          id: item.id,
          peisongyuanuserid: this.user.userId,
          activestatus: "finish",
          peisongstatus: "",
        });

        console.log("======", res);

        if (res.code == 200) {
          this.$toast("");
          this.getData();
        } else {
          this.$toast("");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      }
    },
    async copyToClipboard(item) {
      const address = item.goodsuseridaddressaddress;
      if (navigator.clipboard) {
        try {
          await navigator.clipboard.writeText(address);
          this.showSuccess(address);
          return;
        } catch (err) {
          console.warn("", err);
        }
      }
      this.copyFallback(address);
    },
    copyFallback(address) {
      const input = document.createElement("input");
      input.value = address;
      document.body.appendChild(input);
      input.select();
      document.execCommand("copy");
      document.body.removeChild(input);
      this.showSuccess(address);
    },
    showSuccess(address) {
      // 
      const toast = document.createElement('div');
      toast.style.cssText = `
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: rgba(0, 0, 0, 0.8);
        color: white;
        padding: 12px 20px;
        border-radius: 6px;
        z-index: 9999;
        font-size: 14px;
        text-align: center;
        max-width: 280px;
        word-break: break-word;
        line-height: 1.3;
        animation: fadeInOut 2s ease-in-out forwards;
      `;
      
      // 
      const style = document.createElement('style');
      style.textContent = `
        @keyframes fadeInOut {
          0% { opacity: 0; transform: translate(-50%, -50%) translateY(10px); }
          20% { opacity: 1; transform: translate(-50%, -50%) translateY(0); }
          80% { opacity: 1; transform: translate(-50%, -50%) translateY(0); }
          100% { opacity: 0; transform: translate(-50%, -50%) translateY(-10px); }
        }
      `;
      document.head.appendChild(style);
      
      toast.innerHTML = `${address}`;
      
      document.body.appendChild(toast);
      
      // 2
      setTimeout(() => {
        if (toast.parentNode) {
          toast.parentNode.removeChild(toast);
        }
        if (style.parentNode) {
          style.parentNode.removeChild(style);
        }
      }, 2000);
    },
    copyAndCall(phone) {
      this.copyTelephone(phone);
      window.location.href = `tel:${phone}`;
    },
    copyTelephone(text) {
      const input = document.createElement("input");
      input.value = text;
      document.body.appendChild(input);
      input.select();
      document.execCommand("copy");
      document.body.removeChild(input);
    },
    openMap(item) {
      const { address, latitude, longitude } = item.goodsuseridaddress;
      const universalUrl = `https://uri.amap.com/navigation?to=${longitude},${latitude},${encodeURIComponent(
        address
      )}&mode=car&src=yourAppName`;

      window.location.href = universalUrl;
    },
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.updateWindowHeight);

    if (this.watchId) {
      this.geolocation.clearWatch();
    }
  },
};
</script>

<style scoped>
.top {
  margin: 0px;
  border-radius: 10px;
  padding: 35px 5px; /* 5px */
  box-sizing: border-box;
  display: unset;
  width: 100%;
  background-color: #f5f7fa;
