<template>
  <div class="global-container">
    <div class="top">
      <div
        style="
          margin-bottom: 30px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
        "
      >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="$goMenu"
          ></van-button
        >
        <van-button
          type="primary"
          color="#fa6286"
          style="
            border: 2px solid #fa6286;
            border-radius: 10px;
            font-weight: bold;
          "
          @click="showGenerateDialog"
          ></van-button
        >
      </div>

      <div
        style="
          display: flex;
          padding: 20px 0px;
          border-radius: 15px 15px 0 0;
          margin-bottom: 30px;
        "
      >
        <img :src="logo2" style="width: 40px; height: 40px" />
        <div class="text" style="color: #999">
          
          <div style="color: #000">-</div>
        </div>
      </div>

      <!--  -->
      <div v-if="qrCodeData" style="text-align: center; padding: 20px;">
        <h3 style="margin-bottom: 20px; color: #333;"></h3>
        <img
          :src="'data:image/png;base64,' + qrCodeData"
          style="max-width: 200px; max-height: 200px; border: 1px solid #ddd; border-radius: 8px;"
          alt=""
        />
      </div>

      <!--  -->
      <div style="padding: 20px 0;">
        <h3 style="margin-bottom: 20px; color: #333; text-align: center;"></h3>

        <!--  -->
        <div class="loading-state" v-if="loadingUsers">
          <van-loading size="24px">...</van-loading>
        </div>

        <!--  -->
        <div v-else-if="userList.length > 0" style="padding: 0 10px;">
          <van-cell-group>
            <van-cell
              v-for="user in userList"
              :key="user.userId"
              style="display: flex; align-items: center; padding: 15px;"
              @click="goToUserRecord(user)"
              is-link
            >
              <template #title>
                <div style="display: flex; align-items: center; width: 100%;">
                  <!-- ID -->
                  <div style="flex: 1; font-weight: bold; color: #333;">
                    ID: {{ user.userId }}
                    <div v-if="user.username" style="font-size: 12px; color: #999; font-weight: normal; margin-top: 4px;">
                      {{ user.username }}
                    </div>
                  </div>

                  <!--  -->
                  <div style="flex: 1; text-align: right;">
                    <img
                      v-if="user.unlimitedqrcode"
                      :src="'data:image/png;base64,' + user.unlimitedqrcode"
                      style="width: 80px; height: 80px; object-fit: cover; border: 1px solid #ddd; border-radius: 4px; cursor: pointer;"
                      alt=""
                      @click="previewQRCode(user.unlimitedqrcode)"
                    />
                  </div>
                </div>
              </template>
            </van-cell>
          </van-cell-group>
        </div>

        <!--  -->
        <div class="loading-state" v-else>
          <span></span>
        </div>
      </div>
    </div>

    <!-- sceneStr -->
    <van-dialog
      v-model="showDialog"
      title=""
      show-cancel-button
      confirm-button-text=""
      cancel-button-text=""
      @confirm="generateQRCode"
      @cancel="cancelDialog"
    >
      <van-field
        v-model="sceneStr"
        placeholder="sceneStr"
        label=""
        :rules="[{ required: true, message: '' }]"
      />
    </van-dialog>

    <!--  -->
    <div v-if="showPreview" class="qr-preview-overlay" @click="closePreview">
      <div class="qr-preview-modal" @click.stop>
        <div class="qr-preview-header">
          <h3></h3>
          <button class="close-btn" @click="closePreview"></button>
        </div>

        <!--  -->
        <div class="qr-preview-content">
          <img
            v-if="previewQRCodeData"
            :src="'data:image/png;base64,' + previewQRCodeData"
            alt=""
          />
        </div>

        <!--  -->
        <div class="qr-preview-footer">
          <button class="save-btn" @click="saveToAlbum"></button>
          <button class="cancel-btn" @click="closePreview"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tuiguangcode",
  data() {
    return {
      logo2: require("../assets/shouye_title.png"),
      sceneStr: "",
      showDialog: false,
      qrCodeData: null,
      loading: false,
      userList: [],
      loadingUsers: false,
      showPreview: false,
      previewQRCodeData: null,
    };
  },
  methods: {
    goMenu() {
      this.$router.push("/center");
    },

    // 
    goToUserRecord(user) {
      this.$router.push({
        path: '/tuiguangcodejilu',
        query: {
          userId: user.userId
        }
      });
    },

    // 
    previewQRCode(qrCodeData) {
      this.previewQRCodeData = qrCodeData;
      this.showPreview = true;
    },

    // 
    closePreview() {
      this.showPreview = false;
      this.previewQRCodeData = null;
    },

    // 
    async saveToAlbum() {
      if (!this.previewQRCodeData) {
        this.$toast("");
        return;
      }

      try {
        //  a 
        const link = document.createElement('a');
        link.href = 'data:image/png;base64,' + this.previewQRCodeData;
        link.download = '_' + new Date().getTime() + '.png';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);

        this.$toast("");
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      }
    },

    // 
    showGenerateDialog() {
      this.sceneStr = "";
      this.showDialog = true;
    },

    // 
    cancelDialog() {
      this.showDialog = false;
      this.sceneStr = "";
    },

    // 
    async getUserList() {
      this.loadingUsers = true;
      try {
        const res = await this.$request.get("/user/findAllunlimitedqrcode");

        if (res?.code == 200) {
          this.userList = res.data || [];
        } else {
          this.$toast(res?.msg || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      } finally {
        this.loadingUsers = false;
      }
    },

    // 
    async generateQRCode() {
      if (!this.sceneStr.trim()) {
        this.$toast("");
        return;
      }

      this.loading = true;
      this.showDialog = false;

      try {
        // 
        const user = JSON.parse(localStorage.getItem("bmk_user") || "{}");

        const res = await this.$request.get("/user/wangyeceratUnlimitedQRCode", {
          params: {
            sceneStr: this.sceneStr.trim(),
            page: "pages/shopping/index", // 
            userId: user.userId
          }
        });

        if (res?.code == 200) {
          if (res.data && res.data.unlimitedqrcode) {
            // base64
            this.qrCodeData = res.data.unlimitedqrcode;
          } else if (res.data) {
            // unlimitedqrcode
            this.qrCodeData = res.data.unlimitedqrcode;
          }
          this.$toast("");
          // 
          this.getUserList();
        } else {
          this.$toast(res?.msg || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$toast("");
      } finally {
        this.loading = false;
      }
    },
  },

  mounted() {
    // 
    let user = localStorage.getItem("bmk_user");
    console.log("user", user);
    if (user) {
      this.user = JSON.parse(user);
    }
    // 
    this.getUserList();
  },
};
</script>

