<template>
  <div class="global-container">
    <div class="top">
      <div class="header-container">
        <van-button
          type="primary"
          color="#fa6286"
          class="back-button"
          @click="$goMenu"
        ></van-button>
        <van-button
          type="primary"
          color="#fa6286"
          class="batch-button"
          @click="batchSet"
          :disabled="selectedOrders.length === 0"
        >ID ({{ selectedOrders.length }})</van-button>
      </div>
      <!-- ID -->
      <div v-if="showBatchDialog" class="batch-set-section">
        <div class="section-header">
          <h3 class="section-title">ID</h3>
          <p class="section-subtitle"> {{ selectedOrders.length }} </p>
        </div>
        
        <div class="section-content">
          <div class="input-group">
            <label class="input-label">ID</label>
            <input 
            v-model="batchUserId"
              type="number"
              class="modern-input"
              placeholder="ID"
            />
          </div>
        </div>
        
        <div class="section-actions">
           <button class="cancel-btn" @click="showBatchDialog = false">
             
           </button>
          <button class="confirm-btn" @click="confirmBatchSet">
            
          </button>
        </div>
      </div>
      <div class="flex-col page">
        <!--  -->
        <div v-if="unassignedOrders.length > 0" class="unassigned-orders">
          <div class="orders-header">
            <div class="header-content">
              <h3 class="orders-title"></h3>
              <p class="orders-subtitle"> {{ selectedOrders.length }} / {{ unassignedOrders.length }} </p>
            </div>
            <div class="select-all-container">
              <label class="select-all-label">
                <input 
                  type="checkbox" 
                  class="select-all-checkbox"
                  :checked="isAllSelected"
                  @change="toggleSelectAll"
                />
                <span class="custom-checkbox select-all-checkbox-custom"></span>
                <span class="select-all-text"></span>
              </label>
            </div>
          </div>
          
          <div class="orders-list">
            <div 
              v-for="(order, index) in unassignedOrders" 
              :key="order.goodsdingdannumber || index"
              class="order-item"
              :class="{ 'order-item-selected': isOrderSelected(order.goodsdingdannumber) }"
              @click="toggleOrderSelection(order.goodsdingdannumber)"
            >
              <div class="order-checkbox-container">
                <label class="custom-checkbox-label">
                  <input 
                    type="checkbox" 
                    class="order-checkbox"
                    :checked="isOrderSelected(order.goodsdingdannumber)"
                    @change.stop="toggleOrderSelection(order.goodsdingdannumber)"
                  />
                  <span class="custom-checkbox"></span>
                </label>
              </div>
              <div class="order-info">
                <div class="order-info-row">
                  <span class="label">:</span>
                  <span class="value order-number-text">{{ order.goodsdingdannumber }}</span>
                </div>
                <div class="order-info-row">
                  <span class="label">:</span>
                  <span class="value">{{ order.goodsuseridaddressaddress || '' }}</span>
                </div>
                <div class="order-info-row">
                  <span class="label">:</span>
                  <span class="value">{{ order.goodsuseridaddressphone || '' }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div v-else-if="unassignedCount === 0" class="no-orders">
          <div class="no-orders-content">
            <p> </p>
            <p class="no-orders-subtitle"></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  components: {},
  props: {},
  data() {
    return {
      showBatchDialog: false,
      batchUserId: "",
      items: [],
      user: {},
      unassignedCount: 0,
      unassignedOrders: [], // 
      selectedOrders: [], // 
    };
  },
  mounted() {
    let user = localStorage.getItem("bmk_user");
    this.user = JSON.parse(user);
    console.log("user", this.user);
    if (this.user.userId) {
      this.getData();
      this.getUnassignedCount();
    } else {
      Toast("");
    }
  },
  computed: {
    // 
    isAllSelected() {
      return this.unassignedOrders.length > 0 && 
             this.selectedOrders.length === this.unassignedOrders.length;
    },
  },
  methods: {
    batchSet() {
      if (this.selectedOrders.length === 0) {
        Toast("");
        return;
      }
      this.showBatchDialog = true;
    },
    // 
    toggleOrderSelection(orderNumber) {
      const index = this.selectedOrders.indexOf(orderNumber);
      if (index > -1) {
        this.selectedOrders.splice(index, 1);
      } else {
        this.selectedOrders.push(orderNumber);
      }
    },
    // 
    isOrderSelected(orderNumber) {
      return this.selectedOrders.includes(orderNumber);
    },
    // /
    toggleSelectAll(event) {
      if (event.target.checked) {
        // 
        this.selectedOrders = this.unassignedOrders.map(order => order.goodsdingdannumber);
      } else {
        // 
        this.selectedOrders = [];
      }
    },
    async confirmBatchSet() {
      if (!this.batchUserId.trim()) {
        Toast("ID");
        return;
      }
      
      if (this.selectedOrders.length === 0) {
        Toast("");
        return;
      }
      
      try {
        // 
        const list = this.selectedOrders.map(orderNumber => ({
          goodsdingdannumber: orderNumber,
          fenjianyuanuserid: Number(this.batchUserId)
        }));
        
        console.log(":", list);
        
        // 
        const res = await this.$request.post("/goodsdingdan/piliangupdateByfenjianyuanuserid", list);
        
        console.log("", res);
        if (res.code == 200) {
          Toast(res.data || ` ${this.selectedOrders.length} `);
          this.getData(); // 
          this.getUnassignedCount(); // 
          this.selectedOrders = []; // 
        } else {
          Toast("");
        }
      } catch (error) {
        console.error(":", error);
        Toast("");
      } finally {
        this.showBatchDialog = false;
        this.batchUserId = "";
      }
    },
    getData() {
      if (this.user?.userId) {
        // 
        this.$request
          .get("/goodsdingdan/findByPeisongyuanAndStatus", {
            params: {
              peisongyuanuserid: this.user?.userId,
              peisongstatus: "",
            },
          })
          .then((res) => {
            console.log("", res);
            if (res.code == 200) {
              this.items = res.data;
            }
          });
      }
    },
    // 
    getUnassignedOrders() {
      return this.$request
        .get("/goodsdingdan/findByFenjianyuanuseridIsNull")
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            return res.data;
          }
          return [];
        })
        .catch((error) => {
          console.error(":", error);
          return [];
        });
    },
    // 
    getUnassignedCount() {
      this.$request
        .get("/goodsdingdan/findByFenjianyuanuseridIsNull")
        .then((res) => {
          console.log("", res);
          if (res.code == 200) {
            this.unassignedOrders = res.data || [];
            this.unassignedCount = this.unassignedOrders.length;
            // 
            this.selectedOrders = [];
          }
        })
        .catch((error) => {
          console.error(":", error);
          this.unassignedOrders = [];
          this.unassignedCount = 0;
        });
    },
  },
};
</script>

