/**
 * 
 * ESC/POS
 */

// ESC/POS 
const ESC = '\x1B'; // ESC
const GS = '\x1D';  // GS
const INIT = ESC + '@'; // 
const CUT = GS + 'V' + '\x01'; // 
const LINE_FEED = '\x0A'; // 
const ALIGN_LEFT = ESC + 'a' + '\x00'; // 
const ALIGN_CENTER = ESC + 'a' + '\x01'; // 
const ALIGN_RIGHT = ESC + 'a' + '\x02'; // 
const BOLD_ON = ESC + 'E' + '\x01'; // 
const BOLD_OFF = ESC + 'E' + '\x00'; // 
const FONT_DEFAULT = ESC + '!' + '\x00'; // 
const FONT_LARGE = ESC + '!' + '\x11'; // 
const FONT_SMALL = ESC + '!' + '\x01'; // 
const BARCODE = GS + 'k'; // 
const QR_CODE = GS + '(k'; // 

class Printer
{
  constructor()
  {
    this.buffer = '';
    this.encoder = new TextEncoder();
  }

  // 
  init ()
  {
    this.buffer += INIT;
    // ESC t 00/
    this.buffer += '\x1B\x74\x00';
    // ESC R 0
    this.buffer += '\x1B\x52\x00';
    // ESC R 15
    this.buffer += '\x1B\x52\x0F';
    return this;
  }

  // 
  text (content)
  {
    this.buffer += content;
    return this;
  }

  // 
  newLine (lines = 1)
  {
    for (let i = 0;i < lines;i++) {
      this.buffer += LINE_FEED;
    }
    return this;
  }

  // 
  align (type)
  {
    switch (type) {
      case 'left':
        this.buffer += ALIGN_LEFT;
        break;
      case 'center':
        this.buffer += ALIGN_CENTER;
        break;
      case 'right':
        this.buffer += ALIGN_RIGHT;
        break;
      default:
        this.buffer += ALIGN_LEFT;
    }
    return this;
  }

  // 
  setFont (type)
  {
    switch (type) {
      case 'default':
        this.buffer += FONT_DEFAULT;
        break;
      case 'large':
        this.buffer += FONT_LARGE;
        break;
      case 'small':
        this.buffer += FONT_SMALL;
        break;
      default:
        this.buffer += FONT_DEFAULT;
    }
    return this;
  }

  // 
  bold (on = true)
  {
    this.buffer += on ? BOLD_ON : BOLD_OFF;
    return this;
  }

  // 
  divider (char = '-', length = 32)
  {
    this.buffer += char.repeat(length);
    return this;
  }

  // 
  sparseDivider (char = '-', length = 32)
  {
    this.buffer += (char + ' ').repeat(length / 2).slice(0, length);
    return this;
  }

  // 
  barcode (content, type = 73, width = 2, height = 50)
  {
    //  (73 = CODE128)
    this.buffer += GS + 'w' + String.fromCharCode(width); // 
    this.buffer += GS + 'h' + String.fromCharCode(height); // 
    this.buffer += BARCODE + String.fromCharCode(type) + content + '\x00';
    return this;
  }

  // 
  // qrcode (content, size = 6)
  // {
  //   // 
  //   const model = 2; // QR
  //   const len = content.length + 3;
  //   const pL = len % 256;
  //   const pH = Math.floor(len / 256);

  //   // 
  //   this.buffer += QR_CODE + '\x04\x00' + '1A' + String.fromCharCode(model);

  //   // 
  //   this.buffer += QR_CODE + '\x03\x00' + '1C' + String.fromCharCode(size);

  //   // 
  //   this.buffer += QR_CODE + String.fromCharCode(pL) + String.fromCharCode(pH) + '1P0' + content;

  //   // 
  //   this.buffer += QR_CODE + '\x03\x00' + '1Q0';

  //   return this;
  // }

  qrcode (content, size = 10) {
  console.log('QR :', content.length, 'size:', size);
  const model = 2;
  const len = content.length + 3;
  const pL = len % 256;
  const pH = Math.floor(len / 256);

  // 
  this.buffer += QR_CODE + '\x04\x00' + '1A' + String.fromCharCode(model);
  // 137x37245x4558mm
  this.buffer += QR_CODE + '\x03\x00' + '31' + '\x01';  // '31'p=3,q=1n=1 (Version 1)
  //  L (7%)
  this.buffer += QR_CODE + '\x03\x00' + '1E' + '\x30';  // n=0 for L
  // 
  this.buffer += QR_CODE + '\x03\x00' + '1C' + String.fromCharCode(size);
  // 
  this.buffer += QR_CODE + String.fromCharCode(pL) + String.fromCharCode(pH) + '1P0' + content;
  // 
  this.buffer += QR_CODE + '\x03\x00' + '1Q0';
  return this;
}

  // 
  cut ()
  {
    this.buffer += CUT;
    return this;
  }

  // encoding.jsGBKNodeiconv-litefallbackUTF-8
  getBuffer ()
  {
    // 
    if (typeof window !== 'undefined' && window.encoding) {
      const gbkArr = window.encoding.convert(this.buffer, { from: 'UNICODE', to: 'GBK', type: 'array' });
      return new Uint8Array(gbkArr);
    }
    // Node.js fallback
    try {
      const iconv = require('iconv-lite');
      const gbkBuffer = iconv.encode(this.buffer, 'gbk');
      return new Uint8Array(gbkBuffer);
    } catch (e) {
      // iconv-litefallbackUTF-8
    }
    // fallbackUTF-8
    return this.encoder.encode(this.buffer);
  }

  // 
  clear ()
  {
    this.buffer = '';
    return this;
  }
  _getStrLen (str)
  {
    // 21
    let len = 0;
    for (let i = 0;i < str.length;i++) {
      len += str.charCodeAt(i) > 127 ? 2 : 1;
    }
    return len;
  }
  // 
  _padRight (str, len)
  {
    str = str || '';
    const raw = this._getStrLen(str);
    return str + ' '.repeat(Math.max(0, len - raw));
  }
  _padLeft (str, len)
  {
    str = str || '';
    const raw = this._getStrLen(str);
    return ' '.repeat(Math.max(0, len - raw)) + str;
  }



  // 
  createAlignedLine (left, right, totalWidth = 32, leftWidth = 20)
  {
    const getVisualLength = (str) =>
    {
      return str.replace(/[\u4e00-\u9fa5]/g, 'xx').length;
    };

    // 
    let leftPart = this._truncateToVisualLen(left || '', leftWidth);
    // leftWidth
    while (getVisualLength(leftPart) < leftWidth) {
      leftPart += ' ';
    }

    // 
    const rightStartPos = leftWidth;
    const rightAvailableWidth = totalWidth - rightStartPos;
    let rightPart = right || '';
    const rightLen = getVisualLength(rightPart);
    const padRight = ' '.repeat(Math.max(0, rightAvailableWidth - rightLen));
    rightPart = padRight + rightPart;

    // 
    const line = leftPart + rightPart;
    this.text(line);
    return this;
  }

  // 
  _truncateToVisualLen (str, maxLen)
  {
    let len = 0;
    let result = '';
    for (let i = 0; i < str.length; i++) {
      const charLen = str.charCodeAt(i) > 127 ? 2 : 1;
      if (len + charLen > maxLen) {
        break;
      }
      result += str[i];
      len += charLen;
    }
    return result;
  }

  // 
  printItem (itemName, qty, totalWidth = 32) {
    const qtyStr = `*${qty}`;
    const qtyLen = this._getStrLen(qtyStr);
    const availableLenPerLine = totalWidth - qtyLen; // 

    //  availableLenPerLine
    const lines = [];
    let currentLine = '';
    let currentLen = 0;

    for (let i = 0; i < itemName.length; i++) {
      const char = itemName[i];
      const charLen = char.charCodeAt(0) > 127 ? 2 : 1;

      if (currentLen + charLen > availableLenPerLine && currentLine !== '') {
        lines.push(currentLine);
        currentLine = char;
        currentLen = charLen;
      } else {
        currentLine += char;
        currentLen += charLen;
      }
    }
    if (currentLine !== '') {
      lines.push(currentLine);
    }

    // 
    lines.forEach((line, index) => {
      if (index === lines.length - 1) {
        //  +  + 
        const lineLen = this._getStrLen(line);
        const padding = ' '.repeat(totalWidth - lineLen - qtyLen);
        this.text(line + padding + qtyStr);
      } else {
        //  + 
        const lineLen = this._getStrLen(line);
        this.text(line + ' '.repeat(totalWidth - lineLen));
      }
      this.newLine();
    });

    return this;
  }

  // 
  printReceipt (data)
  {
    try {
      this.clear();  // 

      let orders = [];
      if (data && data.data && Array.isArray(data.data)) {
        orders = data.data;
      } else if (Array.isArray(data)) {
        orders = data;
      } else {
        orders = [data];
      }

      orders.forEach((order) => {
        const yuji = (order.yujisongdadata || '').replace('undefined', '').trim() || '';  // undefined
        const addr = order.goodsuseridaddressaddress || '';
        const dingwei = order.goodsuseridaddressdingweidizhi || '';
        const name = order.goodsuseridaddressname || '';
        const phone = order.goodsuseridaddressphone || '';
        const comment = order.comment || '';

        this.init()
          .align('center')
          .setFont('default')
          .bold(true)
          .text(order.title || '')
          .newLine()
          .bold(false)
          .setFont('default')
          .text(order.subtitle || '')
          .newLine(2)
          .align('left')
          .text(': ' + (order.date || new Date().toLocaleString()))
          .newLine()
          .text(': ' + (order.goodsdingdannumber || ''))
          .newLine()
          .text(': ' + (order.leixing || ''))
          .newLine()
          .divider()
          .newLine()
          .align('left')
          .text(': ' + yuji)
          .newLine()
          .text(': ' + comment)
          .newLine()
          .divider()
          .newLine()
          .text(addr)
          .newLine()
          .text(dingwei)
          .newLine()
          .text(name + ' ' + phone)
          .newLine()
          .divider()
          .newLine()

        // createAlignedLineprintItem
        if (order.goodsdingdanitem && order.goodsdingdanitem.length > 0) {
          // createAlignedLine
          this.align('left')
            .createAlignedLine('', '', 32, 20)
            .newLine()
            .divider()
            .newLine();

          // 
          order.goodsdingdanitem.forEach(item => {
            const itemName = item.goodsgoodsididname || item.name || '';  // 
            const qty = item.goodsgoodsididnumber || item.quantity || 1;
            this.align('left')
              .printItem(itemName, qty, 32)
              .sparseDivider()
              .newLine();
          });
        }

        this.newLine()
          .align('right')
          .text(':')
          .setFont('large')
          .text((order.shijiprice || '0.00').toFixed(2))
          .newLine(2)
          .setFont('default');

        //  - 
        if (order.goodsdingdannumber) {
          this.align('center')
            .qrcode(order.goodsdingdannumber)
            .newLine(2);
        }

        // 
        this.align('center')
          .setFont('default')
          .bold(false)
          .text('15625002620')
          .newLine()
          .align('center')
          .text('')
          .newLine(3)
          .cut();
      });

      // 
      this.init();

      return this.getBuffer();

    } catch (error) {
      console.error(':', error);
      return null;  // null
    }
  }
}

export default new Printer();