  methods: {
    setSamePrice(e) {
      this.houlaiprice = e;
      this.souchuprice = e;
    },
    goScanGoods() {
      this.$router.push({
        path: "/scangoods",
        query: { sid: this.$route.query.id },
      });
    },
    // 
    calculateImageSize() {
      const container = document.querySelector(".images-container");
      if (!container) return;

      const containerWidth = container.offsetWidth;
      const previewSize = (containerWidth - 40) / 3; // 

      // const imageItems = document.querySelectorAll(".image-item");
      // imageItems.forEach((item) => {
      //   item.style.width = `${previewSize}px`;
      //   item.style.height = `${previewSize}px`;
      // });
      this.previewSize = previewSize;
      console.log("calculateImageSize", previewSize);
    },
    onChange(event) {
      const { picker, value, index } = event.detail;
      console.log(`${(picker, value)}, ${index}`);
    },
    onConfirmGuanggao(value) {
      console.log("value", value);
      // this.guanggaoid = value.goodsshouyeguanggaoid;
      this.guanggaoid = value.id;
      this.guanggao = value.guanggaoname;
      console.log("", this.guanggao, this.guanggaoid);
      this.showPickerGuanggao = false;
      // this.setGuanggaoGoods(value);
    },
    setGuanggaoGoods() {
      this.$request
        .post(`goodsshouyeguanggaogoodsgoodsidid`, {
          goodsshouyeguanggaoid: this.guanggaoid,
          goodsgoodsidid: this.goodsgoodsidid,
          goodsgoodsididpaixuhao: 1,
        })
        .then((res) => {
          if (res) {
            this.$Toast("");
          } else {
            this.$Toast("");
          }
        });
    },
    // 
    triggerReplaceOne(index) {
      const input = this.$refs['replaceInputOne' + index];
      if (input) {
        input.click();
      }
    },
    // photosurlwei
    generatePhotosurlwei() {
      const now = new Date();
      const formatNumber = (n) => {
        n = n.toString();
        return n[1] ? n : `0${n}`;
      };
      const year = now.getFullYear();
      const month = now.getMonth() + 1;
      const day = now.getDate();
      const hour = now.getHours();
      const minute = now.getMinutes();
      const second = now.getSeconds();
      return `goodsphotosUrl${year}${formatNumber(month)}${formatNumber(day)}${formatNumber(hour)}${formatNumber(minute)}${formatNumber(second)}`;
    },
    // 
    async handleReplaceOne(event, index) {
      const file = event.target.files[0];
      if (!file) return;
      
      const oldItem = this.fileList[index];
      const photosurl = oldItem.photosUrl || oldItem.url;
      
      if (!photosurl) {
        this.$Toast("");
        return;
      }
      
      this.uploading = true;
      this.uploadProgress = { current: 0, total: 1, percentage: 0 };
      
      try {
        const photosurlwei = this.generatePhotosurlwei() + `-${index + 1}`;
        const formData = new FormData();
        formData.append("file", file);
        formData.append("photosurl", photosurl);
        formData.append("photosurlwei", photosurlwei);
        formData.append("goodsgoodsidid", this.goodsgoodsidid);
        formData.append("userId", this.user.userId);
        
        const res = await this.$request.post("/goodsfiles/upload/updategoodslunbotu", formData);
        
