    async afterReadOne(e) {
      await this.$nextTick();
      this.requestUploadMode('lunbo', e);
    },
    async processAfterReadOne(e, useWebp) {
      await this.$nextTick();
      const uploadEndpoint = useWebp
        ? "/goodsfiles/upload/goodslunbotuwebp"
        : "/goodsfiles/upload/goodslunbotu";
      // 
      if (!Array.isArray(e)) {
        const selectTime = Date.now();
        const currentLength = this.fileList.length;
        const fileIndex = this.fileList.findIndex(
          (item) => item.content === e.content || item.file === e.file
        );
        if (fileIndex !== -1) {
          this.$set(this.fileList[fileIndex], "selectTime", selectTime);
          this.$set(this.fileList[fileIndex], "paixuhao", currentLength);
          this.$set(this.fileList[fileIndex], "isImage", true);
        }
        this.uploading = true;
        this.uploadProgress = {
          current: 0,
          total: 1,
          percentage: 0,
        };
        try {
          await this.uploadFile(e, currentLength - 1, selectTime, false, uploadEndpoint);
          this.uploadProgress.current = 1;
          this.uploadProgress.percentage = 100;
          setTimeout(() => {
            this.uploading = false;
          }, 500);
          this.$Toast("");
        } catch (error) {
          this.uploading = false;
          this.$Toast("");
        }
        return;
      }
      const baseTime = Date.now();
      const currentLength = this.fileList.length - e.length;
      e.forEach((file, index) => {
        const fileIndex = this.fileList.findIndex(
          (item) => item.content === file.content || item.file === file.file
        );
        if (fileIndex !== -1) {
          const selectTime = baseTime + index;
          this.$set(this.fileList[fileIndex], "selectTime", selectTime);
          this.$set(this.fileList[fileIndex], "paixuhao", currentLength + index);
          this.$set(this.fileList[fileIndex], "isImage", true);
          file.selectTime = selectTime;
        }
      });
      this.uploading = true;
      this.uploadProgress = {
        current: 0,
        total: e.length,
        percentage: 0,
      };
      let successCount = 0;
      let failCount = 0;
      for (let index = 0; index < e.length; index++) {
        const file = e[index];
        try {
          await this.uploadFile(
            file,
            currentLength + index - 1,
            file.selectTime,
            false,
            uploadEndpoint
          );
          successCount++;
          this.uploadProgress.current = index + 1;
          this.uploadProgress.percentage = Math.round(((index + 1) / e.length) * 100);
        } catch (error) {
          failCount++;
          this.uploadProgress.current = index + 1;
          this.uploadProgress.percentage = Math.round(((index + 1) / e.length) * 100);
          console.error(` ${index + 1} :`, error);
        }
      }
      setTimeout(() => {
        this.uploading = false;
      }, 500);
      if (failCount === 0) {
        this.$Toast(` ${e.length} `);
      } else if (successCount > 0) {
        this.$Toast(` ${successCount}  ${failCount} `);
      } else {
        this.$Toast("");
      }
    },
    requestUploadMode(type, files) {
      this.uploadModeDialog = {
        visible: true,
        type,
        files,
      };
    },
    handleUploadModeSelect(mode) {
      const { type, files } = this.uploadModeDialog;
      if (!type || !files) {
        this.uploadModeDialog.visible = false;
        return;
      }
      this.uploadModeDialog.visible = false;
      const useWebp = mode === "webp";
      if (type === "lunbo") {
        this.processAfterReadOne(files, useWebp);
      } else if (type === "detail") {
        this.processAfterReadTwo(files, useWebp);
      }
      this.uploadModeDialog.type = "";
      this.uploadModeDialog.files = null;
    },
    cancelUploadModeSelection() {
      const { type, files } = this.uploadModeDialog;
      if (type === "lunbo") {
        this.removePendingFiles(this.fileList, files);
      } else if (type === "detail") {
        this.removePendingFiles(this.fileListXQ, files);
      }
      this.uploadModeDialog.visible = false;
      this.uploadModeDialog.type = "";
      this.uploadModeDialog.files = null;
    },
    removePendingFiles(targetList, files) {
      const pending = Array.isArray(files) ? files : [files];
      pending.forEach((file) => {
        const index = targetList.findIndex(
          (item) =>
            item === file ||
            (file?.content && item?.content === file.content) ||
            (file?.file && item?.file === file.file)
        );
        if (index !== -1) {
          targetList.splice(index, 1);
        }
      });
    },

    uploadFile(file, idx, selectTime, showToast = true, uploadEndpoint = "/goodsfiles/upload/goodslunbotu") {
      return new Promise((resolve, reject) => {
        const base64Data = file.content;
        const blob = this.base64ToBlob(base64Data, "image/jpeg");
        const now = new Date();
        const formatNumber = (n) => {
          n = n.toString();
          return n[1] ? n : `0${n}`;
        };
        const formatTime = (date, format, seconds) => {
          console.log("formatTime:", date, format, seconds);
          const year = date.getFullYear();
          const month = date.getMonth() + 1;
          const day = date.getDate();
          const hour = date.getHours();
          const minute = date.getMinutes();
          const second = date.getSeconds();
          if (format == 1) {
            return `${year}-${formatNumber(month)}-${formatNumber(day)}`;
          } else if (format == 2) {
            if (seconds) {
              return `${formatNumber(month)}-${formatNumber(day)} ${formatNumber(
                hour
              )}:${formatNumber(minute)}}`;
            } else {
              return `${formatNumber(month)}-${formatNumber(day)} ${formatNumber(
                hour
              )}:${formatNumber(minute)}:${formatNumber(second)}`;
            }
          } else if (format == 3) {
            return `${year}-${formatNumber(month)}-${formatNumber(
              day
            )} ${formatNumber(hour)}:${formatNumber(minute)}:${formatNumber(
              second
            )}`;
          } else {
            return `${year}${formatNumber(month)}${formatNumber(
              day
            )}${formatNumber(hour)}${formatNumber(minute)}${formatNumber(
              second
            )}`;
          }
        };
        const filename = `goodsphotosUrl${formatTime(now)}-${idx + 1}`;

        const formData = new FormData();
        formData.append("goodsgoodsidid", this.goodsgoodsidid);
        formData.append("userId", this.user.userId);
        formData.append("photosurlwei", filename);
        formData.append("file", blob, "photo.jpg");

        this.$request
          .post(uploadEndpoint, formData)
          .then((res) => {
            if (res?.code == "200") {
              //  showToast  true 
              if (showToast) {
                this.$Toast("");
              }
              // selectTime
              const index = this.fileList.findIndex(
                (item) => 
                  (item.selectTime && item.selectTime === selectTime) ||
                  item.content === file.content ||
                  (item.file && item.file === file.file) ||
                  (item.url && !item.photosUrl) // photosUrl
              );
              if (index !== -1) {
                //  van-uploader 
                this.$set(this.fileList[index], 'url', res.data);
                this.$set(this.fileList[index], 'photosUrl', res.data);
                this.$set(this.fileList[index], 'goodsgoodsidid', this.goodsgoodsidid);
                this.$set(this.fileList[index], 'name', `uploads/${filename}`);
                // 
                if (this.fileList[index].paixuhao) {
                  this.$set(this.fileList[index], 'paixuhao', this.fileList[index].paixuhao);
                }
                if (selectTime) {
                  this.$set(this.fileList[index], 'selectTime', selectTime);
                }
              }
              resolve(res);
            } else {
              if (showToast) {
                this.$Toast("");
              }
              reject(new Error(""));
            }
          })
          .catch((error) => {
            console.error("", error);
            if (showToast) {
              this.$Toast("");
            }
            reject(error);
          });
      });
    },
