                this.$set(this.fileListXQ[index], 'photosUrl', res.data);
                this.$set(this.fileListXQ[index], 'goodsgoodsidid', this.goodsgoodsidid);
                this.$set(this.fileListXQ[index], 'name', `uploads/${filename}`);
                // 
                if (this.fileListXQ[index].paixuhao) {
                  this.$set(this.fileListXQ[index], 'paixuhao', this.fileListXQ[index].paixuhao);
                }
                if (selectTime) {
                  this.$set(this.fileListXQ[index], 'selectTime', selectTime);
                }
              }
              resolve(res);
            } else {
              if (showToast) {
                this.$Toast("");
              }
              reject(new Error(""));
            }
          })
          .catch((error) => {
            console.error("", error);
            if (showToast) {
              this.$Toast("");
            }
            reject(error);
          });
      });
